  getCountries(): Observable<any[]> {
    return this.http.get<any[]>('assets/json/getCountriesInfo.json').pipe(
      map(data => {  
        return data.map((country: any) => {
          let iddValue = country.idd?.root || '';
  
          if (country.idd?.suffixes?.length > 0) {
            iddValue += country.idd.suffixes[0];
          }
  
          return {
            name: country.name.common, 
            cca2: country.cca2, 
            idd: iddValue, 
            flag: country.flags?.png || '' 
          };
        }).filter(country => country.idd.length > 0); 
      })
    );
  }
  
