  platformName(): string {
    let platformName: string;
    if (this.platform.is('ios')) {
      platformName = 'IOS';
    } else if (this.platform.is('android')) {
      platformName = 'Android';
    } else if (this.platform.is('desktop')) {
      platformName = 'Windows';
    } else {
      platformName = "Web";
    }
    return platformName;
  }
  checkConnection(): boolean {
    // console.log("this.network.type", this.network.type);
    if ((this.network.type === 'none' || !this.network.type) && this.platform.is('cordova') && this.platform.is('android') && this.platform.is('ios')) {
      this.showToast('No Internet connection please connect to the internet');
      return false;
    }
    return true;
  }

  isConnected() {
    if ((this.network.type === 'none' || !this.network.type) && this.platform.is('cordova') && this.platform.is('android') && this.platform.is('ios')) {
      return false;
    }
    return true;
  }

