import { Component, OnDestroy, OnInit } from '@angular/core';
import { ApiFactory, AppData, CommonStorage, LoaderService, UtilProvider } from '../services';
import { Platform } from '@ionic/angular';
import { Clipboard } from '@ionic-native/clipboard/ngx';
@Component({
  selector: 'app-offer-coupon',
  templateUrl: './offer-coupon.page.html',
  styleUrls: ['./offer-coupon.page.scss'],
})
export class OfferCouponPage implements OnInit, OnDestroy {
  couponList: any = [];
  metaData: any;
  arrowShowHide: boolean = false;
  storePage: any = '';

  constructor(public apiFactory: ApiFactory,
    private loaderService: LoaderService,
    private appData: AppData,
    private commonStorage:CommonStorage,
    private util:UtilProvider,
    private platform:Platform,
    private clipboard: Clipboard,
  ) { }

  ngOnInit() {
    this.metaData = this.commonStorage.getItem('metaData');
    this.OfferCouponPage();
    console.log(this.commonStorage.getItem('tabSelected'))
    this.storePage = this.commonStorage.getItem('tabSelected');
    if(this.metaData.msiteFolder=='seabirdTheme' && this.storePage == 'morePage'){
      this.arrowShowHide = true;
    }
  }
  ngOnDestroy() {
    // Reset the variable when leaving the page
    this.storePage = '';
    this.arrowShowHide = false;
  }
  OfferCouponPage(){
    let platformNumber;
    if (this.appData.isWEBAPP) {
      platformNumber = 'web';
  } else if(this.appData.isIOS && !this.appData.isWEBAPP) {
      platformNumber = 'ios';
  }
  this.loaderService.showLoadingDefault();
    this.apiFactory.offerCoupon(platformNumber)
    .subscribe(
      (coupon:any) => {
        this.loaderService.hideLoadingDefault();
        // console.log(coupon)
        if(coupon.code == 200){
          this.couponList.push(coupon.result);
        }
      }, err => {
        this.loaderService.hideLoadingDefault();
      }
    )
  }

  copyTextToClipboard(text: string) {
    if (this.platform.is('cordova')) {
      this.clipboard.copy(text).then(
        () => {
          this.util.showToast("Code copied to clipboard: "+text);
        },
        (err) => {
          console.error('Error copying text:', err);
          this.util.showToast("'Error copying text: "+err);
        }
      );
    } else {
     
      this.copyTextUsingClipboardAPI(text);
    }
}
copyTextUsingClipboardAPI(text: string) {
  if( navigator.clipboard){
    navigator.clipboard.writeText(text).then(
      () => {
        this.util.showToast("Code copied to clipboard: "+text);
      },
      (err) => {
        console.error('Error copying text:', err);
        this.util.showToast("'Error copying text: ",err);
      }
    );
  }else{
    // this.copyTextFallback(text);
    // this.util.showToast(this.platform.is('cordova'));
  }

}
copyCode(couponCode: string) {
  if (this.platform.is('cordova')) {
    this.clipboard.copy(couponCode).then(() => {
  this.util.showToast('Code copied to clipboard')
    }).catch((error) => {
      this.util.showToast("'Error copying code");
    });
  } else {
    this.copyToBrowserClipboard(couponCode);
  }
}

copyToBrowserClipboard(couponCode: string) {
  const textArea = document.createElement('textarea');
  textArea.value = couponCode;
  document.body.appendChild(textArea);
  textArea.select();
  document.execCommand('copy');
  document.body.removeChild(textArea);
  this.util.showToast('Code copied to clipboard')
}
}
