import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-white-common-header',
  templateUrl: './white-common-header.component.html',
  styleUrls: ['./white-common-header.component.scss'],
})
export class WhiteCommonHeaderComponent implements OnInit {
  @Input() public title: string = "";
  @Input() public showsubTxt:  string = "";
  @Input() public showsubtitle:  string = "";
  @Input() public subtitle: string = "";
  isreturnTrip: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public navCtrl: NavController
  ) { }

  ngOnInit() {}
  goBack(){
    this.route.queryParams.subscribe(params => {
      this.isreturnTrip = params["is_return_trip"];

    });
    if (this.isreturnTrip) {
      let navigationExtras: NavigationExtras = {
        queryParams: {
          is_return_trip: true,
        }
      };
      this.navCtrl.navigateForward('seat-layout', navigationExtras)
    } else {
      this.router.navigate(['/seat-layout']);
    }
  }

}
