  margin: 0px;
  box-shadow: none;
}

.search-btn {
  --background: #F2C21A;
  --color: #191966;
  margin: 13px 0;
  padding: 0px 16px;
  --border-radius: 100px;
}

.ripple-parent {
  position: relative;
  overflow: hidden;
}

.fab-revrse {
  --background: #3e3e52;
  position: absolute;
  z-index: 5;
  right: 3%;
  top: 8%;
}

.txt-main {
  margin: 0 0 0 16px;
  width: 100%;
  font-size: 11px;
  color: #A0A0A0;
}

.txt-main-slt {
  font-size: 14px;
  margin: 0 0 0 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #191966;
}

.cbus-wallet {
  border-radius: 100px;
  /* --background-color: white; */
  --background: #ffffff00;
  --box-shadow: none;
  padding: 7px 3px;
  border: 0.5px solid #858585;
}
.cbus-wallet span {
  font-size: 14px;
  font-weight: 700;
}
.cbus-wallet ion-icon {
  margin-right: 5px;
}

.search-txt {
  font-size: 14px;
  font-weight: bold;
  text-transform: capitalize;
}

ion-button {
  margin: 12px;
  border-radius: 2px;
  font-family: "NotoSans";
}

ion-button.fffff.md.button.button-block.button-solid.ion-activatable.ion-focusable.hydrated {
  margin: 6px 12px 6px 10px;
  font-size: 10px;
  font-weight: bold;
  width: 96%;
  border-radius: 8px;
  height: 31px;
}

.themeone-head {
  color: #333333;
  padding: 0;
  margin: 0;
  font-family: Helvetica Now Text;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0em;
}

.themeone-bg-cl {
  color: #ffffff !important;
  --background: white;
  -webkit-text-fill-color: #333333 !important;
  font-weight: 500;
