  isValid(): boolean {
    // console.log('CancellationPage --> cancellationForm --> isValid()', this.loginForm);

    if (this.loginForm.valid) {
      return true
    }
    if (this.loginForm.controls.password.valid) {
      this.showError('Please enter valid email/phone');
    } else {
      this.showError('Please enter login credentials');
    }
    // this.util.showToast('Please enter login credentials.');
    this.errors = true;
    let elem = <any>document.querySelector('ion-input.ng-invalid input');
    if (elem) {
      elem.focus();
    }
    return false
  }

  showError(msg) {
    this.errors = true;
    this.showLoginError = true;
    this.loginFailed = true;
    this.loginErrorMessage = msg;

  }

