  OtpFormValidation() {
    let minPhoneCount;
    let maxPhoneCount;
      if(this.phoneNumCount === "" ){
        minPhoneCount = 10;
        maxPhoneCount = 14;
      }else{
        minPhoneCount = this.phoneNumCount;
        maxPhoneCount = this.phoneNumCount;
      }



    this.otpForm = this.formBuilder.group({
      mobile_number: ['', Validators.compose([Validators.required, Validators.maxLength(maxPhoneCount), Validators.minLength(minPhoneCount)])],
    })
    this.validateOtpForm = this.formBuilder.group({
      otp: ['', Validators.compose([Validators.required])],
    })
  }
  // onInputChange(ev) {
  //   let mob: string = this.otpForm.value.mobile_number.toString();
  //   if (mob.length > this.phoneNumCount) {
  //     this.otpForm.controls['mobile_number'].setValue(mob.substr(0, this.phoneNumCount));
  //   }
