  /**
  * method to check is otp valid 
  */

  isOtpValid() {
    if (this.validateOtpForm.controls.otp.invalid) {
      this.showError("Please enter OTP");
      let elem = <any>document.querySelector('.signup-name ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
    }
  }

  validateOTP() {
    this.loader.showLoadingDefault();
    this.apiFactory.validateOtpLogin(this.validateOtpForm.value.otp, this.commonStorage.getItem('api_key'), this.otpForm.value.mobile_number)
      .subscribe(result => {
        this.loader.hideLoadingDefault();
        try {
          if (result.code == 200) {
            // console.log(result);
            let body = (result.body)
            let token = body.authentication_token;
            localStorage.setItem('currentUser', JSON.stringify({ email: body.email, token: token, userFull: body, user: this.authenticationService.setUser(body) }));
            this.authenticationService.token = token;
            // console.log(token);
            this.util.showToast("Login successful");
            this.commonService.gTrack('login', 'Login with OTP successful');
            this.apiFactory.getMetaAppData()
              .subscribe(
                loginResponse => {
                  if (!loginResponse.code) {
                    let localData = loginResponse[0];
                    this.commonService.setMetaApp(localData);
                  }
                }
              );
            this.navCtrl.navigateRoot('tabs/home');
          }else if(result.code == 491){
            this.activateNoteModal = true;
            this.activateToken = result.body.auth_token
          }
           else {
            this.commonService.gTrack('login', 'Login with OTP failed');
            if (result.error)
              this.util.showAlert('', result.error.message);
            else
              this.util.showAlert('', result.body.message);
          }

          // console.log('otp --->', result)
        } catch (e) {
          // console.log("failed --> signup", e)
        }

      }, error => {
        this.commonService.gTrack('login', 'Login with OTP failed');
        this.loader.hideLoadingDefault();
        //      this.util.showAlert(this.util.tranlateInstant('FAILED'), this.util.tranlateInstant('CANNOT_GET_OTP_TRY_LATER'))
      });
  }

  validations1(type) {
    if (type == 1) {
      this.forgotForm = this.formBuilder.group({
        email: ['', Validators.compose([Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')])],
      })
      this.changePasswordForm = this.formBuilder.group({
        otp: ['', Validators.compose([Validators.required])],
        newPassword: ['', Validators.compose([Validators.required])],
        confirmPassword: ['', Validators.compose([Validators.required])],
      })
    }
    else if (type == 2) {
      this.changePasswordForm = this.formBuilder.group({
        otp: ['', Validators.compose([Validators.required])],
        newPassword: ['', Validators.compose([Validators.required])],
        confirmPassword: ['', Validators.compose([Validators.required])],
      })
    }
  }


  forgotPassword() {
    // console.log("this.dataform", this.forgotForm.value.email);
    this.showForgotError = false;
    this.forgotErrorMessage = null;
    if (this.isValid1()) {
      if (this.data == '') {
        this.data = this.forgotForm.value;
      }
      // console.log("data", this.data);
      this.loader.showLoadingDefault();
      this.authenticationService.forgotPassword(this.data)
        .subscribe(async result => {
          this.loader.hideLoadingDefault();
          if (result.success === true) {

            const alert = await this.alertCtrl.create({
              header: "SUCCESS",
              message: result.body,
              buttons: [{
                text: 'OK',
                handler: data => {
                  this.navCtrl.pop();
                }
              }]
            });

            await alert.present();

          }
          else if (result.body && result.body.otp) {
            this.isphone = true;
            this.validations1(2);
            this.response = result.body;
            // console.log(result);
          }
          else {

            // forgot failed
            if (result.body)
              this.showError1(result.body.message);
            else if (result.error)
              this.showError1(result.error.message);
          }
          // console.log('forgot --->', result)

        },
          err => {
            this.loader.hideLoadingDefault()
            if (!this.util.isConnected()) {
              this.util.showAlert("Error Loading", "Internet not connected please connect the internect and try after again");
            } else {
              this.util.showAlert("Error Loading", "Something went wrong please try after some time");
            }
            this.ex.call('ForgotPasswordPage', 'forgotPassword() --> this.authenticationService.forgotPassword(this.forgotForm.value)', err, '')
          });
    }
  }

  changePassword() {
    if (this.response != undefined) {
      let resetData = {
        otp: this.changePasswordForm.value.otp,
        key: this.response.key,
        newPassword: this.changePasswordForm.value.newPassword,
        confirmPassword: this.changePasswordForm.value.confirmPassword,
        mobileNumber: this.forgotForm.value.email
      };

      // console.log(resetData);

      this.loader.showLoadingDefault();
      this.authenticationService.changePassword(resetData)
        .subscribe(async result => {
          this.loader.hideLoadingDefault();
          if (result.success === 'success') {

            const alert = await this.alertCtrl.create({
              header: "SUCCESS",
              message: result.body,
              buttons: [{
                text: 'OK',
                handler: data => {
                  this.navCtrl.pop();
                }
              }]
            });

            await alert.present();
          }

          else {

            if (result.body)
              this.showError1(result.body.message);
            else if (result.error)
              this.showError1(result.error.message);
          }
          // console.log('forgot --->', result)

        },
          err => {
            this.loader.hideLoadingDefault()
            if (!this.util.isConnected()) {
              this.util.showAlert("Error Loading", "Internet not connected please connect the internect and try after again");
            } else {
              this.util.showAlert("Error Loading", "Something went wrong please try after some time");
            }
            this.ex.call('ForgotPasswordPage', 'forgotPassword() --> this.authenticationService.forgotPassword(this.forgotForm.value)', err, '')
          });

    }
    // console.log(this.response,this.changePasswordForm.value);
    //this.authenticationService.resetPassword(this.data)

  }


  isValid1(): boolean {
    var phoneno = /^\d{10}$/;
    if (this.forgotForm.valid) {
      this.data = { "email": "" + this.forgotForm.value.email + "" };
      return true;
    } else if (this.forgotForm.value.email.match(phoneno)) {
      this.data = { "mobile_number": "" + this.forgotForm.value.email + "" };
      return true;
    } else {
      this.data = '';
    }
    this.showError1('Please enter your registered email/phone number');
    this.errors = true;
    let elem = <any>document.querySelector('ion-input.ng-invalid input');
    if (elem) {
      elem.focus();
    }
    return false
  }

  showError1(msg) {
    this.errors = true;
    this.showForgotError = true;
    this.forgotFailed = true;
    this.forgotErrorMessage = msg;

  }

  onInputChange1(ev) {
    this.isphone = false;
    this.showForgotError = false;
    this.forgotErrorMessage = null;
  }

  activateConsumerStatus(){
    // console.log("hiiactive");
    this.apiFactory.deactivateConsumerStatus(this.activateToken,'1')
    .subscribe(
      res => {
        if(res.code == 200){
          this.util.showToast(res.message);
        }else{
          this.util.showToast(res.message);
        }
      },
      err => {
      })
  }
