//this function will popup the small alert box for redirection
  async showAppRedirectPopup() {
    const currentUrl = window.location.href;
    const domain = new URL(currentUrl).hostname;

    let msg = ''
    if(this.appData.isWEBAPP == true){
      if (this.util.platformName() == 'IOS') {
        msg = "Install our official app from App store";
      }
      if (this.util.platformName() == 'Android') {
        msg = "Install our official app from play store";
      }
      const alert = await this.alertCtrl.create({
        header: domain +' says',
        message: '<span class="message">'+msg+'</span>',
        buttons: [
              {
                text: 'Cancel',
                role: 'cancel',
                cssClass: 'cancel-button'
              },
              {
                text: 'Ok',
                handler: () => {
                  this.openAppOrStore();
                },
                cssClass: 'open-button'
              }
            ]
      });
      await alert.present();
    }
    
  }
