   //this is to prepare json of origins and destinations
   setCities() {
    // this.loaderService.showLoadingDefault();
    this.apiFactory.getCityDestiantionPairs(this.metaData.isBimaEnabled)
      .subscribe(
        cities => {
          // console.log("cities", cities)
          this.loaderService.hideLoadingDefault();
          cities = cities.result.filter(
            it => {
              return it.origin.name && it.destination.name;
            }
          )
          let originGroups = [];
          let origins = {};
          let destinationsGroup = {};
          let destinationsList = [];
          let d = {};
          let dGroup = [];
          let originPGroups = {};
          for (let city of cities) {
            let currentOrigin = city.origin.name;
            let currentOriginname =city.origin.id;
            let currentDestination = city.destination.id;
            if (typeof origins[currentOrigin] == "undefined") {
              origins[currentOrigin] = currentOrigin;
                originGroups.push(city.origin);
            }
            
            if (typeof destinationsGroup[currentOriginname] == "undefined") {
              destinationsGroup[currentOriginname] = [];
            }
            destinationsGroup[currentOriginname].push(city.destination)
            destinationsList.push(city.destination)
            //  without origin destination can be selected

            if (typeof d[currentDestination] == "undefined") {
              d[currentDestination] = currentDestination;
              dGroup.push(city.destination);
            }

            if (typeof originPGroups[currentDestination] == "undefined") {
              originPGroups[currentDestination] = [];
            }
            originPGroups[currentDestination].push(city.origin)



          }
          // this.commonStorage.localSet('dList', dGroup);
          this.commonStorage.setItem('dList', dGroup);
          // this.commonStorage.localSet('originPList', originPList);
          this.commonStorage.setItem('originPList', originPGroups);
          
          this.commonStorage.setItem('origins', originGroups);
          this.commonStorage.setItem('destinations', destinationsGroup);
          this.commonStorage.setItem('destinationsList', destinationsList);
          this.commonStorage.localSet('origins', originGroups);
          this.commonStorage.localSet('destinations', destinationsGroup);
          // this.commonStorage.localSet('destinationsList', destinationsList);
        }, err => {
          this.loaderService.hideLoadingDefault();
        }
      )
  }


