callOnesignal(appId){

    
    // Push notification start //

    this.platform.ready().then(() => {

     // Remove this method to stop OneSignal Debugging
      OneSignal.Debug.setLogLevel(6);

      // Replace YOUR_ONESIGNAL_APP_ID with your OneSignal App ID
      OneSignal.initialize(appId);

      let myClickListener = async function(event) {
        let notificationData = JSON.stringify(event);
      };

      OneSignal.Notifications.addEventListener("click", myClickListener);
      OneSignal.Notifications.requestPermission(true).then((accepted: boolean) => {
        OneSignal.User.pushSubscription.getIdAsync().then((Pushid) => {
          this.subscrptionID = Pushid;
          //alert(this.subscrptionID);
          //alert(this.device.uuid);
          //console.log("Pushid", Pushid);
          //console.log("device", this.device.uuid);
          this.apiFactory.storeSubscriptionId(this.subscrptionID,this.device.uuid).subscribe(result => {
                //alert("result "+JSON.stringify(result));
            },
            err => {
                console.log("error ", err);
            }
        );
        })
      });

    });

    // Push notification end //

  
}

