  openBrowser1(url, data = {}) {
    this.paymentStart = true
    this.iframeLoad();
    let form = <HTMLFormElement>document.getElementById('payMentGateWayForm');
    if (this.platform.is('cordova')) {
      console.log('cordova -->');
      let formHtml: string = '';
      for (let key in data) {
        if (data.hasOwnProperty(key)) {
          let value = data[key];
          // console.log("bookTicket --> ", key, value)
          if (key == "passenger_contact_details" || key == "passengers_details") {
            value = encodeURIComponent(value);
          }

          let value2 = '';
          if (value) {
            value2 = value.toString().replace(/\n/g, '');
          }
          formHtml += '<input type="hidden" value="' + value2 + '" id="' + key + '" name="' + key + '"/>';
        }
      }
      if (!(this.paymentType == "31")) {
        form.innerHTML = formHtml;
      }

      this.resultScript = formHtml;
      this.gotoPaymentModal(url);
      // form.action = url;
      // form.method = "POST";
      // console.log("form ", form.toString());
      // form.submit();
    }
    else {
      form.action = url;
      form.method = "POST";
      // console.log("form ", form.toString());
      // console.log("form ",JSON.stringify(form));
      form.submit();
    }
  }





