  --ion-background-color: #f7f9fb;
}

ion-footer {
  --ion-background-color: #f7f9fb;
}

.invite h3 {
  font-size: 18px;
  padding-left: 14px;
  padding-top: 10px;
  margin-bottom: 5px;
  font-weight: bolder;
  color: var(--sectionHeaderTextColor);
}
.invite span {
  padding-left: 14px;
  cursor: pointer;
  color: rgba(56, 58, 62, 0.6509803922);
  font-size: 14px;
}
.invite .center {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 50%;
}

.referCode {
  border: 1px solid #ccc !important;
  border-radius: 5px;
  padding: 10px;
  width: 45%;
  padding-left: 50px;
}

.sidewalk {
  margin: 10px;
  padding-top: 35px;
}

.readTC {
  padding-right: 5px;
  color: var(--iconsAndButtonsColor);
  float: right;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.legend-box-1 {
  border-radius: 2px;
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 24px 38px 3px rgba(0, 0, 0, 0.14);
  background-color: #ffffff !important;
  opacity: 1 !important;
  padding: 18px 16px;
  width: 90%;
  background-size: cover;
  height: auto;
  background-repeat: no-repeat;
}

.whole {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
}

.login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background: var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  bottom: 0;
  height: 45px;
  border-radius: 4px;
}

.themeone_lable {
  font-family: NotoSans;
  color: #5F5F5F;
  font-size: 16px;
}

.lable1 {
  font-family: NotoSans;
  color: #191966;
  font-size: 18px;
  font-weight: bold;
}

.themeone_lable2 {
  font-family: NotoSans;
  color: #5F5F5F;
  font-size: 14px;
  font-weight: bold;
}

.themeone_referal_code_css {
  padding: 10px;
  background: #F3F3F7;
  margin-left: 25px;
  margin-right: 25px;
  border: 2px dotted #191966;
  border-radius: 15px;
  margin-top: 15px;
}

.themeone_lbl {
  font-family: NotoSans;
  color: #5F5F5F;
  font-size: 16px;
  text-align: justify;
}

.themeone_lbl_col {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.referEarnBg {
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url('ourbus_referearn_back.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 250px;
  font-weight: 600;
  padding: 75px 14px 0px;
  color: white;
  backface-visibility: 0.3;
}

.header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 2%;
  position: absolute;
  top: 0%;
  left: 0%;
}

.text_center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ourbus_btn {
  height: 50px;
  box-shadow: none;
  --border-radius: 8px;
  font-size: 18px;
  font-weight: 700;
  --background:var(--iconsAndButtonsColor);
  --color:var(--primaryText);
  margin: 10px;
}

.ourbus_round_div {
  background: #DBEFDC;
  color: #397D32;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}

.ourbus-step-text {
  font-size: 14px;
  color: #2D2D2D;
}

.ourbus-step-subtext {
  font-size: 12px;
  color: #747474;
  margin-top: -14px;
}

.ourbus-card {
  background: #FFF;
  margin-inline: 10px;
  padding: 5px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.ourbus-referal-code {
  color: #4CAF50;
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
}

.ourbus-share-code {
  color: #2D2D2D;
  font-weight: 700;
  font-size: 16px;
  margin-left: 5px;
  margin-top: 8px;
}

.ourbus-icon-size {
  height: 25px;
  width: 25px;
  margin-left: 10px;
