  --ion-background-color:var(--viewBgColor);
}

.login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  position: fixed;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.ourbus-login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.txt-lbl {
  font-family: NotoSans;
  font-size: 14px;
  font-weight: normal;
  color: #3e3e52 !important;
}

.center-div {
  margin: 15px;
}

.passenger-details {
