}
.passenger-details .code.item-md {
  margin-top: -1px;
}
.passenger-details .code.item-ios {
  margin-top: 1px;
}
.passenger-details ion-select {
  max-width: 100%;
  text-align: center;
}
.passenger-details ion-select .select-text {
  padding: 5px 0;
}
.passenger-details ion-select .select-icon {
  left: -5px;
}
.passenger-details .select-md,
.passenger-details .select-ios {
  padding: 0;
}
.passenger-details .item-ios.item-block .item-inner {
  border: 0;
}
.passenger-details .item-md.code,
.passenger-details .item-ios.code {
  height: 35px;
