.passenger-details ion-col {
  position: relative;
}
.passenger-details ion-item {
  padding-left: 0;
}

ion-content .card-row ion-card {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  width: 100%;
  background-color: #fff !important;
  margin: 0px;
  padding: 16px;
  height: 90vh;
}
ion-content .card-row ion-card ion-item::part(native) {
  background-color: #fff !important;
}
ion-content .card-row ion-card p {
  color: #ADADAD;
  font-size: 12px;
  font-weight: 500;
  margin: 0;
}
ion-content .card-row ion-card ion-item {
  border-radius: 6px;
  border: 1px solid #DFDFDF;
  width: 100%;
  height: 50px;
  --inner-border-width: 0px;
}
ion-content .card-row ion-card ion-item ion-select::part(placeholder) {
  opacity: 1;
}
ion-content .card-row ion-card .box {
  margin-bottom: 16px;
  width: 100%;
  margin-top: 6px;
}
ion-content .card-row ion-card .box h2 {
  color: #191966;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}
ion-content .card-row ion-card ion-button.ticket {
  width: 100%;
}
ion-content .card-row ion-card ion-button.ticket::part(native) {
  background-color: #F2C21A;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #191966;
  font-weight: 600;
  height: 45px;
}

ion-header ion-toolbar {
  color: #ffffff;
}

ion-select {
  color: #000;
}

ion-input {
  color: #000;
}

.themeone-input {
  color: #000;
  height: 50px;
  border-radius: 6px;
  border: 1px solid #DFDFDF;
}

