}

/* src/assets/custom-select.css */
/* Style the select dropdown */
.custom-select .select-icon {
  color: red;
}

/* Style the select options */
.custom-select .select-interface {
  background-color: lightgray;
  border: 1px solid darkgray;
}

/* Style the option items */
.custom-select .select-popover ion-item {
  color: blue;
}

/* Style the option headers */
.custom-select .select-popover ion-list-header {
  background-color: orange;
  color: white;
}

.sc-ion-select-popover sc-ion-label-ios-h sc-ion-label-ios-s ios hydrated {
  text-overflow: unset !important;
  white-space: normal !important;
  overflow: auto !important;
}

.seabird_row {
  background: white;
  padding: 12px;
  margin: 15px;
  border: 0.5px solid #626262;
  border-radius: 15px;
}

.seabird_col {
  display: flex;
  align-items: center;
  justify-content: left;
  font-size: 14px;
}

.seabird_col_line {
  display: flex;
  align-items: center;
  justify-content: left;
  border-bottom: 0.5px dotted #999999;
  padding: 5px;
  font-size: 14px;
}

.seabird_col_line:last-child {
  border-bottom: none;
}

.seabird_col_line1 {
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 0px;
  font-size: 14px;
}

.seabird_item {
  --background: #fff;
  width: 100%;
  --min-height: 34px;
  --border-color: white;
}

.seabird-border-bottom {
  border-bottom: 0.5px solid #626262;
}

.seabird-clickable {
  margin: 18px 0;
}

.seabird-input-hidden {
  display: none;
}

.seabird-select-date {
  color: #2B28CE;
}

.get-otp-text {
  text-align: center;
