  font-size: 14px;
  font-weight: 700;
  padding-top: 20px;
  margin-bottom: 0;
}

.resend-otp-disabled {
  font-size: 14px;
  font-weight: 700;
  color: #9B9B9B;
  text-decoration: underline;
}

.resend-otp-enabled {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 0;
  text-decoration: underline;
  color: var(--iconsAndButtonsColor);
}

.seabird-login-btn {
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText) !important;
  text-transform: initial;
  color: #191966;
  font-family: NotoSans;
  font-weight: bold;
  font-size: 14px;
  height: 50px;
  --border-radius: 20px;
}

.input-spacing {
  margin-bottom: 10px;
}

.seabirdTheme .ion-invalid {
  --highlight-background: transparent !important;
  background-color: transparent !important;
}
.seabirdTheme .item-interactive.ion-valid {
  --highlight-background: transparent;
}

ion-modal#dob {
  --width: 311px !important;
  --height: 543px !important;
  --border-radius: 8px !important;
}

.modify-dropDown {
  width: 18px;
