  async selectCountry_code() {
    if(this.metaData.msiteFolder == 'ourbustheme'){
      const reformattedCountries = this.countryData.map(country => ({
        name: country.name,
        cca2: country.code,
        idd: country.dialCode,
        flags: country.flag
      }));
      
      console.log(reformattedCountries);
      const modal = await this.modalController.create({
        component: CitiesModalPage,
        componentProps: { "list": 9, "destinationList": reformattedCountries,"isOurbus": 'true' }
      });
      await modal.present();
      const { data } = await modal.onDidDismiss();
      if (data != '') {
        // this.selected_Country_code = data.idd;
        this.selectedCountry = null;
        this.flagSelected  = data.flags;
        this.dialCodeSelected = data.idd;
      }
      console.log(this.selectedCountry,"this.selectedCountry")
    }else{
      const modal = await this.modalController.create({
        component: CitiesModalPage,
        componentProps: { "list": 9, "destinationList": this.countries,"isOurbus": 'false' }
      });
      await modal.present();
      const { data } = await modal.onDidDismiss();
      if (data != '') {
        this.selected_Country_code = data.idd;
      }
    }    
  }

