  width: 100%;
  text-align: left;
}
table tbody tr {
  border-top: 1px solid #ddd;
}
table td {
  padding: 2px;
}

.ticket-details ion-row {
  border-top: 1px solid #e1e1e1;
}
.ticket-details ion-row.fare {
  background: #f5f5f5;
  /* font-size: 1rem; */
  padding: 0;
}
.ticket-details ion-row.fare span {
  font-size: 1.5rem;
  font-weight: bold;
}
.ticket-details ion-row .day {
  font-size: 1.6rem;
}
.ticket-details ion-row ion-icon {
  margin: 5px 5px 0 0;
  padding: 0 5px;
}
.ticket-details ion-row .top-span {
  margin-top: 10px;
  display: block;
}

h5 {
  background-color: #eee;
  padding: 8px 8px 0;
  margin: 5px 0 0;
  border: 1px solid #ddd;
  border-bottom: 0;
}

.round-trip-box {
  background-color: #fff;
  border-radius: 3px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  margin-top: 15px;
  padding: 15px;
  display: block;
  color: #000;
}
.round-trip-box h5 {
  margin: 0;
  padding: 0 0 5px;
  border: 0;
  border-bottom: 1px solid #ccc;
  font-size: 1.2em;
  background: #fff;
}
.round-trip-box > div {
  padding: 3px 0;
}
.round-trip-box > div span {
  display: block;
  margin: 4px 0 0;
}

.high-light {
  color: #b8860b;
