  async onSlideChanged(slide) {
    //  slide.getActiveIndex();
    const currentIndex = await this.slides.getActiveIndex();
    // console.log('Segment changed', slide, currentIndex);

    switch (currentIndex) {
      case 0:
        this.segment = "all";
        break;
      case 1:
        this.segment = "booking";
        break;
      case 2:
        this.segment = "cancellation";
        break;
      case 3:
        this.segment = "cashback";
        break;
      default:
        this.segment = "all";
        break;

    }
  }

  async goToSlide(slide, slideNum: number, tabName: string) {
    // console.log('slide', slide, this.slides, this.slides);
    if (tabName == 'all') {
      //slide.slidePrev();
      await this.slides.slideTo(0);
    }
    else if (tabName == "booking") {
      //    slide.slideNext();
      await this.slides.slideTo(1);
    }
    else if (tabName == "cancellation") {
      //    slide.slideNext();
      await this.slides.slideTo(2);
    }
    else if (tabName == "cashback") {
      //    slide.slideNext();
      await this.slides.slideTo(3);
    }
    this.segment = tabName;
  }
  async addMoney() {
    if (!this.util.checkConnection()) {
      return false;
    }
    console.log(this.enteramount)
    const modal = await this.modalController.create({
      component: AddMoneyWalletPage,
      componentProps:{'enterAmount':this.enteramount}
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    console.log(data);
    if (data) {
      if (data == 'success') {
        this.defaultCall();
        this.util.showAlert('SUCCESS', "Money Added successfully to wallet")
      } else {
        this.util.showAlert('FAILURE', "Transaction failed")
      }
    }

  }
