  getBookingList(type) {
    if (type == 1) {
      this.selectedTab = 1;
      this.transactionsArray = this.transcationsList.filter(
        it => {
          return this.transactions;
        }
      )
      // console.log("this.transactions", this.transactions);
    } else if (type == 2) {
      this.selectedTab = 2;
      this.transactionsArray = this.transcationsList.filter(
        it => {
          return (it.payment_transaction_type == 'Booking');
        }
      )
    } else if (type == 3) {
      this.selectedTab = 3;
      this.transactionsArray = this.transcationsList.filter(
        it => {
          return (it.payment_transaction_type == 'Cashback');
        }
      )
    } else if (type == 4) {
      this.selectedTab = 4;
    }
  }


