   scrollToBottom(scrollableDiv: any) {
    if (scrollableDiv) {
      scrollableDiv.scroll({
        top: scrollableDiv.scrollHeight,
        behavior: 'smooth'
      });
      this.isAtBottom = true;
      this.isAtTop = false;
    }
  }

  scrollToTop(scrollableDiv: any) {
    if (scrollableDiv) {
      scrollableDiv.scroll({
        top: 0,
        behavior: 'smooth'
      });
      this.isAtTop = true;
      this.isAtBottom = false;
    }
  }
  onScroll(scrollableDiv: any) {
    // Check if the user is at the top
    this.isAtTop = scrollableDiv.scrollTop === 0;
    
    // Check if the user is at the bottom
    this.isAtBottom = (scrollableDiv.scrollTop + scrollableDiv.clientHeight) >= scrollableDiv.scrollHeight;
  }


