import { CommonStorage } from './../services/common-storage';
import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import * as moment from 'moment';

@Component({
  selector: 'app-custom-ourbus-calender',
  templateUrl: './custom-ourbus-calender.page.html',
  styleUrls: ['./custom-ourbus-calender.page.scss'],
})
export class CustomOurbusCalenderPage implements OnInit {
  months: any[] = [];
  weekDays: string[] = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];
  today = moment();
  selectedDates: moment.Moment[] = [];
  multipleDateSelection: any;
  departureDate: any;
  type: any;
  departureDateReturn: any;
  title: any;
  selectedEndDate: moment.Moment | null = null;
  originalPreselectedDate: moment.Moment | null = null;
  selectedTripSegment:any;

  constructor(public modalController: ModalController, public params: NavParams,  public commonStorage: CommonStorage) {
    let departureDate = params.get('departureDate');
    this.departureDate = moment(departureDate.formattedDate, "DD-MM-YYYY").format("YYYY-MM-DD");
    this.type = params.get('type');
    let departureDateReturn =params.get('departureDateReturn');
    this.departureDateReturn =moment(departureDateReturn.formattedDate, "DD-MM-YYYY").format("YYYY-MM-DD");
    this.title = params.get('title');
    console.log(" this.departureDate", this.departureDate, "this.type", this.type, "this.departureDateReturn", this.departureDateReturn, "this.title", this.title);

    this.multipleDateSelection = this.type === 2;
    this.selectedTripSegment = params.get('selectedTripSegment'); 
    console.log("selectedTripSegment",this.selectedTripSegment);
    
    this.originalPreselectedDate = moment(this.departureDate); // Save original date for checking later
  }

  ngOnInit() {
    // Set the flag for multiple date selection based on the selected trip segment
    if (this.selectedTripSegment === 'oneway') {
      this.multipleDateSelection = false; // Disable multiple date selection for one-way trips
    } else {
      this.multipleDateSelection = this.type === 2; // Allow multiple date selection for return trips
    }
  
    // Initialize the departure and return date logic
    if (this.departureDate && this.departureDate.formattedDate) {
      this.departureDate = moment(this.departureDate.formattedDate, "DD-MM-YYYY").format("YYYY-MM-DD");
    }
  
    if (this.departureDateReturn && this.departureDateReturn.formattedDate) {
      this.departureDateReturn = moment(this.departureDateReturn.formattedDate, "DD-MM-YYYY").format("YYYY-MM-DD");
    }
  
    // Conditionally set selected dates based on the trip segment type
    if (this.selectedTripSegment === 'oneway') {
      this.selectedDates = [moment(this.departureDate, "YYYY-MM-DD")]; // Only departure date for one-way
    } else if (this.departureDate && this.departureDateReturn) {
      this.selectedDates = [
        moment(this.departureDate, "YYYY-MM-DD"),
        moment(this.departureDateReturn, "YYYY-MM-DD"),
      ];
      this.selectedEndDate = this.selectedDates[1];
      this.highlightRange();
    } else if (this.departureDate) {
      this.selectedDates = [moment(this.departureDate, "YYYY-MM-DD")];
    }
  
    // Generate the calendar with or without a date range
    this.generateCalendar({
      numMonths: 12,
      showPreviousMonths: false,
      preselectedDate: this.departureDate || null,
      dateRange: this.selectedTripSegment === 'oneway'
        ? null // No date range for one-way
        : {
            start: this.departureDate,
            end: this.departureDateReturn || '',
          },
    });
  }
  

  generateCalendar({
    numMonths = 12,
    showPreviousMonths = false,
    preselectedDate = null,
    dateRange = null,
  } = {}) {
    this.months = [];
    const direction = showPreviousMonths ? -1 : 1;
  
    for (let i = 0; i < numMonths; i++) {
      const monthStart = moment(this.today).add(i * direction, 'months').startOf('month');
      const daysInMonth = monthStart.daysInMonth();
      const days = [];
      const startingDay = monthStart.day() === 0 ? 6 : monthStart.day() - 1;
      const startingBlanks = Array(startingDay).fill(null);
  
      for (let d = 1; d <= daysInMonth; d++) {
        const currentDay = moment(monthStart).date(d);
        const isInRange = dateRange
          ? currentDay.isBetween(dateRange.start, dateRange.end, 'day', '[]')
          : false;
  
        const isDisabled = dateRange?.start
          ? currentDay.isBefore(this.today, 'day') || currentDay.isBefore(moment(dateRange.start), 'day')
          : currentDay.isBefore(this.today, 'day');
  
        days.push({
          date: currentDay.date(),
          fullDate: currentDay,
          isSelected: preselectedDate
            ? currentDay.isSame(preselectedDate, 'day') || currentDay.isSame(this.selectedEndDate, 'day')
            : false,
          isDisabled: isDisabled,
          isInRange: isInRange,
          price: `$${Math.floor(Math.random() * 100)}`, // Example price
        });
      }
  
      this.months.push({
        name: monthStart.format('MMMM'),
        year: monthStart.format('YYYY'),
        days: days,
        startingBlanks: startingBlanks,
      });
    }
  
    if (showPreviousMonths) {
      this.months.reverse();
    }
  
    if (dateRange) {
      this.highlightRange();
    }
  }
  

  selectDate(day: any) {
    if (day.isDisabled) return;
  
    if (this.multipleDateSelection) {
      if (
        this.departureDate === day.fullDate.format("YYYY-MM-DD") &&
        this.selectedDates.length === 1
      ) {
        this.selectedDates.push(day.fullDate);
        this.selectedEndDate = day.fullDate;
        this.markSelectedDate(this.departureDate);
        this.markSelectedDate(this.selectedEndDate);
        this.highlightRange();
      } else {
        this.handleMultipleDateSelection(day);
      }
    } else {
      this.handleSingleDateSelection(day);
    }
  }
  

  handleSingleDateSelection(day: any) {
    this.clearSelectedDates();
    this.selectedDates = [day.fullDate];
    day.isSelected = true;
  }

  handleMultipleDateSelection(day: any) {
    if (this.selectedDates.length === 0) {
      // Start selection with the departure date
      this.selectedDates = [moment(this.departureDate)];
      this.markSelectedDate(this.departureDate);
    }
  
    if (this.selectedDates.length === 1) {
      // Select the end date
      day.isSelected = true;
      this.selectedEndDate = day.fullDate;
      this.selectedDates.push(day.fullDate);
      this.markSelectedDate(this.selectedEndDate);
      this.highlightRange();
    } else if (this.selectedDates.length === 2) {
      // If two dates are already selected, reset and start over
      this.clearSelectedDates();
      this.selectedDates = [moment(this.departureDate), day.fullDate];
      this.selectedEndDate = day.fullDate;
      this.markSelectedDate(this.departureDate);
      this.markSelectedDate(this.selectedEndDate);
      this.highlightRange();
    }
  }

  markSelectedDate(date: moment.Moment) {
    this.months.forEach(month => {
      month.days.forEach((day: any) => {
        if (day.fullDate.isSame(date, 'day')) {
          day.isSelected = true;
        }
      });
    });
  }
  

  clearEndDateSelection() {
    this.months.forEach(month => {
      month.days.forEach((day: any) => {
        if (day.fullDate.isSame(this.selectedEndDate)) {
          day.isSelected = false;
          day.isInRange = false;
        }
      });
    });
    this.selectedEndDate = null;
  }

  clearSelectedDates() {
    this.months.forEach(month => {
      month.days.forEach((day: any) => {
        day.isSelected = false;
        day.isInRange = false;
      });
    });
    this.selectedDates = [];
  }
  

  highlightRange() {
    if (this.selectedDates.length < 2) return;

  const start = this.selectedDates[0];
  const end = this.selectedDates[1];

  this.months.forEach(month => {
    month.days.forEach((day: any) => {
      const dayDate = day.fullDate;
      day.isInRange = dayDate.isAfter(start, 'day') && dayDate.isBefore(end, 'day');
      if (dayDate.isSame(start, 'day') || dayDate.isSame(end, 'day')) {
        day.isSelected = true; // Ensure start and end dates remain selected
      }
    });
  });
  }
  

  confirmSelection() {
  // If no dates are selected, use departureDate
  if (this.selectedDates.length === 0 && this.departureDate) {
    this.selectedDates = [moment(this.departureDate, "YYYY-MM-DD")];
  }

  // Sort selected dates
  this.selectedDates.sort((a, b) => a.diff(b));
  const startDate = this.selectedDates[0];
  const endDate =
    this.selectedDates.length > 1
      ? this.selectedDates[this.selectedDates.length - 1]
      : startDate;

  if (this.type === 1) {
    this.modalController.dismiss({
      start_date: startDate.format("YYYY-MM-DD"),
    });
  } else if (this.type === 2) {
    this.modalController.dismiss({
      start_date: startDate.format("YYYY-MM-DD"),
      end_date: endDate.format("YYYY-MM-DD"),
    });

    // Logic for storing in commonStorage
    const startDateFormatted = startDate.format("YYYY-MM-DD");
    const endDateFormatted = endDate.format("YYYY-MM-DD");

    this.commonStorage.localSet(
      "isSameStartEndDate",
      startDateFormatted === endDateFormatted ? "yes" : "no"
    );

    const currentDateFormatted = moment().format("YYYY-MM-DD");
    this.commonStorage.localSet(
      "isCSameStartEndDate",
      startDateFormatted === currentDateFormatted &&
        endDateFormatted === currentDateFormatted
        ? "yes"
        : "no"
    );

    const isNextDay = endDate.isSame(startDate.clone().add(1, "day"), "day");
    this.commonStorage.localSet("isNextDay", isNextDay ? "yes" : "no");
  } else {
    // Handle other types if needed
  }
}

  gotoback() {
    this.modalController.dismiss();
  }
}
