}
ion-card .bus-contact h4 {
  font-size: 14px;
  margin: 0;
}
ion-card .bus-contact .bus-contact-circle {
  width: 40px;
  height: 40px;
  background-color: #25E88A;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
ion-card .payment-details {
  background-color: #F3F3F7;
  border-radius: 10px;
}
ion-card .payment-details h2 {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  text-align: left;
  color: #333333;
}
ion-card .payment-details p.left {
