  text-align: left;
  font-weight: 500;
  color: #333333;
}
ion-card .payment-details p.right {
  font-size: 14px;
  text-align: right;
  font-weight: 600;
  color: #333333;
}
ion-card .payment-details .payment-details-line {
  height: 1px;
  border-top: 1px solid #C2C2C2;
}
ion-card h2.edit-ticket {
  margin: 0;
  text-align: left;
  font-size: 16px;
  color: #191966;
  font-weight: 600;
}
ion-card h2.Method-pay {
  margin: 0;
  text-align: start;
  font-size: 16px;
  font-weight: 600;
  color: #333333;
}

ion-button.Continue-button::part(native) {
