  --background: var(--iconsAndButtonsColor) !important;
  --ion-color-base: var(--iconsAndButtonsColor) !important;
}
.seabirdTheme .searbird-login-btn {
  --background: var(--iconsAndButtonsColor);
  margin: 10px 4px;
  --border-radius: 20px;
