import { Injectable } from '@angular/core';

@Injectable()

export class CommonStorage {
    public requiredData: any = {};

    constructor() { }

    setItem(name, values) {
        this.requiredData[name] = values;
    }
    getItem(name) {
        return this.requiredData[name] ? this.requiredData[name] : this.localGet(name);
    }

    localSet(name, values) {
        // if(typeof values == "string"){
        //     localStorage.setItem(name, values);
        //     return;
        // }
        localStorage.setItem(name, JSON.stringify(values));
        // this.requiredData[name] = values;
    }
    localGet(name) {
        // return JSON.parse(localStorage.getItem(name))?JSON.parse(localStorage.getItem(name)):this.requiredData[name];
        return JSON.parse(localStorage.getItem(name) ) 

    }


    localGetValue(name) {
        const value = localStorage.getItem(name);
        if (value) {
            try {
                return JSON.parse(value);
            } catch (error) {
                console.error(`Error parsing JSON for key "${name}":`, error);
                return null; 
            }
        }
        return null; 
    }
    

    localRemove(name) {
        localStorage.removeItem(name);
        // this.requiredData[name] = null;
    }
    localHas(name) {
        return localStorage.hasOwnProperty(name);
    }
}