        toolbarcolor: '#FF0000', // Customize toolbar color (optional)
        closebuttoncolor: '#00adb5', // Customize close button color (optional)
        hideurlbar: 'yes', // Hide the URL bar (optional)
        location: 'yes', // Show the URL in the location bar
