import { Component, OnInit } from '@angular/core';
import { ApiFactory, AppData, LoaderService, UtilProvider } from 'src/app/services';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-privacy-policy',
  templateUrl: './privacy-policy.page.html',
  styleUrls: ['./privacy-policy.page.scss'],
})
export class PrivacyPolicyPage implements OnInit {
  privacypolicy: any;
  mobileWebVersion:String = "";
  iosVersion:String = "";


  constructor(
    private apiFactory: ApiFactory,
    private appData : AppData,
    public util: UtilProvider,
    private loader:LoaderService,
    private location: Location,
    private router: Router
  ) {

      this.mobileWebVersion = this.appData.mobileWebVersion;
      this.iosVersion = this.appData.iosVersion;

      this.defaultCall()
  }

  ngOnInit() {
  }

  defaultCall() {
    this.loader.showLoadingDefault();
    this.apiFactory.getPrivacyPolicy()
      .subscribe(
        privacypolicy => {
          this.privacypolicy = privacypolicy.privacy_policy;
          this.loader.hideLoadingDefault();
        },
        error => {
          this.loader.hideLoadingDefault();
         }
      )
  }
  handleBackButton() {
    // if (this.location.isCurrentPathEqualTo('/home')) {
    //   // Prevent navigating back to itself
    //   return;
    // }
    
    if (window.history.length > 1) {
      // If history is present, go back
      this.location.back();
    } else {
      // If no history, navigate to the home page
      this.router.navigate(['tabs/home']);
    }
  }
}
