  color: #4a4a4a;
  margin: 5px;
}

.themeone-legend-txt {
  font-size: 14px;
  color: #4a4a4a;
  margin: 5px;
  text-align: center;
  width: 200px;
}

.seabird_fare {
  color: #00904B;
  font-size: 12px;
  font-weight: 700;
}

.seabird_fare_row {
  border: 1px solid #626262;
  border-radius: 10px;
  background: white;
  padding: 10px 5px;
  margin-bottom: 12px;
}

.seabirdPayContainer {
  padding-bottom: 20px;
}

.seabird_seatNum {
  font-size: 12px;
  font-weight: 700;
}

.ourbus-scroll-container {
  width: 100%;
  overflow-x: auto;
  display: block;
  margin-top: 12px;
}

.ourbus-scroll-content {
  display: flex;
  flex-wrap: nowrap;
  width: max-content;
  margin-top: 15px;
}

.ourbustheme ion-row {
  display: flex;
  flex-wrap: nowrap;
}

.ourbus-seat-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-right: 10px;
}

.ourbus-legend-seat {
  width: 25px;
  height: 25px;
  border-radius: 5px;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
}

.ourbus-legend-txt {
  font-size: 12px;
}

.ourbus_btn {
  height: 50px;
  box-shadow: none;
  --border-radius: 8px;
  font-size: 18px;
  font-weight: 700;
  --background:var(--iconsAndButtonsColor);
  --color:var(--primaryText);
  --background-activated: var(--iconsAndButtonsColor) !important;
}

.ourbus-hd-ln {
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #747474;
  margin-top: 5px;
  margin-left: 10px;
}

.ourbus-inline-icon {
  width: 15px;
  height: 15px;
  margin: 0 5px;
  vertical-align: middle;
}

.singleview_layout #mySlider {
  width: 94% !important;
}
.singleview_layout #lower {
  width: 57% !important;
  margin: 25px 0px;
}
.singleview_layout #upper {
  width: 57% !important;
  margin: 25px 0px;
}

.lower_icon {
  width: 32px;
  height: 28px;
  top: 23px;
  filter: invert(46%) sepia(6%) saturate(12%) hue-rotate(324deg) brightness(98%) contrast(84%);
}

.upper_icon {
  width: 32px;
  height: 28px;
  top: 23px;
  filter: invert(46%) sepia(6%) saturate(12%) hue-rotate(324deg) brightness(98%) contrast(84%);
  visibility: hidden;
}

.our-bus-svg-text {
  position: absolute;
  top: 60%;
  left: 60%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
  white-space: normal;
  word-wrap: break-word;
  font-size: 10px;
  color: #000;
}

.our-bus-svg-text-slepper {
  position: absolute;
  top: 60%;
  left: 55%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
  white-space: normal;
  word-wrap: break-word;
  font-size: 10px;
  color: #000;
}

.ourbustheme .horizontal {
  transform: rotate(270deg);
  margin-top: 0px;
  margin-left: -20px;
}
.ourbustheme .horizontal .our-bus-svg-text {
  transform: rotate(45deg);
  margin-top: 0px;
  margin-left: -12px;
}

.drawer-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 0;
}

.pull-icon {
  font-size: 24px;
  color: #666; /* Adjust color as needed */
}

.seat_text {
  text-transform: uppercase;
  color: #747474;
}

ion-segment-button {
  --indicator-color: var(--iconsAndButtonsColor) !important;
  --indicator-color-checked: transparent !important;
}

.seat_icons {
  height: 30px;
  padding: 4px 4px 0px;
  width: 32px;
