.header:after {
  content: none;
}

.Event-calendar {
  background-color: #ffffff;
  color: black;
  border-radius: 0px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
}

.Event-calendar .unit {
  float: left;
  width: 12.28%;
  text-align: center;
  color: #fff;
}
.Event-calendar .unit.selected b {
  background-color: initial !important;
  background-image: linear-gradient(50deg, #3c7e33 0%, #3c9e33 100%) !important;
}

.Event-calendar .years .prev {
  text-align: left;
  width: 34%;
  display: flex;
  height: 100%;
  color: #999;
  font-size: 7vw;
  align-items: center;
  justify-content: flex-start;
}

.Event-calendar .years .next {
  text-align: right;
  width: 34%;
  display: flex;
  height: 100%;
  color: #999;
  font-size: 7vw;
  align-items: center;
  justify-content: flex-end;
}

.Event-calendar .years img {
  width: 30px;
  opacity: 0.55;
}

.Event-calendar .years .prev em,
.Event-calendar .years .next em {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: 1px solid #666;
  color: #666;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
}

.Event-calendar .years .prev em:before,
.Event-calendar .years .next em:before {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  margin-top: 6px;
  font-size: 0;
  content: "";
  border-style: solid;
  border-width: 7px;
}

.Event-calendar .years .prev em:before {
  top: 1px;
  left: 2px;
  border-color: transparent #666 transparent transparent;
}

.Event-calendar .years .next em:before {
  top: 1px;
  left: 12px;
  border-color: transparent transparent transparent #666;
}

.years {
  height: 12vw;
  background: #FFDE21;
  color: white;
}

.Event-calendar .years .monyear {
  float: left;
  width: 32%;
  text-align: center;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.Event-calendar .days {
  margin-top: 2px;
  border-top: 1px solid #d7dee8;
}

.Event-calendar .WeekDays .unit {
  margin: 1%;
}

.Event-calendar .days .unit {
  height: 60px;
  /* line-height: 60px; */
  /* background: #d7dee8; */
  /* text-align: center; */
  /* position: absolute; */
  margin: 1%;
  overflow: hidden;
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: center; */
  height: 12vw;
}

.Event-calendar .days .unit b {
  color: white;
  font-weight: normal;
  display: block;
  width: 100%;
  height: 100%;
  border: 2px solid white;
  /* background: #434a54; */
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.Event-calendar .days .unit b:hover {
  background: #ccc !important;
}

.Event-calendar .days .unit span {
  background: red;
  padding: 5px;
  border-radius: 50%;
}

.Event-calendar .days .unit .tl {
  z-index: 10;
  position: absolute;
  margin-left: -85%;
  background: #f6bc41;
}

.Event-calendar .days .unit .tr {
  z-index: 10;
  position: absolute;
  margin-left: -20%;
  background: #3ab0d8;
}

.Event-calendar .days .unit .bl {
  z-index: 10;
  position: absolute;
  margin-left: -40px;
  margin-top: 31px;
  background: #e9593e;
}

.Event-calendar .days .unit .br {
  z-index: 10;
  position: absolute;
  margin-left: -20%;
  /*Else correct one with px*/
  /*margin-left: -15px;*/
  /*Else correct one with px*/
  margin-top: 31px;
  background: #656d78;
}

.Event-calendar .days .unit.older b {
  width: auto;
  height: auto;
  color: #ddd;
  cursor: default;
  background-color: transparent;
}

/*Pnkr Code CSS*/
ul li {
  background: #e4ebf3;
}

ul li span {
  padding: 5px;
  border-radius: 50%;
}

ul li .glyphicon-header {
  background: #f6bc41;
}

ul li .glyphicon-eur {
  background: #3ab0d8;
}

ul li .glyphicon-king {
  background: #656d78;
}

ul li .glyphicon-plus {
  background: #e9593e;
}

.today {
