}
.past b {
  opacity: 0.2;
}

.future {
  opacity: 1;
}
.future b {
  opacity: 0.2;
}

.transparent {
  opacity: 0;
}

.unit b:hover b {
  opacity: 0.8 !important;
}

.inactive {
  opacity: 0.5;
}

