import { CommonService } from './common-service';
import { CommonStorage } from './common-storage';
import { Injectable } from '@angular/core';

/*
  Generated class for the UtilProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class ThemeService {
  themeColors: any;
  themeColorName: any = {};
  primary: string;
  secondary: string;
  primaryText: string;   ////button text color
  secondaryText: string;
  placeholderColor: string;
  viewBgColor: string; // App Background Color like container background color 
  iconsAndButtonsColor: string; //Button Background color
  sectionHeaderTextColor: string;   //Red Color (Ex: Contact details / Traveller Details )


  constructor(
    private commonStorage: CommonStorage,
    private commonService: CommonService
  ) {
    this.themeColorName = commonStorage.localGet("appColors");

    if (this.themeColorName) {
      this.primary = this.themeColorName.primary;
      // this.primary="#1463e0";
      this.secondary = '#2b332d';
      // this.primaryText="#fff";
      this.primaryText = this.themeColorName.primaryText;
      this.viewBgColor = this.themeColorName.viewBgColor;
      this.iconsAndButtonsColor = this.themeColorName.iconsAndButtonsColor;
      this.sectionHeaderTextColor = this.themeColorName.sectionHeaderTextColor;
      this.placeholderColor = this.themeColorName.textFieldPlaceholderColor;

      this.secondaryText = '#fff';
      document.body.style.setProperty('--primary', this.primary);
      document.body.style.setProperty('--iconsAndButtonsColor', this.iconsAndButtonsColor);
      document.body.style.setProperty('--primaryText', this.primaryText);
      document.body.style.setProperty('--viewBgColor', this.viewBgColor);
      document.body.style.setProperty('--sectionHeaderTextColor', this.sectionHeaderTextColor);
      document.body.style.setProperty('--placeholderColor', this.placeholderColor);

      // console.log("place holder", this.placeholderColor);
      // console.log(this.themeColorName, this.primary, this.themeColorName.nav_bg_color);
    }
    this.commonService.metaDataLoaded
      .subscribe(data => {
        try {
          this.themeColorName = data.appColors;
          this.primary = this.themeColorName.primary;
          this.secondary = '#2b332d';
          this.primaryText = this.themeColorName.primaryText;
          this.viewBgColor = this.themeColorName.viewBgColor;
          this.iconsAndButtonsColor = this.themeColorName.iconsAndButtonsColor;
          this.sectionHeaderTextColor = this.themeColorName.sectionHeaderTextColor;
          this.placeholderColor = this.themeColorName.textFieldPlaceholderColor;

          // console.log("next", this.placeholderColor)

          this.secondaryText = '#fff';
          document.body.style.setProperty('--iconsAndButtonsColor', this.iconsAndButtonsColor);
          document.body.style.setProperty('--primaryText', this.primaryText);
          document.body.style.setProperty('--viewBgColor', this.viewBgColor);
          document.body.style.setProperty('--sectionHeaderTextColor', this.sectionHeaderTextColor);
          document.body.style.setProperty('--placeholderColor', this.placeholderColor);
          // this.themeColorName = this.commonStorage.getItem("metaData").themeName;
          // this.themeColorName = this.themeColorName.substring(0, this.themeColorName.indexOf('.'));
          // this.themeColorName = this.themeColorName ? this.themeColorName + '_' : '';
          // window.localStorage.setItem("themeName", this.themeColorName);
          // this.themeColorName = 'purple_';
        } catch (e) {
          console.log("Exception in ThemeService -->  this.commonService.metaDataLoaded", e)
        }
      })

  }
  getCoros() {
    return {
      primary: this.primary,
      primaryText: this.primaryText
    }
  }
  getThemeHeaderColor() {
    // let themeColorName = this.commonStorage.getItem("metaData").themeName;
    // themeColorName = themeColorName.substring(0, themeColorName.indexOf('.'));
    // switch (themeColorName) {
    //   case 'default': return '#fcdd09';
    //   case 'blue': return '#0f47af';
    //   case 'dark_blue': return '#14114e';
    //   case 'light_blue': return '#0c00ff';
    //   case 'yellow': return '#fcdd09';
    //   case 'dark_yellow': return '#9e8a01';
    //   case 'red': return '#da121a';
    //   case 'dark_red': return '#930006';
    //   case 'green': return '#078930';
    //   case 'dark_green': return '#253e08';
    //   case 'orange': return '#eb771e';
    //   case 'purple': return '#742c64';
    //   case 'dark_purple': return '##4e126a';
    // }
  }
}
