    preZero(val): string {
        // Prepend zero if smaller than 10
        return val < '10' ? '0' + val : val;
    }

    isPast(d: number, m: number, y: number): boolean {
        let today = new Date();
        today.setHours(0, 0, 0, 0);

        let dt = new Date(y, m - 1, d - 1)
        if (dt < today) {
            return true;
        }
        return false;
    }

