    isSameOrOneDayAfterGivenDate(d: number, m: number, y: number, travel_return_date: Date): boolean {
        // Create a Date object from the provided day, month, and year
        let dt = new Date(y, m - 1, d);
        dt.setHours(0, 0, 0, 0);
    
        // Set travel_return_date to midnight for accurate comparison
        travel_return_date.setHours(0, 0, 0, 0);
    
        // Check if the date is the same as travel_return_date
        if (dt.getTime() === travel_return_date.getTime()) {
            return true;
        }
    
        // Add one day to dt to check if it’s one day after travel_return_date
        dt.setDate(dt.getDate());
    
        // Check if the modified date (one day after) matches travel_return_date
        return dt.getTime() === travel_return_date.getTime();
    }

