    isSameOrOneDayAfterTravelDate(d: number, m: number, y: number, travel_date: Date): boolean {
        // Create a Date object from the provided day, month, and year
        let dt = new Date(y, m - 1, d);
        dt.setHours(0, 0, 0, 0); // Set to midnight for accurate comparison
    
        // Set travel_date to midnight for accurate comparison
        travel_date.setHours(0, 0, 0, 0);
    
        // Check if the date is the same as travel_date
        if (dt.getTime() === travel_date.getTime()) {
            return true;
        }
    
        // Add one day to dt to check if it’s one day after travel_date
        dt.setDate(dt.getDate() + 1);
    
        // Check if the modified date (one day after) matches travel_date
        return dt.getTime() === travel_date.getTime();
    }
    
    

    parseDateFromString(dateStr: string): Date {
        const [day, month, year] = dateStr.split("-").map(Number);
        return new Date(year, month - 1, day); // month - 1 because months are zero-based
    }    


    isPastDate(date, format = 'dd/mm/yyyy'): boolean {
        let today = new Date();
        today.setHours(0, 0, 0, 0);
        let d = 0, m = 0, y = 0;

        if (format == "dd/mm/yyyy") {
            d = +date.substr(0, 2);
            m = +date.substr(3, 2);
            y = +date.substr(6, 4);
        } else if (format == "mm/dd/yyyy") {
            m = +date.substr(0, 2);
            d = +date.substr(3, 2);
            y = +date.substr(6, 4);
        } else if (format == "yyyy/mm/dd") {
            y = +date.substr(0, 4);
            m = +date.substr(5, 2);
            d = +date.substr(8, 2);
        }

        let dt = new Date(y, m - 1, d)
        if (dt < today) {
            return true;
        }
        return false;
    }


