    getDayname(day, type = "half") {
        let dayName: string;
        switch (day) {
            case 0:
                dayName = type == 'half' ? 'Sun' : "Sunday";
                break;
            case 1:
                dayName = type == 'half' ? 'Mon' : "Monday";
                break;
            case 2:
                dayName = type == 'half' ? 'Tue' : "Tuesday";
                break;
            case 3:
                dayName = type == 'half' ? 'Wed' : "Wednesday";
                break;
            case 4:
                dayName = type == 'half' ? 'Thu' : "Thurday";
                break;
            case 5:
                dayName = type == 'half' ? 'Fri' : "Friday";
                break;
            case 6:
                dayName = type == 'half' ? 'Sat' : "Saturday";
                break;
            default:
                dayName = type == 'Sun' ? 'Jan' : "Sunday";
                break;

        }
        return dayName;
    }


