import { Component, OnInit, Input } from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-theme-apply-coupon-modal',
  templateUrl: `./theme-1-apply-coupon-modal.component.html`,
  // templateUrl: `./${environment.client.templateUrl}-apply-coupon-modal.component.html`,
  styleUrls: ['./theme-apply-coupon-modal.component.scss'],
})
export class ThemeApplyCouponModalComponent implements OnInit {
  @Input() offerCoupons = [];
  newCouponCode: string;
  constructor(
    public modalController: ModalController,
    private params : NavParams,
  ) {
    this.offerCoupons = this.params.get('offerCoupons');
  }

  ngOnInit() {
    this.offerCoupons = this.params.get('offerCoupons');
    // console.log("this.offerCoupons : ", this.offerCoupons);
  }

  /**
   * @param coupon coupon code
   * @param isMenual true, if user enter coupo-code menually from input field. 
   */
  applyCoupon(coupon: any, isMenual?: boolean) {
    /** @note if user enter coupon-code menually */
    if (isMenual) {
      if (coupon && coupon.length) { this.modalController.dismiss(coupon) }
    } else { /** @note apply coupon from coupons-list. */
      this.modalController.dismiss(coupon.coupon_code)
    }
  }

}
