* {
  font-family: "NotoSans";
}

ion-button {
  font-family: "NotoSans";
}

.legend-box {
  border-radius: 8px;
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 24px 38px 3px rgba(0, 0, 0, 0.14);
  background-color: #ffffff !important;
  opacity: 1 !important;
  padding: 18px 16px;
  width: 325px;
  height: auto;
}

.whole {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
}

ion-menu {
  --width: 100%;
}

.icn-main-wthtlogin {
  vertical-align: middle;
  position: initial;
  right: 0;
  padding: 0;
  width: 140px;
  height: 40px;
  top: 0;
  float: right;
  margin-right: 10px;
  margin-top: 4px;
}

.ourbus_login_border {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ourbus_login {
  font-size: 16px;
  font-weight: 500;
  color: #4CAF50;
  line-height: 24px;
  border: 1px solid #4CAF50;
  padding: 12px 24px;
  border-radius: 8px;
}

.icon-set {
  width: 24px;
  height: 24px;
  color: #2D2D2D;
  margin-right: 8px;
}

.textSet {
  font-size: 14px;
  color: #2D2D2D;
  font-weight: 500;
}

.modify {
  margin-top: 10px;
  margin-bottom: 10px;
}

.txt-msg-hd {
  text-align: center;
  font-size: 22px;
  color: #2D2D2D;
  font-weight: 700;
}

.txt-msg {
  text-align: center;
  font-size: 18px;
  font-weight: 300;
  padding: 10px;
  color: #2D2D2D;
}

.brws-txt {
  border: 1px solid #4CAF50;
  font-size: 18px;
  padding: 10px;
  text-align: center;
  color: #4CAF50;
  font-weight: 300;
  border-radius: 10px;
}

.themeone-dwn-txt {
  background-color: #4CAF50;
  font-size: 18px;
  padding: 10px;
  text-align: center;
  color: white;
  font-weight: 700;
  border-radius: 10px;
