import { Component, OnInit } from '@angular/core';
import { LoaderService, UtilProvider, ExceptionProvider, CommonStorage, CommonService, ApiFactory, DateService } from '../services';
import { ThemeService } from '../services/theme-service';
import { ModalController, NavController, NavParams } from '@ionic/angular';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { GlobalDataService } from '../services/global-data.service';

@Component({
  selector: 'app-add-existing-passenger',
  templateUrl: './add-existing-passenger.html',
  styleUrls: ['./add-existing-passenger.scss'],
})
export class AddExistingPassengerComponent implements OnInit {
  travellersList: any;
  selectedPassengers: any = [];
  travellerlength: any;
  constructor(private loader: LoaderService,
    private util: UtilProvider,
    public theme: ThemeService,
    private ex: ExceptionProvider,
    private commonStorage: CommonStorage,
    private commonService: CommonService,
    private apiFactory: ApiFactory, public modalController: ModalController,
    public dateService: DateService,
    public navCtrl: NavController,
    private route: ActivatedRoute,
    private router: Router,
    public globalData: GlobalDataService,
    private modalCtrl: ModalController, navParams: NavParams, ) {
    this.commonService.gTrack('addexistingpassenger');
    this.travellerlength = navParams.get('travellerLength');
    this.selectedPassengers = navParams.get('passengerList');
    console.log("selectedPassengers>>>", this.selectedPassengers);
    this.getPassengerList();
    this.commonService.gTrack('addExistingPassenger','“Add existing customer” is clicked')

  }

  ngOnInit() {
    
   }

  getPassengerList() {
    this.apiFactory.travellersList()
      .subscribe(
        result => {
          this.loader.hideLoadingDefault();
          console.log(result);
          if (typeof result.code == "undefined") {

            this.travellersList = result.travellers_list;
            this.travellersList.forEach((value) => {
              value['checked'] = false;
            });
            console.log(this.travellersList);
            this.selectedPassengers.forEach((s) => {
              if (s.checked) {
                this.travellersList.forEach((t) => {
                  if (s.id == t.id) t['checked'] = s.checked;
                });
              }
            });

            // let navigationExtras: NavigationExtras = {
            //   state: {
            //     user: ""
            //   }
            // };
            // this.router.navigate(['cancel-confirmation-ticket'], navigationExtras);

          } else {
            setTimeout(() => {
              this.util.showAlert('', result.message);
            });
          }

        },
        err => {
          this.loader.hideLoadingDefault()
          if (!this.util.isConnected()) {
            // this.util.showAlert(this.util.tranlateInstant("ERROR_LOADING"),this.util.tranlateInstant("INTERNET_NOT_CONNECTED_PLEASE_CONNECT_INTERNET_AND_TRY_AFTER_AGAIN"));
          } else {
            // this.util.showAlert(this.util.tranlateInstant("ERROR_LOADING"),this.util.tranlateInstant("INTERNET_NOT_CONNECTED_PLEASE_CONNECT_INTERNET_AND_TRY_AFTER_AGAIN"));
          }
          // this.ex.call('CancellationPage','continueToCancel() --> this.apiFactory.getTicketDetails(this.cancellationForm.value.pnrNumber)',err,'')
        }
      )
  }

  selectPassenger(data) {
    console.log(data);
    if (data.checked != true) {
      this.selectedPassengers.push(data);
    } else {
      let newArray = this.selectedPassengers.filter(function (el) {
        return el.id !== data.id;
      });
      this.selectedPassengers = newArray;
    }
    console.log(this.selectedPassengers)
  }

  addTraveller() {
    if (this.selectedPassengers.length > this.travellerlength) {
      this.util.showAlert('', "you can't select more than " + this.travellerlength + " passenger");
      return false;
    } else {
      this.dismiss();
    }

  }
  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalCtrl.dismiss(this.selectedPassengers);
  }
}
