  //     const [firstID, secondID] = originId.split(':');
  //     if (firstID === '-1') {
  //       cityArray.push({ id: bpdpStagesArray[i].id, name: bpdpStagesArray[i].name });
  //     }
  //   }

  //   for (let i = 0; i < cityArray.length; i++) {
  //     const arrNew = [];

  //     const [firstID, secondID] = cityArray[i].id.split(':');
  //     for (let k = 0; k < bpdpStagesArray.length; k++) {

  //       if (secondID === bpdpStagesArray[k].id.split(':')[1]) {
