import { dismiss } from '@ionic/core/dist/types/utils/overlays';
import { Component, OnInit, ViewChild } from '@angular/core';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ApiFactory, AppData, AuthenticationService, CommonService, CommonStorage, LoaderService, UtilProvider } from '../services';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { IonInput } from '@ionic/angular';
import countryData from '../../countries/countries.json';
@Component({
  selector: 'app-forced-login-with-otp',
  templateUrl: './forced-login-with-otp.page.html',
  styleUrls: ['./forced-login-with-otp.page.scss'],
})
export class ForcedLoginWithOtpPage implements OnInit {
  logo: any;
  otpForm: any;
  validateOtpForm: any;
  phoneNumCount: any = '';
  showForm: boolean;
  remainingTime: any = 60;
  time: string;
  otpKey: any;
  code: any;
  key: any;
  referralCode: boolean;
  activateNoteModal: any = false;
  activateToken: any;
  metaData: any;
  isButtonDisabled = true;
  countryData: any;

  constructor(private commonStorage: CommonStorage,
    private formBuilder: UntypedFormBuilder,
    private apiFactory : ApiFactory,
    private loader: LoaderService,
    private util: UtilProvider,
    private router: Router,
    private commonService: CommonService,
    private authenticationService: AuthenticationService,
    public appData: AppData,
    private modalctrl:ModalController
    ) {
      this.appData.newTheme = this.util.getNewTheme();
    this.logo = this.commonStorage.getItem("metaData").mobileLoginPageLogo?this.commonStorage.getItem("metaData").mobileLoginPageLogo:this.commonStorage.getItem("metaData").headerLogo;
    this.defaultCall();
    // this.phoneNumCount = this.commonStorage.localGet('metaData').phoneNumCount;
    // if(  this.phoneNumCount === 0 ||  this.phoneNumCount === null ){
    //   this.phoneNumCount = "";
    // }
   }

   ngAfterViewInit() {
  }
  

  ngOnInit() {
    // this.mobileInput.setFocus();
    this.countryData = countryData;
    this.countryData = this.countryData.filter(country => country.dialCode === '+91');
    this.metaData = this.commonStorage.getItem('metaData');
    console.log("this.metaData",this.metaData);
    
    this.phoneNumCount = this.commonStorage.getItem('metaData').phoneNumCount ? this.commonStorage.getItem('metaData').phoneNumCount : 10;
    }
    
  OtpFormValidation() {
    let minPhoneCount;
    let maxPhoneCount;

    if(this.phoneNumCount === "" ){
      minPhoneCount = 10;
      maxPhoneCount = 14;
    }else{
      minPhoneCount = this.phoneNumCount;
      maxPhoneCount = this.phoneNumCount;
    }

    this.otpForm = this.formBuilder.group({
      mobile_number: ['', Validators.compose([Validators.required, Validators.maxLength(maxPhoneCount), Validators.minLength(minPhoneCount)])],
    })
    this.validateOtpForm = this.formBuilder.group({
      otp: ['', Validators.compose([Validators.required])],
      referal_code: ['', Validators.compose([Validators.required])]
    })
    // this.otpForm = this.formBuilder.group({
    //   mobile_number: ['', Validators.compose([Validators.required, Validators.maxLength(this.phoneNumCount), Validators.minLength(this.phoneNumCount)])],
    // })
    // this.validateOtpForm = this.formBuilder.group({
    //   otp: ['', Validators.compose([Validators.required])],
    // })
  }
  defaultCall() {
    this.OtpFormValidation();
  }

  backtologin(){
    console.log("backtologin",this.showForm);
    
    this.showForm = false;
    this.validateOtpForm.reset();
  }

  gototermsCondition(){
    this.modalctrl.dismiss();
    this.router.navigate(['term-conditions']);
    // this.router.navigate([']);
  }
  
  generateOTP(type){    
    console.log(type);
    if(type == 'resend'){
      this.validateOtpForm.reset();
    }
    // if (!this.otpForm.value.mobile_number) {
    //   this.util.showToast("Please enter valid mobile number");
    //   return
    // }
    // if (this.otpForm.value.mobile_number.length < 10) {
    //   this.util.showToast("Enter mobile number");
    //   return
    // }
    this.remainingTime = 59;
    if (!this.otpForm.value.mobile_number) {
      this.util.showToast("Please enter valid mobile number");
      return
    }

    let mob: string = this.otpForm.value.mobile_number.toString();
    if(this.phoneNumCount === ""){
      if(mob.length < 10 ){
        this.util.showToast("Phone number digits should be longer than 10 and shorter than 14");
        return
      }else if(mob.length > 14 ){
        this.util.showToast("Phone number digits should be longer than 10 and shorter than 14");
        return
      }
      
    }else{
      if(mob.length != this.phoneNumCount ){
        this.util.showToast("Phone number should be "+this.phoneNumCount+" digit only");
        return
      }
    }
    this.loader.showLoadingDefault();
    this.apiFactory.forcedLoginWithOtp(this.otpForm.value.mobile_number)
    .subscribe(
      res => {
        this.loader.hideLoadingDefault();
        if(res.code == 200){
          this.showForm = true;
          this.referralCode = true
          this.startTimer();
          this.otpKey = res.body.otp_key;
          this.code = res.code
        }else if(res.code == 422){
          // this.util.showToast(res.body.message);
          this.apiFactory.loginwithOTP(this.otpForm.value.mobile_number)
          .subscribe(result => {
        
        this.loader.hideLoadingDefault();
        try {
          if (result.code == 200) {
            this.util.showToast("OTP sent successfully", "");
              this.showForm = true;
              this.referralCode = false
              this.startTimer();
            // this.navCtrl.setRoot(HomePage);
            this.key = result.body.key;
          }else if(result.code == 491){
            this.activateNoteModal = true;
            this.activateToken = result.body.auth_token
          }
           else {
            // this.showError(result.body.message);
            if (result.error) this.util.showAlert('', result.error.message);
            this.util.showAlert('', result.message)
          }

          // console.log('otp --->', result)
        } catch (e) {
          // console.log("failed --> signup", e)
        }

      }, error => {
        this.loader.hideLoadingDefault();
        this.util.showAlert('Failed', 'Cannot get OTP , try again later')
      });
        }else{
          this.util.showToast(res.body.message);
        }
      },
      err => {

      }
    )
  }
  onInputOtpChange(ev){
    const input = ev.target as HTMLInputElement;
    let value = input.value;
    value = value.replace(/[^0-9]/g, '');
    // const numericValue = value.replace(/[^0-9]/g, '');
    input.value = value;
    this.validateOtpForm.controls['otp'].setValue(value);
  }
  enforceMaxLength(event: any) {
    const input = event.target as HTMLInputElement;
    let sanitizedValue = input.value.replace(/[^0-9]/g, '');
    sanitizedValue = sanitizedValue.slice(0, 10);
    input.value = sanitizedValue;
    // const formControl = this.formData.get('emergencyPhoneNumber');
    // if (formControl) {
    //   formControl.setValue(sanitizedValue, { emitEvent: false });
    // }
  }
  enforceMaxLengthOtp(event: any) {
    const input = event.target as HTMLInputElement;
    let sanitizedValue = input.value.replace(/[^0-9]/g, '');
    sanitizedValue = sanitizedValue.slice(0, 6);
    input.value = sanitizedValue;
    // const formControl = this.formData.get('emergencyPhoneNumber');
    // if (formControl) {
    //   formControl.setValue(sanitizedValue, { emitEvent: false });
    // }
  }
  validateReferal(){
    this.apiFactory.validateReferalCode(this.validateOtpForm.value.referal_code).subscribe(res =>{
      if(res.code==200){
        this.validateOTP()
      }else{
        this.util.showToast(res.message)
      }
    })
  }
  onInputChange(ev) {
    // console.log(ev);
    const input = ev.target as HTMLInputElement;
    let value = input.value;
    value = value.replace(/[^0-9]/g, '');
    // const numericValue = value.replace(/[^0-9]/g, '');

    const trimmedValue = parseInt(value, 10).toString();
    console.log(trimmedValue)
    // If input is empty (leading zeros turned empty), set value to '' or any default like '0'
    if (trimmedValue =='0') {
      input.value = '';
      this.otpForm.controls['mobile_number'].setValue(input.value);
      return;
    }

    if (value.length > 10) {
      value = value.substring(0, 10); 
    }
    // input.value = numericValue;
    this.otpForm.controls['mobile_number'].setValue(value);

    let mob: string = value;
    if(this.otpForm.value.mobile_number) {
     mob = this.otpForm.value.mobile_number.toString();
    }
    let minPhoneCount = this.phoneNumCount === "" ? 0 : this.phoneNumCount;
    let maxPhoneCount = this.phoneNumCount === "" ? 18 : this.phoneNumCount;
    if (mob.length > this.phoneNumCount) {
      this.otpForm.controls['mobile_number'].setValue(mob.substr(0, maxPhoneCount));
    }
    // const truncatedValue = numericValue.substring(0, 10);
    this.isButtonDisabled = value.length !== 10; 
    this.showForm = false;
    this.validateOtpForm.reset();
  }

  getTimerClock(inputSeconds: number) {
    var sec_num = parseInt(inputSeconds.toString(), 10);
    this.remainingTime = sec_num; //Define variable
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);
    var hoursString = '';
    var minutesString = '';
    var secondsString = '';
    hoursString = (hours < 10) ? "0" + hours : hours.toString();
    minutesString = (minutes < 10) ? "0" + minutes : minutes.toString();
    secondsString = (seconds < 10) ? "0" + seconds : seconds.toString();  
    return minutesString + ':' + secondsString;
    }
  startTimer() {   
      var counter = 
      setTimeout(() => {
         this.time = this.getTimerClock(this.remainingTime);
        //  console.log(this.time);
        if (this.remainingTime > 0) {
          this.startTimer();
          this.remainingTime--;
        }
        else {
          clearInterval(counter);
        }
      }, 1000);
    }
  validateOTP(){
    let obj = {
      "otpKey": this.otpKey,
      "MobileNumber" : this.otpForm.value.mobile_number,
      "otp": this.validateOtpForm.value.otp,
      "referalCode": this.validateOtpForm.value.referal_code
    };
    if(this.otpKey){
      this.commonStorage.localSet("sign-otp",obj);
      if(!this.otpForm.value.mobile_number  && this.otpForm.value.mobile_number == null){
        this.util.showToast('Please enter the mobile number')
      }else if(!this.validateOtpForm.value.otp && this.validateOtpForm.value.otp == null){
        this.util.showToast('Please enter the otp')
      }
      else{
        this.loader.showLoadingDefault();
        this.apiFactory.forcedValidateOtp(this.otpKey,this.validateOtpForm.value.otp).subscribe(result =>{
          if(result.valid == true){
            this.loader.hideLoadingDefault();
            this.modalctrl.dismiss();

            // this.commonStorage.localSet('autofillData', false);
            // this.commonStorage.localRemove('ticketRecentData')
            // this.commonStorage.localRemove('ticketRecentData');
            // this.commonStorage.localRemove('passenger_personal_contact');
            // this.commonStorage.localRemove('passenger_personal_data');

            this.router.navigate(['sign-up']);
            this.validateOtpForm.reset();
            this.otpForm.reset();
          }else{
            this.util.showToast('Invalid OTP');
            this.loader.hideLoadingDefault();
          }
          // console.log(result,"forcedValidateOtp")
        },error => {
          this.commonService.gTrack('login', 'Login with OTP failed');
          this.loader.hideLoadingDefault();
        })
        // this.router.navigate(['sign-up']);
      }
    }else{
      this.loader.showLoadingDefault();
      if(this.validateOtpForm.value.otp != null && this.key != null && this.otpForm.value.mobile_number != null){
        this.apiFactory.validateOtpLogin(this.validateOtpForm.value.otp, this.key, this.otpForm.value.mobile_number)
        .subscribe(result => {
          this.loader.hideLoadingDefault();
          try {
            if (result.code == 200) {
              // console.log(result);
              // this.validateOtpForm.reset();
              // this.otpForm.reset();
              console.log(result);
              // this.validateOtpForm.reset();
              // this.otpForm.reset();

              // this.commonStorage.localSet('autofillData', false);
              // this.commonStorage.localRemove('ticketRecentData')
              // this.commonStorage.localRemove('ticketRecentData');
              // this.commonStorage.localRemove('passenger_personal_contact');
              // this.commonStorage.localRemove('passenger_personal_data');

              let body = (result.body)
              let token = body.authentication_token;
              localStorage.setItem('currentUser', JSON.stringify({ email: body.email, token: token, userFull: body, user: this.authenticationService.setUser(body) }));
              this.authenticationService.token = token;
              // console.log(token);
              this.util.showToast("Login successful");
              this.commonService.gTrack('login', 'Login with OTP successful');
              this.apiFactory.getMetaAppData()
              .subscribe(
                loginResponse => {
                  if (!loginResponse.code) {
                    let localData = loginResponse[0];
                    this.commonService.setMetaApp(localData);
                  }
                }
              );
              if(this.metaData.msiteFolder=='ourbustheme'){
                this.modalctrl.dismiss();
                this.router.navigate(['home']).then(() => {
                  window.location.reload();
                });
                this.validateOtpForm.reset();
                this.otpForm.reset();
              }else{
                this.router.navigate(['tabs/home']);
                this.validateOtpForm.reset();
                this.otpForm.reset();
              }
            } else {
              this.commonService.gTrack('login', 'Login with OTP failed');
              if (result.error)
                this.util.showAlert('', result.error.message);
              else
                this.util.showAlert('', result.body.message);
            }
  
            // console.log('otp --->', result)
          } catch (e) {
            // console.log("failed --> signup", e)
          }
  
        }, error => {
          this.loader.hideLoadingDefault();
          this.commonService.gTrack('login', 'Login with OTP failed');
          this.loader.hideLoadingDefault();
          //      this.util.showAlert(this.util.tranlateInstant('FAILED'), this.util.tranlateInstant('CANNOT_GET_OTP_TRY_LATER'))
        });
      }

    }
  }  


  activateConsumerStatus(){
    this.apiFactory.deactivateConsumerStatus(this.activateToken,'1')
    .subscribe(
      res => {
        if(res.code == 200){
          this.util.showToast(res.message);
        }else{
          this.util.showToast(res.message);
        }
      },
      err => {
      })
  }
  

}
