  getFormatDate(dateGet: any) {
    // Split the date string to extract day, month, and year
    const [day, month, year] = dateGet.split("/");
  
    // Create a new Date object using the extracted values (year, month-1, day) to account for zero-based months
    const travelDate = new Date(parseInt(year), parseInt(month) - 1, parseInt(day));
  
    // Format the date as 'Day, Month DD'
    return travelDate.toLocaleDateString('en-US', {
      weekday: 'short', // Fri
      month: 'short',   // Nov
      day: '2-digit'    // 08
    });
  }


