  onAgeChange(ev, i, type) {
    let ageValue = ev.target.value ? ev.target.value.toString().trim() : '';
    if (!ageValue || isNaN(Number(ageValue)) || Number(ageValue) === 0) {
      if (type === 1) {
        this.passengerForm[i].get('age')?.setValue('');
        this.passengerForm[i].get('age')?.setErrors({ required: true });
      } else {
        this.passengerFormR[i].get('age')?.setValue('');
        this.passengerFormR[i].get('age')?.setErrors({ required: true });
      }
      return;
    }
    if(this.metaData.msiteFolder == 'ourbustheme'){
      this.showFormError = false;
      this.formErrorMessage = null;

      const input = ev.target as HTMLInputElement;
      let value = input.value;

      value = value.replace(/[^0-9]/g, '');


      const trimmedValue = parseInt(value, 10).toString();
      // If input is empty (leading zeros turned empty), set value to '' or any default like '0'
      if (trimmedValue =='0') {
        input.value = '';

        if (type === 1) {
          this.passengerForm[i].controls['age'].setValue(input.value);
        } else {
          this.passengerFormR[i].controls['age'].setValue(input.value);
        }

        return;
      }

      
      if (value.length > 3) {
        value = value.substring(0, 3); 
      }

      // this.contactForm.controls['mobile_number'].setValue(value);
      if (type === 1) {
        this.passengerForm[i].controls['age'].setValue(value);
      } else {
        this.passengerFormR[i].controls['age'].setValue(value);
      }

      let age: string = value;
      // let age: string = '';
      if (type === 1) {
        age = this.passengerForm[i].value.age != null ? this.passengerForm[i].value.age.toString() : '';
      } else {
        age = this.passengerFormR[i].value.age != null ? this.passengerFormR[i].value.age.toString() : '';
      }
      
        // age = age.replace(/[^0-9]/g, '');
        // if (age.length > 3) { 
        //     if (type == 1) {
        //         this.passengerForm[i].controls['age'].setValue(age.slice(0, 3));
        //     } else {
        //         this.passengerFormR[i].controls['age'].setValue(age.slice(0, 3));
        //     }
        // }

        const ageValue = parseInt(age, 10);
        if (ageValue > 150) {
            if (type == 1) {
                this.passengerForm[i].controls['age'].setValue(150);
            } else {
                this.passengerFormR[i].controls['age'].setValue(150);
            }
        }
    }else{
      this.showFormError = false;
      this.formErrorMessage = null;
      // let age: string = ''; 

      
      const input = ev.target as HTMLInputElement;
      let value = input.value;

      value = value.replace(/[^0-9]/g, '');


      const trimmedValue = parseInt(value, 10).toString();

      if (trimmedValue =='0') {
        input.value = '';

        if (type === 1) {
          this.passengerForm[i].controls['age'].setValue(input.value);
        } else {
          this.passengerFormR[i].controls['age'].setValue(input.value);
        }

        return;
      }
      if (value.length > 2) {
        value = value.substring(0, 2); 
      }

      // this.contactForm.controls['mobile_number'].setValue(value);
      if (type === 1) {
        this.passengerForm[i].controls['age'].setValue(value);
      } else {
        this.passengerFormR[i].controls['age'].setValue(value);
      }

      let age: string = value;
      // let age: string = '';
      if (type === 1) {
        age = this.passengerForm[i].value.age != null ? this.passengerForm[i].value.age.toString() : '';
      } else {
        age = this.passengerFormR[i].value.age != null ? this.passengerFormR[i].value.age.toString() : '';
      }

      const ageValue = parseInt(age, 10);
        if (ageValue > 99) {
            if (type == 1) {
                this.passengerForm[i].controls['age'].setValue(99);
            } else {
                this.passengerFormR[i].controls['age'].setValue(99);
            }
        }
    }
  }



