  async selectPassportDate(type, date, i) {
    let dt;
    if (date) {
      dt = this.dateService.getDateObjFromDateStr(date)
    } else {
      dt = this.dateService.getToday();
    }
    // let modal = this.modalCtrl.create(ModalCalendarPage, {
    //   departureDate: dt,
    //   showPastDate: type == 1 ? true : false,
    //   title: type == 1 ? "Select Issue Date" : "Select Expiry Date",
    //   deactivateFutureDate: type == 1 ? true : false
    // });
    // modal.present();
    let modal = await this.modalController.create({
      component: CalenderModalPage,
      componentProps: {
        departureDate: dt,
        showPastDate: type == 1 ? true : false,
        title: type == 1 ? "Select Issue Date" : "Select Expiry Date",
        deactivateFutureDate: type == 1 ? true : false,
        // type: type
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();

    if (data) {
      let dateStr = data.formattedDate;
      if (type == 1) {
        this.passengerForm[i].controls.passport_issued_date.setValue(dateStr);
      } else {
        this.passengerForm[i].controls.passport_expiry_date.setValue(dateStr);
      }
    };

  }


