  async addExistingPass() {
    // this.navCtrl.navigateForward('add-passenger');
    this.resetForm();
    const modal = await this.modalController.create({
      component: AddExistingPassengerComponent,
      componentProps: {
        travellerLength: this.onwardSeatDetails.length,
        passengerList: this.selectedPassengerList
      }
    });
    await modal.present();
    const { data } = await modal.onDidDismiss();
    if (data != undefined) {
      this.selectedPassengerList = data;
      for (var i = 0; i < this.selectedPassengerList.length; i++) {
        if (this.selectedPassengerList[i].age) this.passengerForm[i].controls['age'].setValue(this.selectedPassengerList[i].age.substr(0, 2));
        if (this.selectedPassengerList[i].name) this.passengerForm[i].controls['name'].setValue(this.selectedPassengerList[i].name);
        if (this.selectedPassengerList[i].title) this.passengerForm[i].controls['sex'].setValue(this.selectedPassengerList[i].title);

        // if(this.selectedPassengerList[i].id_card_type) this.passengerForm[i].controls['id_card_type'].setValue(this.selectedPassengerList[i].id_card_type);

      }
      // console.log("selected traveller", this.selectedPassengerList)
    }
  }

