  onchangeStateId(ev) {
    //console.log(this.stateId.length,"this.stateId")
    // console.log(this.stateId.length,"this.state")
    if (this.stateId.length > 6) {
      this.stateId = this.stateId.substr(0, 6);      
    }
    if(this.stateId.length < 6){
      this.stateGetName = ''
      this.stateType = ''
      this.cityName = ''
      this.cityId = ''
    }
    if (this.stateId.length == 6) {
      console.log(this.stateId.length,"this.stateId")
      let gst = '';
      if(this.metaData.msiteFolder != 'ourbustheme'){
         gst = this.gstId;
      }else{
        gst = ''
      }
      this.apiFactory.getStateByPin(this.stateId, gst)
        .subscribe(
          res => {
            // console.log("getStateByPin", res)
            if (res.code == 200) {
              if (res.result.city != '') {
                this.cityName = res.result.city;
                // this.cityId = this.cityName
              }
              this.stateGetName = res.result.state.toUpperCase();

              this.cityId = res.result.city
              this.stateType = res.result.state;
              // this.isState = this.state.some(
              //   state =>
              //     typeof state === 'string' &&
              //     state.toLowerCase() === this.stateType.toLowerCase()
              // );
                if(res.result.state){
                    this.isEditable = false;
                }
                else{  
                  this.isEditable = true;
                }
              
              console.log(this.cityName,this.stateType, "this.cityName")
              if (this.isServiceTaxApplicable == true) {
                this.passengerDetails.gst_info = {
                  "pin_code": this.stateId,
                  "state": this.stateGetName ? this.stateGetName : this.stateType,
                  "city": this.cityName ? this.cityName : this.cityId
                }
              }

            } else {
              this.stateGetName = ''
              this.stateType = ''
              this.cityName = ''
              this.cityId = ''
              this.util.showToast(res.message);
            }
          },
          err => {

          }
        )
    }
    if (this.stateId.length < 6) {
      this.isEditable = true;
      this.isState = true;
    }
    // else{
    //   this.cityName = '';
    //   this.stateGetName = '';
    // }


  }

