isDisabled(i: number, formType: number): boolean {

  if(this.concessions_category == '' || this.concessions_category == undefined || this.concessions_category == null){
    this.concessions_category = []
    return;
  }
  const formGroup = formType === 1 ? this.passengerForm : this.passengerFormR;

  let selectedPassenger = formGroup[i]?.value;
  let selectedCategory = this.concessions_category.find(data => data.id == selectedPassenger?.concessions_category);
  let isCardValidationRequired = selectedCategory?.requires_card_validation ?? false;

  let isNameEntered = selectedPassenger?.name_by_id !== '';

  if (isCardValidationRequired && isNameEntered) {
    return true;  
  }else{
    return false; 
  }

 // Enable Male & Female radio buttons
}



