  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.context {
  font-family: NotoSans;
  font-size: 13px;
}

.txt-lbl {
  font-family: NotoSans;
  font-size: 14px;
  font-weight: normal;
  color: #3e3e52 !important;
}

.card-1 {
  padding: 16px 12px;
  margin: 0;
