}

.small {
  font-size: 0.8rem;
}

.bold {
  font-weight: bold;
}

.center-div {
  margin: 15px;
}

.card1 {
  background-color: #fdfdfd;
  padding: 10px;
  overflow: hidden;
}

.passenger-details {
  /* border: 1px solid #999; */
  margin: 0 0 5px;
  background-color: #fff;
}
.passenger-details .code.item-md {
  margin-top: -1px;
}
.passenger-details .code.item-ios {
  margin-top: 1px;
}
.passenger-details .grey-box ion-row {
  margin-bottom: -8px;
}
.passenger-details ion-select {
  max-width: 100%;
  text-align: center;
}
.passenger-details ion-select .select-text {
  padding: 5px 0;
}
.passenger-details ion-select .select-icon {
  left: -5px;
}
.passenger-details .select-ios,
.passenger-details .select-md {
  padding: 0;
}
.passenger-details .item-ios.item-block .item-inner {
  border: 0;
}
.passenger-details .item-ios.code,
.passenger-details .item-md.code {
  height: 35px;
  min-height: 35px;
  padding: 0;
  position: absolute;
}
.passenger-details .input-wrapper {
  display: block;
}
.passenger-details i,
.passenger-details ion-icon {
  display: inline-block;
  font-size: 1.6em;
  color: #555;
  position: absolute;
  bottom: 11px;
}
.passenger-details ion-col {
  position: relative;
}
.passenger-details ion-item {
  padding-left: 0;
}

.hd-refund {
  margin: 44px 16px 50px;
  font-family: NotoSans;
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  color: #4a4a4a;
}

.theme1 {
  color: #fff !important;
  --color: #fff !important;
}
.theme1 .card-row ion-card {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
