  --background: #F1C11A;
  color: #191966;
  font-family: NotoSans;
  font-weight: bold;
  font-size: 13px;
  position: fixed;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.theme1title {
  color: #fff;
}

.sc-ion-input-ios-h {
  --padding-start: 16px;
}

.seabird_row {
  background: white;
  padding: 12px;
  margin: 15px;
  border: 0.5px solid #626262;
  border-radius: 15px;
}

.seabird_col {
  display: flex;
  align-items: center;
  justify-content: left;
  font-size: 14px;
}

.seabird_col_line {
  display: flex;
  align-items: center;
  justify-content: left;
  border-bottom: 0.5px dotted #999999;
  padding: 5px;
  font-size: 14px;
}

.seabird_col_line:last-child {
  border-bottom: none;
}

.seabird_col_line1 {
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 0px;
  font-size: 14px;
}

.seabird_item {
  --background: #fff;
  width: 100%;
  --min-height: 34px;
  --border-color: white;
  font-size: 14px;
}
.seabird_item .sc-ion-input-ios-h {
  --padding-start: 10px;
}

.seabird-border-bottom {
  border-bottom: 0.5px solid #626262;
}

.seabird-clickable {
  margin: 18px 0;
}

.seabird-txt-lbl {
  font-family: NotoSans;
  font-size: 15px;
  font-weight: normal;
  color: black;
}

.seabird-select-date {
  color: #2B28CE;
}

.seabird-login-btn {
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  --color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  height: 50px;
  --border-radius: 12px;
}

.custom-radio {
  --color-checked: #2b28cd;
  --border-color: #2b28cd;
}

.input-spacing {
  margin-bottom: 10px;
}

.seabirdTheme .ion-invalid {
  --highlight-background: transparent !important;
  background-color: transparent !important;
}
.seabirdTheme .item-interactive.ion-valid {
  --highlight-background: transparent;
}

.ourbustheme .ourbus_head {
  font-size: 18px;
  font-weight: 700;
  color: #2D2D2D;
  text-align: center;
}
.ourbustheme .roundedDiv {
  margin: 16px 0px;
  width: 80px;
  height: 80px;
  background: #DBEFDC;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
}
.ourbustheme .big_head {
  color: #4CAF50;
  font-size: 24px;
  font-weight: 700;
  text-align: center;
}
.ourbustheme .center-hr {
  justify-content: start !important;
}
.ourbustheme .step_text {
  color: #747474;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  margin: 16px;
}
.ourbustheme ion-card {
  margin: 16px 0px;
  padding: 8px;
}
.ourbustheme .small_roundedDiv {
  width: 40px;
  height: 40px;
  background: #DBEFDC;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  color: #397D32;
  font-size: 18px;
  font-weight: 700;
}
.ourbustheme .msg_1 {
  font-size: 14px;
  color: #2D2D2D;
  font-weight: 500;
  margin-bottom: 8px;
}
.ourbustheme .msg_2 {
  font-size: 12px;
