  font-weight: 500;
}
.ourbustheme ion-radio {
  --color: var(--iconsAndButtonsColor);
  --color-checked: var(--iconsAndButtonsColor);
}
.ourbustheme .ourbus-login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 24px;
  height: 50px;
  border-radius: 4px;
}
.ourbustheme .cardSec {
  margin: 16px;
  padding: 8px;
}
.ourbustheme .secText {
  color: #2d2d2d;
  font-size: 14px;
  font-weight: 700;
}
.ourbustheme .rfDetails {
  font-size: 16px;
  font-weight: 700;
  color: #4CAF50;
  margin: 8px 0px;
}

