import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { CommonStorage, ApiFactory, UtilProvider, CommonService, AppData, LoaderService } from 'src/app/services';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-term-conditions',
  templateUrl: './term-conditions.page.html',
  styleUrls: ['./term-conditions.page.scss'],
})
export class TermConditionsPage implements OnInit {
  htmlData:any;
  constructor(
    private apiFactory: ApiFactory,
    private util:UtilProvider,
    public appData:AppData,
    private loader:LoaderService,
    private location: Location,
    private router: Router
  ) { 
    this.defaultCall();
  }
  ionViewDidEnter(){

  }

  ngOnInit() {
    this.appData.newTheme = this.util.getNewTheme();

  }

  defaultCall(){
    this.loader.showLoadingDefault();
    this.apiFactory.getTermsAndConditions()
      .subscribe(
        htmlData => {
          this.loader.hideLoadingDefault();
          this.htmlData = htmlData;
        },
        error => {
          this.loader.hideLoadingDefault();
          this.htmlData = error.error.text
            // console.log(error.error.text);
        }
      )
  }
  handleBackButton() {
    // if (this.location.isCurrentPathEqualTo('/home')) {
    //   // Prevent navigating back to itself
    //   return;
    // }
    
    if (window.history.length > 1) {
      // If history is present, go back
      this.location.back();
    } else {
      // If no history, navigate to the home page
      this.router.navigate(['tabs/home']);
    }
  }
}
