import { Component, OnInit, Input } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { UtilProvider, CommonService } from 'src/app/services';

@Component({
  selector: 'app-modal-share',
  templateUrl: './modal-share.page.html',
  styleUrls: ['./modal-share.page.scss'],
})
export class ModalSharePage implements OnInit {
  // Data passed in by componentProps
  @Input() appUrl: string;
  @Input() message: string;
  @Input() imageUrl: string;

  constructor(navParams: NavParams,
    private modalCtrl: ModalController,
    private socialSharing: SocialSharing,
    private util: UtilProvider,
    public commonService: CommonService) {
    // componentProps can also be accessed at construction time using NavParams
    // console.log(navParams.get('message'));
    this.appUrl = navParams.get('appUrl');
    this.imageUrl = navParams.get('imageUrl');
    this.message = navParams.get('message');
    this.commonService.gTrack("shareapp");
  }

  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalCtrl.dismiss({
      'dismissed': true
    });
  }

  ngOnInit() {

  }

  ionViewDidLoad(){
    this.commonService.gTrack('shareApp','“Share this app” is clicked')
  }

  share(type) {

    switch (type) {
      case "whatsapp":
        this.socialSharing.shareViaWhatsApp(this.message, null, '')
          .then(
            () => {
              // console.log("share success");
              // this.dismiss();
            }, () => {
              this.socialShare();

            }
          )
        break;
      case "facebook":
        try {
          
          this.socialSharing.shareViaFacebook(this.message, null, '')
            .then(
              () => {
                // console.log("share success")
                // this.dismiss();
              }, () => {
                try {
                  this.socialShare();
                } catch (err) {
                  // console.log("share loop fb", err)
                }
              }
            )
        }
        catch (err) {
          // console.log("facebook", err)
        }
        break;
      case "twitter":
        this.socialSharing.shareViaTwitter(this.message, null, '')
          .then(
            () => {
              // console.log("share success")
              // this.dismiss();
            }, () => {
              this.socialShare();
            }
          )
        break;
      case "instagram":
        this.socialSharing.shareViaInstagram(this.appUrl, this.imageUrl)
          .then(
            () => {
              // console.log("share success")
              // this.dismiss();
            }, () => {
              this.socialShare();
            }
          )
        break;
      case "linkedin":
        this.socialSharing.share(this.message, null, null, '')
          .then(
            () => {
              // console.log("share success")
              // this.dismiss();
            }, () => {
              this.socialShare();
            }
          )
        break;
      default:
        this.socialSharing.share(this.message, null, null, '')
          .then(
            () => {
              // console.log("share success")
              // this.dismiss();
            }, () => {
              // console.log("share failure")
              this.util.showToast("Failed to share app with " + type);
            }
          )
        break;

    }
  }

  socialShare() {
    this.socialSharing.share(this.message, null, '', '')
      .then(
        () => {
          // console.log("share success")
          // this.dismiss();
        }, () => {
          // console.log("share failure")
          this.util.showToast("Failed to share app ");
        }
      )
  }
}
