  margin: 12px;
}

.card {
  height: 72px;
  border-radius: 4px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.12);
  background-color: #ffffff;
}

.seat-card-bg {
  border-radius: 4px;
  border: solid 1px #dddddd;
  background-color: #ffffff;
  width: 100%;
  height: auto;
  margin: 0px 0px 10px 0px;
  display: inline-block;
  padding: 10px 0px;
}

.queries {
  font-weight: bold;
  color: #383a3e;
  text-align: justify;
  font-size: 14px;
  padding: 8px;
}

.rate_div {
  margin-top: -22px;
  margin-left: -90px;
}

.login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background: var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  position: fixed;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.rateBtn {
  display: flex;
  /* flex-direction: row; */
  justify-content: center;
  align-items: center;
  /* padding: 4px; */
  /* gap: 4px; */
  width: 46px;
  height: 32px;
  border-radius: 4px;
  background: #f0f0f0;
}

.msgShow {
  font-size: 14px;
  font-weight: 700;
  color: #4b4b4b;
  padding: 0px 10px 10px 10px;
}

.textSet {
  font-size: 14px;
  font-weight: 700;
  color: #4b4b4b;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0px;
}

.btnSelect {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btnDesign {
  background-color: #f0f0f0;
  margin-right: 10px;
  margin-left: 10px;
  padding: 10px 26px;
  border-radius: 5px;
}

.feedbackSet {
  color: #4a4a4a;
  font-size: 12px;
  font-weight: 400;
}

.submitBtn {
  --background: var(--iconsAndButtonsColor);
}

ion-textarea.sc-ion-textarea-md-h.sc-ion-textarea-md-s.md.hydrated {
  --padding-top: 0px;
  --padding-bottom: 0px;
}

ion-slide {
  margin-top: auto;
  margin-bottom: auto;
}

ion-slide.md.swiper-slide.swiper-zoom-container.hydrated.swiper-slide-active {
  display: block;
}

.sourToDest {
  font-size: 12px;
  font-weight: 400;
  color: #4b4b4b;
}

.feebackSubmit {
