  getHeaders(): HttpHeaders {
    if (this.authenticate.isLoggedIn()) {
      // this.headers = new Headers({ 'platform': this.platformName, 'Content-Type': 'application/json', 'Auth-Token': this.authenticate.token });
      this.headers = new HttpHeaders().set('platform', this.platformName).set('Content-Type', 'application/json').set('Auth-Token', this.authenticate.token);
      this.headers.append('Access-Control-Allow-Origin', '*');
      this.headers.append('Access-Control-Allow-Methods', 'POST, GET, OPTIONS, PUT');
    } else {
      // this.headers = new Headers({ 'platform': this.platformName, 'Content-Type': 'application/json' });
      this.headers = new HttpHeaders().set('platform', this.platformName).set('Content-Type', 'application/json');
      this.headers.append('Access-Control-Allow-Origin', '*');
      this.headers.append('Access-Control-Allow-Methods', 'POST, GET, OPTIONS, PUT');

    }
    // this.options = new RequestOptions({ headers: this.headers });
    // return this.options;
    return this.headers;
  }

