  async showAlert(title, msg) {
    const alert = await this.alertCtrl.create({
      header: title,
      message: msg,
      buttons: [
        {
          text: 'OK',
          handler: data => {
            if(this.loginType != 'Forced Signup with OTP Login'){
              this.navCtrl.navigateForward('login');
            }else{
              this.authenticationService.login(this.signupForm.value.email, this.signupForm.value.password)
              .subscribe(result => {
      
                
                this.globalData.IS_GST = false;
                this.loader.hideLoadingDefault();
                if (result.success === true) {
                  this.commonService.gTrack('login', 'Login successful');
                  this.util.showToast('Login Success', 'center');
                  this.apiFactory.getMetaAppData()
                    .subscribe(
                      loginResponse => {
                        if (!loginResponse.code) {
                          let localData = loginResponse[0];
      
                          this.commonService.setMetaApp(localData);
      
                          //this.setupPush();
                        }
                      }
                    );
                  // this.navCtrl.navigateRoot('/tabs/my-bookings');
                  // this.navCtrl.setRoot(HomePage);
      
                  this.navCtrl.navigateRoot('tabs/home');
                } else {
                  this.commonService.gTrack('login', 'Login failed');
                  this.util.showAlert('', result.body.message);
                  // login failed
                  this.showError(result.body.message);
                }
                console.log('login --->', result)
              }, error => {
                this.commonService.gTrack('login', 'Login failed');
                this.loader.hideLoadingDefault();
                this.util.showAlert("Failed", "Login failed try again later")
                this.ex.call('LoginPage', 'Login()', error)
              });
            }
          }
        }
      ]
    });

    await alert.present();
  }
  isValid(): boolean {
    console.log('CancellationPage --> cancellationForm --> isValid()', this.signupForm);

    if (this.signupForm.valid) {
      return true
    }
    this.errors = true;
    if (!this.signupForm.value.otp) {
      // this.util.showToast('Please enter OTP')
    }



    if (this.isNameValid()) {
      return;
    }
    if (this.signupForm.controls.last_name.invalid) {
      this.showError("Please enter last name");
      return
    }

    if (this.isEmailValid()) {
      return;
    }
    if (this.isPhoneValid()) {
      return;
    }
    if (!this.isPasswordValid()) {
      return;
    }
    if (!this.isConfirmPasswordValid()) {
      return;
    }


    return false
  }

  /**
   * is confirm password valid
   */
  isConfirmPasswordValid() {
    if (this.signupForm.value.password != this.signupForm.value.password_confirmation) {
      this.showError("Password and confirm password not matching");
      let elem = <any>document.querySelector('.signup-confirm ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return false;
    }
    return true;
  }

  /**
   * method to check is email valid 
   */
  enforceMaxLength(event: any) {
    const input = event.target as HTMLInputElement;
    let sanitizedValue = input.value.replace(/[^0-9]/g, '');
    sanitizedValue = sanitizedValue.slice(0, 10);
    input.value = sanitizedValue;
    // const formControl = this.formData.get('emergencyPhoneNumber');
    // if (formControl) {
    //   formControl.setValue(sanitizedValue, { emitEvent: false });
    // }
  }
  isEmailValid() {
    if (this.signupForm.controls.email.invalid) {
      this.showError("Please enter valid Email.");
      let elem = <any>document.querySelector('.signup-email ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return true;
    }
    return;
  }

  /**
   * method to check is password valid
   */

  isPasswordValid() {
    if (this.signupForm.value.password.length < 8) {
      this.showError("Password should be min 8 characters");
      let elem = <any>document.querySelector('.signup-password ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return false;
    }
    return true;
  }

  /**
 * method to check is phone no valid 
 */

  isNameValid() {
    if (this.signupForm.controls.first_name.invalid) {
      this.showError('Please enter your Name');
      let elem = <any>document.querySelector('.signup-name ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return true;
    }
    return;
  }

  /**
* method to check is otp valid 
*/

  isOtpValid() {
    if (this.signupForm.controls.otp.invalid) {
      this.showError("Please enter the OTP");
      let elem = <any>document.querySelector('.signup-name ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return true;
    }
    return;
  }

  /**
   * method to check is phone no valid 
   */

  isPhoneValid() {
    if (!(this.signupForm.value.mobile_number) || (this.signupForm.value.mobile_number.toString().length != this.phoneNumCount) || !this.signupForm.value.mobile_number) {
      this.showError("Please enter your " + this.phoneNumCount + " digit mobile number");
      let elem = <any>document.querySelector('.signup-mobile_number ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return true;
    }
    return;
  }

