  async validateDateOfBirth(event: any) {
    let dateObj: any = new Date(event.detail.value);
    const result = this.dateService.formattedDateData(dateObj);
    this.showDate= result.formattedBirthDate;
    const minAge = 12;
    const maxAge = 120;

    const isValidAge = this.dateService.isAgeWithinRange(this.showDate, minAge, maxAge);

    if (!isValidAge) {
      const toast = await this.toastController.create({
        message: `Age must be between ${minAge} and ${maxAge} years.`,
        duration: 3000,
        position: 'bottom',
        color: 'danger'
      });
      await toast.present();
    }
  }

