___CSS_LOADER_EXPORT___.push([module.id, `ion-content {
  --background: #ffffff;
}

.month-section {
  padding: 10px;
}
.month-section p {
  text-align: left;
  margin-left: 10px;
  font-weight: 700;
}

.week-days {
  display: flex;
  justify-content: space-around;
  font-weight: bold;
  margin-bottom: 14px;
  margin-top: 25px;
}

.days-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0px; /* Remove space between dates */
}

button {
  background: transparent;
  border: none;
  font-size: 14px;
  position: relative;
  padding: 0;
  height: 55px;
  width: 100%;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
}

button.selected {
  background-color: var(--iconsAndButtonsColor);
  color: var(--primaryText);
}

button.in-range {
  position: relative;
  color: #333;
  border-radius: 0px;
  z-index: 1; /* Ensure text stays above the background */
  overflow: hidden; /* Prevent the pseudo-element from overflowing */
}

button.in-range::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--iconsAndButtonsColor);
  opacity: 0.1; /* 10% opacity for the background */
  z-index: -1; /* Place behind the button text */
  border-radius: inherit; /* Match border-radius of the button */
}

button.disabled {
  color: #cccccc;
  pointer-events: none;
}

button .price {
  font-size: 10px;
  color: #666;
  margin-top: 4px;
}

.range-line {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 100%;
  background-color: #eef7ed;
  z-index: -1;
}

.col {
  display: flex;
  justify-content: center;
  align-items: center;
}
.col ion-icon {
  font-size: 25px;
}

.header_p {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}

.ourbus_btn {
  height: 50px;
  box-shadow: none;
  --border-radius: 8px;
  font-size: 18px;
  font-weight: 700;
  --background:var(--iconsAndButtonsColor);
  --color:var(--primaryText);
  margin: 16px;
  --background-activated:var(--iconsAndButtonsColor);
}

/* Add a range highlight class */
.range-highlight {
  border-radius: 18%;
}

/* Selected date styling */
.selected-date {
  border-radius: 18%;
