    apiFormatDate(date) {
        //yyyy-MM-dd year/month/day
        return date.year + '-' + this.dateService.preZero(date.month) + '-' + this.dateService.preZero(date.day)
    }

    displayFormatDate(date) {
        //dd-MM-yyyy day-month-year
        return this.dateService.preZero(date.day) + '-' + this.dateService.preZero(date.month) + '-' + date.year
    }

    apiResonseFormatDate(date) {
        //dd/MM/yyyy day/month/year
        return this.dateService.preZero(date.day) + '/' + this.dateService.preZero(date.month) + '/' + date.year
    }

    checkDateFormat() {
        let date = this.commonStorage.getItem('metaData').dateFormat;
        if (date.indexOf('/') == 2) {
            // console.log("dd/mm/yyyy");
            return "dd/mm/yyyy";
        } else if (date.indexOf('/') == 4) {
            // console.log("yyyy/mm/dd");
            return "yyyy/mm/dd";
        } else if (date.indexOf('-') == 2) {
            // console.log("dd-mm-yyyy");
            return "dd-mm-yyyy";
        } else if (date.indexOf('-') == 4) {
            // console.log("yyyy-mm-dd");
            return "yyyy-mm-dd";
        }
    }

    checkPurposeTime(value) {
        let time = value.split(' ');
        let t = time[0].split(':');
        if (time[1] == 'PM' && t[0] != '12') {
            t[0] = (parseInt(t[0]) + 12).toString();
        }
        let result = t[0] + t[1];
        return result;
    }
    calculateAmountByPercentage(amount: number, percent: number) {
        return this.roundOfN(Math.round((amount * percent / 100) * 100) / 100, 2);
        // Math.round(x)
    }

    roundOfN(val: number, num: number) {
        return parseFloat(val.toFixed(num));
    }

