    gTrack(msg, action = 'click') {
        // console.log("tracking ", msg);
        msg = location.origin + " : " + msg;
        try {
            this.ga.trackView(msg).then(() => { console.log('track view called') })
            this.ga.trackEvent(location.origin, msg+":"+action).then(() => { console.log('track event called') })
        } catch (err) {
            // console.log("track err", err)
        }

    }

    colorTheme() {
        this.themeColorName = this.commonStorage.localGet("appColors");
        if (this.themeColorName && this.themeColorName.viewBgColor != undefined && this.themeColorName.textFieldPlaceholderColor != undefined) {
            console.log("final theme", this.themeColorName, this.sectionHeaderTextColor,this.homaPageHeadingTextColor);
            this.primary = this.themeColorName.primary;
            // this.primary="#1463e0";
            this.secondary = '#2b332d';
            // this.primaryText="#fff";
            this.primaryText = this.themeColorName.primaryText;
            this.viewBgColor = this.themeColorName.viewBgColor;
            this.iconsAndButtonsColor = this.themeColorName.iconsAndButtonsColor;
            this.sectionHeaderTextColor = this.themeColorName.sectionHeaderTextColor;
            this.placeholderColor == this.themeColorName.textFieldPlaceholderColor;
            this.homaPageHeadingTextColor == this.themeColorName.homaPageHeadingTextColor
            
            this.secondaryText = '#fff';
            document.body.style.setProperty('--primary', this.primary);
            document.body.style.setProperty('--iconsAndButtonsColor', this.iconsAndButtonsColor);
            document.body.style.setProperty('--primaryText', this.primaryText);
            document.body.style.setProperty('--viewBgColor', this.viewBgColor);
            document.body.style.setProperty('--sectionHeaderTextColor', this.sectionHeaderTextColor);
            document.body.style.setProperty('--homaPageHeadingTextColor',this.themeColorName.homaPageHeadingTextColor )
            //   document.body.style.setProperty('--placeholderColor', this.placeholderColor);
        }
    }

    private subjects: Subject<any> = new Subject();
    publish(eventName: string) {
        // ensure a subject for the event name exists
        this.subjects[eventName] = this.subjects[eventName] || new Subject();

        // publish event
        this.subjects[eventName].next();
    }
    on(eventName: string): Observable<any> {
        // ensure a subject for the event name exists
        this.subjects[eventName] = this.subjects[eventName] || new Subject();

        // return observable
        return this.subjects[eventName].asObservable();
    }

