import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { ApiFactory, AppData, LoaderService, UtilProvider } from 'src/app/services';

@Component({
  selector: 'app-about-us',
  templateUrl: './about-us.page.html',
  styleUrls: ['./about-us.page.scss'],
})
export class AboutUsPage implements OnInit {
  aboutus: any;
  mobileWebVersion:String = "";
  iosVersion:String = "";


  constructor(
    private apiFactory: ApiFactory,
    public appData : AppData,
    public util: UtilProvider,
    private loader:LoaderService,
    private location: Location,
    private router: Router,
  ) {

      this.mobileWebVersion = this.appData.mobileWebVersion;
      this.iosVersion = this.appData.iosVersion;
      this.appData.newTheme = this.util.getNewTheme();
      this.defaultCall()
  }

  ngOnInit() {
  }

  defaultCall() {
    this.loader.showLoadingDefault();
    this.apiFactory.getAboutUs()
      .subscribe(
        aboutus => {
          this.aboutus = aboutus.about_us;
          this.loader.hideLoadingDefault();
        },
        error => { 
          this.loader.hideLoadingDefault();
        }
      )
  }
  handleBackButton() {
    // if (this.location.isCurrentPathEqualTo('/home')) {
    //   // Prevent navigating back to itself
    //   return;
    // }
    
    if (window.history.length > 1) {
      // If history is present, go back
      this.location.back();
    } else {
      // If no history, navigate to the home page
      this.router.navigate(['tabs/home']);
    }
  }
}
