import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { CommonStorage, ApiFactory, UtilProvider, CommonService, AppData } from 'src/app/services';

@Component({
  selector: 'app-cancellation-policies',
  templateUrl: './cancellation-policies.page.html',
  styleUrls: ['./cancellation-policies.page.scss'],
})
export class CancellationPoliciesPage implements OnInit {

  htmlData:any;
  cancellationPolices:any;
  constructor(
    private apiFactory: ApiFactory,
    public appData: AppData,
    public util: UtilProvider
  ) { 
    this.defaultCall();
    this.appData.newTheme = this.util.getNewTheme();
  }
  ionViewDidEnter(){
    
  }

  ngOnInit() {
  }

  defaultCall(){
    this.apiFactory.getTermsAndConditionsWithCancellationPolicies()
      .subscribe(
        htmlData => {
          this.htmlData = htmlData;
          this.cancellationPolices = htmlData 

        },
        error => {
            // console.log(error);
        }
      )
  }

}
