import { Component, Input, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import {CommonService} from 'src/app/services';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-theme-price-breakup-modal',
  templateUrl: `./theme-1-price-breakup-modal.component.html`,
  // templateUrl: `./${environment.client.templateUrl}-price-breakup-modal.component.html`,
  styleUrls: ['./theme-price-breakup-modal.component.scss'],
})
export class ThemePriceBreakupModalComponent implements OnInit {
  @Input() fareBreakupDetails: any;
  @Input() currencySym: any;
  @Input() checkCancelProtect: boolean;
  @Input() phoneBooking: boolean;
  @Input() metaData: any;
  @Input() payAmount: any;
  constructor(
    private params : NavParams,
    public commonService: CommonService,
    public modalController: ModalController,
  ) {
    this.fareBreakupDetails = params.get('pricetBreakupData');
    this.currencySym = params.get('currencySym');
    this.checkCancelProtect = params.get('checkCancelProtect');
    this.phoneBooking = params.get('phoneBooking');
    this.metaData = params.get('metaData');
    this.payAmount = params.get('payAmount');
  }

  ngOnInit() {}

  showDiscount(fareBreakupDetails) {
    let discount = 0;
    discount =
      (fareBreakupDetails.offer_discount
        ? fareBreakupDetails.offer_discount
        : 0) +
      (fareBreakupDetails.ebooking_discount
        ? fareBreakupDetails.ebooking_discount
        : 0) +
      (fareBreakupDetails.previous_pnr_discount
        ? fareBreakupDetails.previous_pnr_discount
        : 0) +
      (fareBreakupDetails.privilege_card_discount
        ? fareBreakupDetails.privilege_card_discount
        : 0);

    if (discount == 0) {
      return false;
    } else return true;
  }
}
