import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { NavController, AlertController } from '@ionic/angular';
import { CommonStorage, ApiFactory, LoaderService, CommonService, UtilProvider, ExceptionProvider, DateService, AppData } from '../services';
import { UntypedFormBuilder } from '@angular/forms';
import { ThemeService } from '../services/theme-service';
import * as moment from 'moment';

@Component({
  selector: 'app-cancel-confirmation-ticket',
  templateUrl: './cancel-confirmation-ticket.page.html',
  styleUrls: ['./cancel-confirmation-ticket.page.scss'],
})
export class CancelConfirmationTicketPage implements OnInit {

  data: any;
  ticketDetails: any;
  currencySym: string;
  pnrNumber: string;
  email: string;
  displayDate: any;
  PassengerDetails: any;
  showCancel: any;
  refundType: any;
  cancelCouponDetails: any = {};
  showRefundType = false;
  showCoupon: boolean = false;
  fareBreakupDetails:any;
  metaData: any;
  uidNumber: string = '';
  otpStatus: boolean | null = null;
  constructor(private route: ActivatedRoute, private router: Router, public navCtrl: NavController,
    private commonStorage: CommonStorage,
    public formBuilder: UntypedFormBuilder,
    private apiFactory: ApiFactory,
    private loader: LoaderService,
    private commonService: CommonService,
    private util: UtilProvider,
    private dateService: DateService,
    public theme: ThemeService,
    private ex: ExceptionProvider,
    public appData: AppData,
    private alertCtrl: AlertController) {
    this.route.queryParams.subscribe(params => {
      if (this.router.getCurrentNavigation().extras.state) {
        this.data = this.router.getCurrentNavigation().extras.state.user;
      }
      // console.log(this.data);
      this.email = this.data;
      this.defaultCall();

    });

    this.appData.newTheme = this.util.getNewTheme();
    this.metaData=this.commonStorage.getItem("metaData")
  }

  // changeDateFormat(date) {
  //   var d = new Date(date.toString().trim());
  //   var m = this.util.getDayName(d.getDay()+1)+ ", " + d.getDate() + " " + this.util.getMonthName(d.getMonth()+1) + " " + d.getFullYear();

  //   // var m = this.dateService.changeDateFormat(date, "yyyy-mm-dd")
  //   // return m.dayNameShort + " " + m.day + " " + m.monthNameShort + " " + m.year;
  //   return m;
  // }

  changeDateFormat(date) {
    if (date && date != undefined && date != null && date != '') {
      var r = this.dateService.getDateObjFromDateStr(date, this.commonService.checkDateFormat(), "yyyy-mm-dd");
      if(r.dayName !=undefined && r.dayName!= null && r.dayName!=''){
        return r.dayName+", "+r.day+" "+r.monthName+" "+r.year;
      }else{
        var d = new Date(date.substr(0, 10).toString().trim());
        if(d.toString()!="Invalid Date" && d!=null){
          var m = this.util.getDayName(d.getDay() + 1) + " " + d.getDate() + " " + this.util.getMonthName(d.getMonth() + 1) + " " + d.getFullYear();
          return m;
        }else{
          return date;
        }
      }
    } else {
      return date;
    }
  }

  defaultCall() {
    this.metaData=this.commonStorage.getItem("metaData")

    this.currencySym = this.commonStorage.getItem('metaData').currencySym;
    this.showCoupon = this.commonStorage.getItem('metaData').isCouponAllowed;
    this.ticketDetails = {};
    this.ticketDetails.boarding_details = {};
    this.showCancel = false;
    this.ticketDetails = this.commonStorage.getItem('cancelTicketDetails');
    if (!this.ticketDetails.is_coupon_used && 
      !this.ticketDetails.wallet_booking && 
      this.commonStorage.getItem('metaData').isAllowCashCredit) {
    this.refundType = '2'; 
  } else if (this.ticketDetails.wallet_booking) {
    this.refundType = '3'; 
  } else {
    this.refundType = '3'; 
  }
    this.fareBreakupDetails=this.ticketDetails.fare_break_up_hash
    // console.log(this.ticketDetails);
    console.log(this.ticketDetails);
    if (this.metaData.msiteFolder == 'ourbustheme') {
      this.PassengerDetails = this.ticketDetails.passenger_details.filter(passenger => passenger.ticket_status !== 'Cancelled'); 
      this.PassengerDetails.forEach(passenger => {
        passenger.checked = false;  // Make sure none of the checkboxes are selected initially
      });
       console.log("this.PassengerDetails",this.PassengerDetails);
       
    }else{
    this.PassengerDetails = this.ticketDetails.passenger_details;
    if (this.PassengerDetails.length > 1) {
      // this.PassengerDetails[0].checked = true;
    }
    }
    
    this.loader.hideLoadingDefault();
    this.refundType = this.ticketDetails.is_coupon_used ? '1' : (this.ticketDetails.wallet_booking ? '3' : (!this.commonStorage.getItem('metaData').isAllowCashCredit ? '1' : '2'));
    // console.log(this.ticketDetails.is_coupon_used ? '1' : (this.ticketDetails.wallet_booking ? '3' : (!this.commonStorage.getItem('metaData').isAllowCashCredit ? '1' : '2')))
  }

  ionViewDidLoad() {

  }

  getFormatedDate(date: string) {
    return moment(date, "DD/MM/YYYY").format("DD MMM YYYY");
  }
  checkeBoxChange() {
    // console.log('checkeBoxChange()', this.PassengerDetails);
    for (let passenger of this.PassengerDetails) {
      if (passenger.checked) {
        this.showCancel = true;
        break;
      }
      this.showCancel = false;
  }
  }

  check(index) {
      this.PassengerDetails[index].checked = !this.PassengerDetails[index].checked;
  }

  canCancel(passengerDetails) {
    if (!this.util.checkConnection()) {
      return false;
    }
    if (!this.seatNos(passengerDetails)) {
      this.util.showToast('Please select seat to cancel');
      return;
    }
    this.loader.showLoadingDefault();
    this.apiFactory.canCancel(this.ticketDetails.ticket_number, this.seatNos(passengerDetails))
      .subscribe(
        result => {
          this.loader.hideLoadingDefault();
          this.onCancelSuccess(result);
        }, error => {
          this.loader.hideLoadingDefault();
        }
      )
  }


  seatNos(passengerDetails): string {
    let seatNos = "";

    for (let passenger of passengerDetails) {
      if (passenger.checked) {
        seatNos += passenger.seat_number + ",";
      }
    }
    return seatNos;
  }

  onCancelSuccess(result) {
    if (result.success && result.body) {
      // if(result.body.refund_amount){
      this.cancelCouponDetails = result.body;
      this.showRefundType = true;
      // }else{
      //   this.util.showAlert("","CANNOT_CANCEL_TICKET");
      // }
    } else if (!result.success && result.body) {
      if (result.body.response) {
        this.util.showToast(result.body.response.message)
      }
    }


  }



  cancelTicket(passengerDetails) {
    if (!this.util.checkConnection()) {
      return false;
    }
    let cancelSeatsList = [];
    for (let passenger of passengerDetails) {
      if (passenger.checked) {
        cancelSeatsList.push(passenger.seat_number);
      }
    }

    if (cancelSeatsList.length > 0) {
      this.loader.showLoadingDefault();
      try {

        let path = "ticket_number=" + this.ticketDetails.ticket_number + '&contact_details=' + this.email + '&travel_date=' + this.ticketDetails.travel_date + "&refund_type=" + this.refundType;
        if (this.ticketDetails.wallet_booking) {
          let path = "ticket_number=" + this.ticketDetails.ticket_number + '&contact_details=' + this.email + '&travel_date=' + this.ticketDetails.travel_date;
        }
        let completedStatus = cancelSeatsList.length === this.PassengerDetails.length;
        path = completedStatus ? path : (path + "&seat_numbers=" + cancelSeatsList.join(','));
        let currentCancelState = completedStatus ? this.apiFactory.cancelETicketCompletely(path) : this.apiFactory.cancelETicketPartially(path)
        currentCancelState
          .subscribe(result => {
            this.loader.hideLoadingDefault();
            // console.log('result', result);
            if (typeof result.code != "undefined") {
              this.util.showAlert('', result.message)
            } else {
              let navigationExtras: NavigationExtras = {
                queryParams: {
                  refundDetails: result,
                  ticketDetails: this.ticketDetails,
                  couponCancel: this.refundType == 1 ? true : false
                }
              };
              this.commonService.gTrack('cancelTicket','cancel ticket successful')
  
              this.navCtrl.navigateForward('confirm-cancellation-component', navigationExtras)
              // this.navCtrl.setRoot(ConfirmCancellationComponent,{
              //   refundDetails:result,
              //   ticketDetails:this.ticketDetails,
              //   couponCancel:this.refundType == 1?true:false
              // })
            }
          },
            err => {
              this.commonService.gTrack('cancelTicket','cancel ticket failed')
              this.loader.hideLoadingDefault()
              if (!this.util.isConnected()) {
                // this.util.showAlert(this.util.tranlateInstant("ERROR_LOADING"),this.util.tranlateInstant("INTERNET_NOT_CONNECTED_PLEASE_CONNECT_INTERNET_AND_TRY_AFTER_AGAIN"));
              } else {
                // this.util.showAlert(this.util.tranlateInstant("ERROR_LOADING"),this.util.tranlateInstant("SOMETHING_WENT_WRONG_PLEASE_TRY_AFTER_SOMTIME"));
              }
              // this.ex.call('ChangePasswordPage','changePassword() --> this.apiFactory.changePassword(this.changeForm.value)',err,'')
            })
      } catch (e) {
        this.commonService.gTrack('cancelTicket','cancel ticket failed')
        this.loader.hideLoadingDefault();
        // console.log("confirm cancellation page --> submitted(passengerDetails)", e);
      }
    } else {
      this.commonService.gTrack('cancelTicket','cancel ticket failed')
      // this.util.showToast(this.util.tranlateInstant('PLEASE_SELECT_ATLEAST_ONE_PERSON_FOR_CANCELLATION'));
    }
    // console.log('cancelSeatsList', cancelSeatsList.length);
  }

  ngOnInit() { }

  navigateBack() {
    this.navCtrl.back();
  }


  // validateUID() {
  //   this.otpStatus = this.uidNumber.length == 4; 
  // }
}
