import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { ApiFactory, UtilProvider } from '../services';

@Component({
  selector: 'app-rate-journey',
  templateUrl: './rate-journey.page.html',
  styleUrls: ['./rate-journey.page.scss'],
})
export class RateJourneyPage implements OnInit {
  queriesList:any = [];
  msg1: any;
  msg2: any;
  msg3: any;
  msg4: any;
  msg5: any;
  pnrNumber: string;
  feedback = "";
  msg1Rating: any;
  msg2Rating: any;
  msg3Rating: any;
  msg4Rating: any;
  msg5Rating: any;
  travelAgainmsg: any;
  tt: any;
  rateBtn: any;
  searchData: { id: string; msg: string; }[];
  ratingdata = {};
  enteredData: any;
  strLength: number;
  

  constructor(private apiFactory: ApiFactory,public util: UtilProvider,private navCtrl : NavController) { 
    // this.submitFeedback();
  }

  ngOnInit() {
    this.pnrNumber = localStorage.getItem('pnrNumber');
    
    this.apiFactory.getFeedbackQueries().subscribe(
      res => {
        // console.log(res);
        this.searchData = [];
        for(let i=0;i<= res.length -1 ;i++){
          // console.log(res[i], 'omdatat');
          var d = { id: "",msg: "" };
          for (const property in res[i]) {
            //console.log(`${property}: ${res[i][property]}`);
            d.id=property;
            d.msg=res[i][property];
          }
          this.searchData.push(d);
        }
        // console.log(this.searchData, 'omResult');
      }, err => {
        
       }
    );
  }
  getrating1(msgId,ratingnumber){
    // console.log(msgId,ratingnumber,'gjhrgkrh');
    // this.msg1Rating = ratingnumber;
    let io = [];
    for(let j=1; j<=5;j++){
      document.getElementById('btn_'+msgId+'_'+j).style.backgroundColor = '#f0f0f0';
    }

    for(let i=1; i<=ratingnumber;i++){
      io.push(i);
      // console.log(io, "omm");
      io.forEach((item ) =>{
        // console.log(item)
        document.getElementById('btn_'+msgId+'_'+item).style.backgroundColor = 'var(--iconsAndButtonsColor)';
      });
      this.ratingdata[msgId]=ratingnumber;
      // console.log(this.ratingdata,"OMRESULT")
    }

  }

  travelAgain(value){
    // console.log(value);
    this.travelAgainmsg = value;
    if(value == 'yes'){
      document.getElementById('btn_yes').style.backgroundColor = 'var(--iconsAndButtonsColor)';
    }
    else{
      document.getElementById('btn_yes').style.backgroundColor = '#f0f0f0';
    }
    if(value == 'no'){
      document.getElementById('btn_no').style.backgroundColor = 'var(--iconsAndButtonsColor)';
    }else{
      document.getElementById('btn_no').style.backgroundColor = '#f0f0f0';
    }
  }
  submitFeedback(){
   var data = Object.keys(this.ratingdata);
   this.enteredData = data;
    // console.log(this.enteredData, 'Omset')
    // if(this.searchData.length == data.length && this.travelAgainmsg != null){
      this.ratingdata['pnr_number'] = this.pnrNumber;
      this.ratingdata[ "device_id"] = "";
      this.ratingdata["action"] = "update_feedback_ratings";
      this.ratingdata["controller"] = "api";
      this.ratingdata["format"] = "json";
      this.ratingdata["like_to_travel_again"] = this.travelAgainmsg;
      this.ratingdata["feedback"] = this.feedback;
        this.apiFactory.updateFeedback(this.pnrNumber,"",this.ratingdata).subscribe(
        res => {
          // console.log(res,"hjfkdghjkf");
          if(res.code == '401'){
            this.util.showToast(res.message);

          }else{
            this.util.showToast(res.message);
          }
          
          // this.navCtrl.navigateBack('my-bookings')

        }, err => {
          // this.loader.hideLoadingDefault();
         })
    // }else{
    //     this.util.showToast('data is mandatory');
    //   }
  }
   countChars(){
     const myTextArea = this.feedback;
     const charNum = document.getElementById("charNum")
    var maxLength = 250;
    // this .strLength = this.feedback.length;
    // console.log(this.strLength, 'omTextarea');
    //   if(this.strLength >= maxLength){
    //     document.getElementById("charNum").innerHTML = '<span style="color: red;">'+this.strLength+' out of '+maxLength+' characters</span>';
    // }else{
    //     document.getElementById("charNum").innerHTML = this.strLength+' out of '+maxLength+' characters';
    // }
    const remaining = maxLength - myTextArea.length;
    charNum.textContent =`${remaining} characters (s) left`;
    // console.log(remaining, 'omTextarea');
}
}
