.seabirdtheme .seabird_header {
  padding: 10px;
  font-weight: bold;
  font-size: 16px;
}
.seabirdtheme .seabird_header-content {
  display: flex;
  flex-direction: column;
}
.seabirdtheme .seabird_title-container {
  display: flex;
  align-items: center;
  height: 60px;
}
.seabirdtheme .headText {
  margin: 0px;
  font-size: 14px;
  font-weight: 700;
}
.seabirdtheme .ticketShow {
  margin: 2px;
  color: #999999;
  font-size: 12px;
  font-weight: 400;
}
.seabirdtheme .srcDesti1 {
  text-align: end;
  font-size: 14px;
  font-weight: 700;
  color: #000;
}
.seabirdtheme .msgShow1 {
  text-align: end;
  font-size: 10px;
  font-weight: 400;
  margin: 8px 0px;
  color: #000;
}
.seabirdtheme .srcDesti {
  text-align: start;
  font-size: 14px;
  font-weight: 700;
  color: #000;
}
.seabirdtheme .msgShow {
  text-align: start;
  font-size: 10px;
  font-weight: 400;
  margin: 8px 0px;
  color: #000;
}
.seabirdtheme .borderSet {
  border-top: 1px dashed #626262;
  border-bottom: 1px dashed #626262;
}
.seabirdtheme .passengerName {
  text-align: start;
  padding: 8px 0px;
  font-size: 14px;
  font-weight: 700;
  color: #000000;
}
.seabirdtheme .seatDetails {
  text-align: end;
  padding: 8px 0px;
  font-size: 14px;
  color: #2B28CE;
  font-weight: 700;
}
.seabirdtheme .busRow {
  margin: 8px;
  background: #F2F8FF;
  padding: 8px;
  border-radius: 5px;
}
.seabirdtheme .busCircle {
  border-radius: 50%;
  border: 1px solid #000000;
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
}
.seabirdtheme .startPosition p {
  text-align: start;
  color: #000;
  padding: 2px 0px;
}
.seabirdtheme .mainHeading {
  font-size: 13px;
  font-weight: 700;
}
.seabirdtheme .Sea_bird_Completed {
  color: green !important;
}
.seabirdtheme .cancelled-class {
  color: red !important;
}

.ourbus-bustype-div {
  font-size: 14px;
  font-weight: 300;
  color: #747474;
}

.ourbus-dark-text-div {
  font-size: 14px;
  font-weight: 700;
  color: #2D2D2D;
}

.color-change {
  filter: brightness(0) saturate(100%) invert(43%) sepia(58%) saturate(376%) hue-rotate(92deg) brightness(97%) contrast(94%);
}

.login-btn {
  --background: var(--iconsAndButtonsColor);
  margin: 10px 4px;
  --border-radius: 20px;
  height: 45px;
}`, "",{"version":3,"sources":["webpack://./src/app/booking-details/booking-details.component.scss"],"names":[],"mappings":"AAAA;EACE,qBAAA;EACA,eAAA;EACA,iBAAA;EACA,oBAAA;EACA,kBAAA;EAEA,sBAAA;EACA,cAAA;AAAF;;AAIA;EACE,YAAA;AADF;;AAIA;EACG,YAAA;AADH;;AAGA;EACE,kCAAA;EACA,eAAA;EACA,iBAAA;AAAF;;AAEA;EACE,oCAAA;EACA,eAAA;EACA,iBAAA;AACF;;AACA;EACE,eAAA;EACA,WAAA;EACA,WAAA;EACA,YAAA;EACA,oCAAA;AAEF;;AACA;EACE,kBAAA;EACA,wHAAA;EACA,oCAAA;EACA,qBAAA;EACA,kBAAA;EACA,YAAA;EACA,aAAA;AAEF;;AACA;EACE,qBAAA;EACA,eAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;EACA,cAAA;EACA,gBAAA;EACA,WAAA;EACA,gBAAA;AAEF;;AACA;EACE,qBAAA;EACA,eAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;EACA,cAAA;EACA,gBAAA;EACA,WAAA;EACA,iBAAA;AAEF;;AAAA;EACE,qBAAA;EACA,eAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;EACA,kCAAA;EACA,gBAAA;EACA,WAAA;EACA,iBAAA;AAGF;;AADA;EACE,eAAA;EACA,mBAAA;EACA,sBAAA;EACA,yBAAA;EACA,gBAAA;EACA,WAAA;AAIF;;AADA;EACE,SAAA;EACA,qBAAA;EACA,eAAA;EACA,iBAAA;EACA,oBAAA;EACA,kBAAA;EACA,iBAAA;EACA,sBAAA;EACA,cAAA;EACA,gBAAA;AAIF;;AADA;EACE,YAAA;EACA,qBAAA;EACA,eAAA;EACA,mBAAA;EACA,oBAAA;EACA,kBAAA;EACA,iBAAA;EACA,sBAAA;EACA,cAAA;EACA,qBAAA;EACA,gBAAA;AAIF;;AADA;EAEE,0CAAA;AAGF;;AAAA;EACE,mBAAA;AAGF;;AAAA;EACE,wDAAA;EAEA,UAAA;AAEF;;AAAA;EACE,yCAAA;EACA,yBAAA;EAA0B,iBAAA;EACxB,kBAAA;EACA,oBAAA;AAIJ;;AAFA;EACE,qBAAA;EACE,eAAA;EAAgB,UAAA;EAChB,iBAAA;AAMJ;;AAHA;EACE,eAAA;EACA,iBAAA;AAMF;;AAJA;EACE,gBAAA;AAOF;;AAJA;EACE,iBAAA;EACA,mBAAA;EACA,oBAAA;EACA,kBAAA;EACA,iBAAA;EACA,sBAAA;EACA,cAAA;EACA,mBAAA;AAOF;;AAJA;EACE,YAAA;EACA,qBAAA;EACA,eAAA;EACA,iBAAA;EACA,oBAAA;EACA,kBAAA;EACA,iBAAA;EACA,sBAAA;EACA,UAAA;EACA,wBAAA;AAOF;;AAJA;EAEE,yCAAA;EACA,yBAAA;EAGA,iBAAA;EACE,kBAAA;EACF,iBAAA;AAIF;;AAAA;EACE,yCAAA;EACA,yBAAA;EAGA,iBAAA;EACE,kBAAA;AACJ;;AAEA;EACE,qBAAA;EACA,eAAA;EACA,mBAAA;EACA,oBAAA;EACA,kBAAA;EACA,mBAAA;EACA,sBAAA;EACA,cAAA;EACA,aAAA;EAEA,WAAA;AAAF;;AAGA;EACA,SAAA;EACA,eAAA;EACA,iBAAA;AAAA;;AAEA;EACE,UAAA;EACA,eAAA;EACA,iBAAA;AACF;;AAEA;EACE,aAAA;AACF;;AAEA;EACE,YAAA;EACA,gBAAA;EACA,qBAAA;AACF;;AAEA;EACE,qBAAA