  font-family: NotoSans;
  font-size: 14px;
  margin: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}

.card-1 {
  padding: 16px 12px;
  margin: 0;
  box-shadow: rgba(0, 0, 0, 0) 0 0 0 0px, rgba(0, 0, 0, 0) 0 0 0 0, rgba(0, 0, 0, 0) 0 0 0 0;
  border-radius: 0;
  height: 44px;
}

.h50 {
  height: 50px;
}

.bg-content {
  --background: var(--viewBgColor);
}

.card-p {
  padding: 5px;
  margin: 0;
  /* text-align: justify; */
  font-size: 13px;
}

.align-price {
  position: absolute;
  right: 10px;
  font-size: 14px;
  font-weight: bold;
  top: 15px;
}

.product-name {
  width: 50%;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 14px;
  font-weight: bold;
}

.product-dec {
  font-size: 8px;
  white-space: normal;
  margin-top: 5px;
}

.qty {
  width: 25px;
  height: 25px;
  text-align: center;
  border: none;
  background: #ccc;
  font-size: 14px;
  color: rgba(115, 114, 114, 0.9803921569);
  text-align: center;
  line-height: 22px;
}

.qtyplus {
  width: 25px;
  height: 25px;
  background: #00adb5;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
}

.qtyminus {
  width: 25px;
  height: 25px;
  background: rgba(128, 128, 128, 0.7019607843);
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 30px;
  line-height: 20px;
  color: #fff;
}

.inc-dic {
  margin-top: -26px;
  position: absolute;
  right: 7px;
  background: #ccc;
  padding: 2px;
  border-radius: 5px;
}

.display-qty {
  position: absolute;
  top: 65px;
  left: 10px;
  background: rgba(0, 173, 181, 0.7294117647);
  width: 70px;
  height: 17px;
  border-radius: 5px;
  font-size: 12px;
  color: #fff;
  text-align: center;
}

.item ion-thumbnail {
  min-width: 3.5rem;
  min-height: 4.5rem;
}

ion-card .item ion-thumbnail {
  min-width: 3.5rem;
  min-height: 4.5rem;
}

.routes-footer {
  width: 100%;
  display: block;
  position: fixed;
  bottom: 0px;
  margin: 0px;
  z-index: 99999;
  padding: 8px 8px 0px 8px;
}

.routes-footer ion-button {
  font-weight: bolder;
  font-size: 12px;
  align-items: center;
  justify-content: center;
}

.routes-footer .button1 {
  --background:#3e3e52;
  height: 45px;
  width: 80px;
}

.routes-footer .button2 {
  --background:var(--iconsAndButtonsColor);
  height: 45px;
  width: calc(100% - 88px);
