    bookTypeChanged(value) {
        // console.log("changed pay opts", value);
        if (this.offerType == '5') {
            if (!this.offerCoupon) {
                this.checkOfferCoupon('');
            } else
                this.checkOfferCoupon(this.offerCoupon);
        }
        if (value == 1) {
            // console.log(value);
            this.bookType = '1';
            this.bookOnline = true;
            this.phoneBooking = false;
            this.payFromWallet = false;
            this.is_cancel_protect = true;
            if (this.firstTimeLoad > 0)
                if(this.metaData.msiteFolder != 'ourbustheme'){
                    this.util.showAlert('', 'Transaction charges are applicable in online booking')
                }

            this.bookTypeChangeFareUpdate();
        } else if (value == 2) {
            this.bookOnline = false;
            this.phoneBooking = true;
            this.payFromWallet = false;
            this.is_cancel_protect = false;
            this.bookTypeChangeFareUpdate();
        } else if (value == 3) {
            this.bookType = value;
            // value = this.bookType;
            this.is_cancel_protect = true;
            this.walletBookingPopup();

        } else {
        }

    }

    async walletBookingPopup() {
        // this.is_cancel_protect = true;
        // true
        if (this.selectedPromoCouponDetails && this.selectedPromoCouponDetails['exclude_wallet']) {
            // console.log("exclude_wallet>>>");
            let alert = await this.alertCtrl.create({
                // header: '',
                message: 'Coupon is invalid for wallet booking',
                buttons: [
                    {
                        text: 'Ok',
                        handler: () => {
                            this.bookOnline = false;
                            this.phoneBooking = false;
                            this.payFromWallet = true;
                            this.bookTypeChangeFareUpdate();
                        }
                    },
                    {
                        text: 'Cancel',
                        role: 'cancel',
                        cssClass: "alertCancel",
                        handler: () => {
                            if (this.bookOnline) {
                                this.bookType = "1";
                            } else if (this.phoneBooking) {
                                this.bookType = "2";
                            } else {
                                this.payFromWallet = true;
                            }
                            return false;
                        }
                    }
                ]
            });
            await alert.present();
            // this.promoCoupon='';
            // this.checkpromoCoupon(this.promoCoupon,false)
        } else {
            this.bookOnline = false;
            this.phoneBooking = false;
            this.payFromWallet = true;
            this.getFare();
            if (this.pnrDiscountAplied) {
                setTimeout(() => {
                    this.checkQuotePNR();
                }, 500);
            }
        }
    }

