    /**
     * method executed when continues button is tapped
     */

    async pay() {
        if(this.appData.isWEBAPP){
            this.firebaseAnalyticsService.logCustomEvent('payment_gateway', { page: 'Payment Details Page' });
        }
        if (this.metaData.ispopupEnabled && this.commonStorage.getItem('SELECTED_SERVICE') != null) {
            let popUpData = this.commonStorage.getItem('SELECTED_SERVICE').popup_details;
            // console.log(popUpData, 'omdata')
            if (popUpData != null) {
                if (Object.keys(popUpData).length !== 0) {
                    if (popUpData.id != null || popUpData.id != '') {
                        let alertt = await this.alertCtrl.create({
                            header: popUpData.title,
                            subHeader: popUpData.message,
                            buttons: [{
                                text: 'Cancel',
                                handler: (data) => {
                                    // console.log(data);
                                },
                            },
                            {
                                text: 'continue',
                                handler: async (data) => {
                                    this.popUpDetailsRoundTrip();
                                }
                            }]
                        });
                        await alertt.present();
                    }
                    else {
                        this.popUpDetailsRoundTrip();
                    }
                }
                else {
                    this.popUpDetailsRoundTrip();
                }
            } else {
                this.popUpDetailsRoundTrip();
            }
        } else {
            this.popUpDetailsRoundTrip();
        }
    }
    async popUpDetailsRoundTrip() {
        if (this.metaData.ispopupEnabled && this.commonStorage.getItem('SELECTED_SERVICE_RETURN') != null) {
            let popUpData = this.commonStorage.getItem('SELECTED_SERVICE_RETURN').popup_details;
            // console.log(popUpData, 'omdata')
            if (popUpData != null) {
                if (Object.keys(popUpData).length !== 0) {
                    if (popUpData.id != null || popUpData.id != '') {

                        let alertt = await this.alertCtrl.create({
                            header: popUpData.title,
                            subHeader: popUpData.message,
                            buttons: [{
                                text: 'Cancel',
                                handler: (data) => {
                                    // console.log(data);
                                },
                            },
                            {
                                text: 'continue',
                                handler: async (data) => {
                                    this.pay1();

                                }
                            }]
                        });
                        await alertt.present();
                    }
                    else {
                        this.pay1();
                    }
                } else {
                    this.pay1();
                }
            } else {
                this.pay1();
            }
        } else {
            this.pay1()
        }
    }
