import { Component, OnInit } from '@angular/core';
import { NavParams, Platform, ModalController, AlertController, NavController } from '@ionic/angular';
import { dismiss } from '@ionic/core/dist/types/utils/overlays';

@Component({
  selector: 'app-modal-payment',
  templateUrl: './modal-payment.page.html',
  styleUrls: ['./modal-payment.page.scss'],
})
export class ModalPaymentPage implements OnInit {
  formHtml: any = "";
  innerHtmlStr: any = "";
  innerHtmlStr1: any = "";
  url: any = "";
  constructor(
    private navParams: NavParams,
    private platform: Platform,
    private modalCtrl: ModalController,
    private alertCtrl: AlertController,
    private navCtrl: NavController
  ) {

    // this.platform.backButton.subscribe(0, () => {
    //   if (navigator['app'] && navigator['app'].canGoBack()) {
    //     navigator['app'].pop();
    //   } else {
    //     navigator['app'].exitApp();
    //     this.util.showAlert("Exit", "Do you want to exit the app?");
    //     this.util.showToast("You want to exit the app?");
    //   }
    // });
    this.platform.backButton.subscribe(() => {
      // console.log('Handler was called!');
      this.dismiss();
    });
    this.innerHtmlStr = " <iframe id='pay-iframe' srcdoc=" + this.innerHtmlStr1 + " width='100%' height='100%'></iframe>";

    this.formHtml = this.navParams.get('formHtml');
    this.url = this.navParams.get('url');
    // console.log("formIp", this.formHtml)
    this.innerHtmlStr1 = "<!DOCTYPE html><html>" +
      "<body><b>Please wait we are redirecting to the payment page....</b>" +
      "<form id='payMentGateWayForm' name='payMentGateWayForm'></form>" +
      "</body></html>";

    this.openPaymentGateway(); 
  }

  ngOnInit() {
  }

  dismiss() {
    this.modalCtrl.dismiss({
      'dismissed': true
    });
  }

  openPaymentGateway() {
    try {
      let form = (<HTMLFormElement>document.getElementById('payMentGateWayForm'));
      let iframe = <HTMLElement>document.getElementById("pay-iframe");
      var e = document.getElementById("page_is_dirty")
      // console.log("value++++++++++++", form, e)
      //console.log("value+++=&&&&&&=+", form.contentWindow)
      //console.log("value+++=6666===+", form.contentWindow.location)
      //console.log("value+++========+", form.contentWindow.location.href)


      form.action = this.url;
      form.method = "POST";
      form.submit();
    }
    catch (err) {
      // console.log("Error", err);
      this.showAlert("Transaction Ended", err);
    }
  }


  async showAlert(title, msg) {
    const alert = await this.alertCtrl.create({
      header: title,
      message: msg,
      buttons: [
        {
          text: 'OK',
          handler: data => {
            // this.navCtrl.navigateRoot("tabs/home");
          }
        }]
    });

    await alert.present();
  }

}
