import { Component, OnInit } from '@angular/core';
import { NavController, AlertController } from '@ionic/angular';
import { AuthenticationService, LoaderService, UtilProvider, ExceptionProvider, CommonService, CommonStorage, AppData } from '../services';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ThemeService } from '../services/theme-service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.page.html',
  styleUrls: ['./forgot-password.page.scss'],
})
export class ForgotPasswordPage implements OnInit {

  forgotFailed: boolean;
  forgotForm: any;
  response: any;
  changePasswordForm: any;
  isphone: boolean = false;
  showHideImg : boolean = true
  errors: boolean;
  data: any;
  showForgotError: boolean;
  forgotErrorMessage: string;
  logo: any;
  passwordFieldType: string = 'password';
  cfmpasswordFieldType: string = 'password';
  cfmshowpass: boolean =false;
  showpass: boolean=false;
  metaData: any;
  constructor(
    public navCtrl: NavController,
    private authenticationService: AuthenticationService,
    private loader: LoaderService,
    private util: UtilProvider,
    private formBuilder: UntypedFormBuilder,
    private alertCtrl: AlertController,
    public theme: ThemeService,
    private ex: ExceptionProvider,
    private commonService: CommonService,
    private commonStorage: CommonStorage,
    public appData: AppData) {
    this.defaultCall()
    this.appData.newTheme = this.util.getNewTheme();
    this.metaData=this.commonStorage.getItem("metaData")
    // this.logo = this.commonStorage.getItem("metaData").headerLogo;
    this.logo = this.commonStorage.localGet("metaData").mobileLoginPageLogo?this.commonStorage.localGet("metaData").mobileLoginPageLogo:this.commonStorage.localGet("metaData").headerLogo;
  }

  ngOnInit() {
  }

  defaultCall() {
    this.forgotFailed = false;
    this.errors = false;
    this.showForgotError = false;
    this.forgotErrorMessage = null;
    this.validations(1);
  }


  /**
  * create validation rules for forgot details
  */
  validations(type) {
    if (type == 1) {
      this.forgotForm = this.formBuilder.group({
        email: ['', Validators.compose([Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')])],
      })
      this.changePasswordForm = this.formBuilder.group({
        otp: ['', Validators.compose([Validators.required])],
        newPassword: ['', Validators.compose([Validators.required])],
        confirmPassword: ['', Validators.compose([Validators.required])],
      })
    }
    else if (type == 2) {
      this.changePasswordForm = this.formBuilder.group({
        otp: ['', Validators.compose([Validators.required])],
        newPassword: ['', Validators.compose([Validators.required])],
        confirmPassword: ['', Validators.compose([Validators.required])],
      })
    }
  }

  goBack() {
    this.navCtrl.pop();
  }


  forgotPassword() {
    // console.log("this.dataform", this.forgotForm.value.email);
    this.showForgotError = false;
    this.forgotErrorMessage = null;
    if (this.isValid()) {
      if (this.data == '') {
        this.data = this.forgotForm.value;
      }
      // console.log("data", this.data);
      this.commonService.gTrack("forgetpassword");
      this.loader.showLoadingDefault();
      this.authenticationService.forgotPassword(this.data)
        .subscribe(async result => {
         
          this.loader.hideLoadingDefault();
          if (result.success === true) {

            const alert = await this.alertCtrl.create({
              header: "SUCCESS",
              message: result.body.message,
              buttons: [{
                text: 'OK',
                handler: data => {
                  this.navCtrl.pop();
                }
              }]
            });

            await alert.present();

          }
          else if (result.body && result.body.otp) {
            this.isphone = true;
            this.showHideImg = false
            this.validations(2);
            this.response = result.body;
            // console.log(result);
          }
          else {

            // forgot failed
            if (result.body)
              this.util.showAlert('', result.body.message);
            else if (result.error)
              this.util.showAlert('', result.error.message);
          }
          // console.log('forgot --->', result)

        },
          err => {
            this.loader.hideLoadingDefault()
            if (!this.util.isConnected()) {
              this.util.showAlert("Error Loading", "Internet not connected please connect the internect and try after again");
            } else {
              this.util.showAlert("Error Loading", "Something went wrong please try after some time");
            }
            this.ex.call('ForgotPasswordPage', 'forgotPassword() --> this.authenticationService.forgotPassword(this.forgotForm.value)', err, '')
          });
    }
  }

  changePassword() {
    if (this.response != undefined) {
      let resetData = {
        otp: this.changePasswordForm.value.otp,
        key: this.response.key,
        newPassword: this.changePasswordForm.value.newPassword,
        confirmPassword: this.changePasswordForm.value.confirmPassword,
        mobileNumber: this.forgotForm.value.email
      };

      // console.log(resetData);

      this.loader.showLoadingDefault();
      this.authenticationService.changePassword(resetData)
        .subscribe(async result => {
          this.loader.hideLoadingDefault();
          if (result.success === 'success') {

            const alert = await this.alertCtrl.create({
              header: "SUCCESS",
              message: result.body,
              buttons: [{
                text: 'OK',
                handler: data => {
                  this.navCtrl.pop();
                }
              }]
            });

            await alert.present();
          }

          else {

            if (result.body)
              this.showError(result.body.message);
            else if (result.error)
              this.showError(result.error.message);
          }
          // console.log('forgot --->', result)

        },
          err => {
            this.loader.hideLoadingDefault()
            if (!this.util.isConnected()) {
              this.util.showAlert("Error Loading", "Internet not connected please connect the internect and try after again");
            } else {
              this.util.showAlert("Error Loading", "Something went wrong please try after some time");
            }
            this.ex.call('ForgotPasswordPage', 'forgotPassword() --> this.authenticationService.forgotPassword(this.forgotForm.value)', err, '')
          });

    }
    // console.log(this.response,this.changePasswordForm.value);
    //this.authenticationService.resetPassword(this.data)

  }


  isValid(): boolean {
    var phoneno = /^\d{10}$/;
    if (this.forgotForm.valid) {
      this.data = { "email": "" + this.forgotForm.value.email + "" };
      return true;
    } else if (this.forgotForm.value.email.match(phoneno)) {
      this.data = { "mobile_number": "" + this.forgotForm.value.email + "" };
      return true;
    } else {
      this.data = '';
    }
    this.showError('Please enter your registered email/phone number');
    this.errors = true;
    let elem = <any>document.querySelector('ion-input.ng-invalid input');
    if (elem) {
      elem.focus();
    }
    return false
  }

  showError(msg) {
    this.errors = true;
    this.showForgotError = true;
    this.forgotFailed = true;
    this.forgotErrorMessage = msg;

  }

  onInputChange(ev) {
    this.isphone = false;
    // this.showHideImg = true
    this.showForgotError = false;
    this.forgotErrorMessage = null;
  }

  ionViewDidEnter() {
    // this.commonService.gTrack("ForgotPasswordPage");
  }


  showPassword(){
    this.showpass = !this.showpass;
    this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password';
  }
  cfmshowPassword(){
    this.cfmpasswordFieldType = this.cfmpasswordFieldType === 'password' ? 'text' : 'password';
    this.cfmshowpass = !this.cfmshowpass;
  }
}
