  padding-bottom: 0px;
  --ion-item-background: #fff;
}

.section {
  --ion-item-background: #f2f2f2f7;
  font-weight: bold;
}

.section-active {
  --ion-item-background: #f2f2f2f7;
  --ion-item-color: var(--sectionHeaderTextColor);
  font-weight: 600;
}
.section-active ion-icon {
  color: var(--sectionHeaderTextColor);
}

.seabird_section {
  --ion-item-background: #F1F7FA;
  font-weight: bold;
}

.seabird_section-active {
  --ion-item-background: #F1F7FA;
  --ion-item-color: var(--sectionHeaderTextColor);
  font-weight: 600;
}
.seabird_section-active ion-icon {
  color: var(--sectionHeaderTextColor);
}

ion-icon {
  margin-right: 5px;
}

.child-list {
  padding: 0;
  margin: 0;
}
.child-list .child, .child-list .child-active {
  margin-bottom: 2px;
}

