}

.child-active {
  --ion-item-background: #fad86b;
  --ion-item-color: #fff;
}
.child-active ion-icon {
  color: #fff;
}

.product-list {
  padding: 0;
  margin: 0;
}

ion-col p {
  margin-top: -2px;
  font-size: 15px;
  color: grey;
}

.mrg {
  margin-top: -10px;
  margin-bottom: -15px;
}

.icon {
  margin-top: -4px;
  margin-left: -16px;
  font-size: 18px;
}

.cornerSet {
  margin-left: 3%;
  margin-right: 3%;
  padding: 0px;
}

