import { Injectable } from '@angular/core';
import { FirebaseAnalytics } from '@ionic-native/firebase-analytics/ngx';
import { CommonStorage } from './common-storage';
import { Platform } from '@ionic/angular';
import { initializeApp } from 'firebase/app';
import { getAnalytics, logEvent } from 'firebase/analytics'; 
import { AppData } from './appdata';
import { ApiFactory } from './api-factory';
@Injectable({
  providedIn: 'root'
})

export class FirebaseAnalyticsService {
  operatorName: any;
  analytics: any;
  isAnalyticsReady = false;
  pendingEvents = [];
 
constructor(
  private firebaseAnalytics: FirebaseAnalytics,
  public commonStorage: CommonStorage,
  private platform: Platform,
  public appdata: AppData,
  public apiFactory: ApiFactory,
) { 
  // Immediately try to get metadata if it doesn't exist yet
  if (!this.commonStorage.localHas('metaData')) {
    this.apiFactory.getMetaAppData().subscribe(
      response => {
        if (!response.code || response.code == 200) {
          this.commonStorage.localSet('metaData', response[0]);
          console.log('MetaData loaded in FirebaseAnalyticsService');
          this.initializeFirebase();
        }
      },
      error => {
        console.error('Error loading metaData in FirebaseAnalyticsService', error);
        this.initializeFirebase(); // Try with any existing data
      }
    );
  } else {
    // Use existing metadata
    this.initializeFirebase();
  }
}

// Initialize Firebase with timeout to ensure other services have loaded
private initializeFirebase() {
  setTimeout(() => {
    try {
      const localData = this.commonStorage.localGet("metaData");
      this.operatorName = localData ? localData.operatorName : null;
      console.log('Operator Name loaded:', this.operatorName);

      let firebaseConfig: any;
      if (localData && localData.msiteFolder == 'cbustheme') {
        firebaseConfig = {
          apiKey: "AIzaSyCGL9kGMW80ARug7-1-m0zHX0q4N6EMmNg",
          authDomain: "pc-api-5560718033266272099-659.firebaseapp.com",
          projectId: "pc-api-5560718033266272099-659",
          storageBucket: "pc-api-5560718033266272099-659.firebasestorage.app",
          messagingSenderId: "276783339554",
          appId: "1:276783339554:web:a3d1878b9e2601740f927b",
          measurementId: "G-8PFZ6HYLEM"
        };
      } else {
        firebaseConfig = {
          apiKey: "AIzaSyADe1YXngQ8NPrMkDyy1UBtOH1XqhqMsgA",
          authDomain: "customer-app---mweb.firebaseapp.com",
          projectId: "customer-app---mweb",
          storageBucket: "customer-app---mweb.firebasestorage.app",
          messagingSenderId: "697968204843",
          appId: "1:697968204843:web:2acfadff1e6821c0eede6e",
          measurementId: "G-ZFC9S0PR6W"
        };
      }

      const app = initializeApp(firebaseConfig);
      this.analytics = getAnalytics(app);
      this.isAnalyticsReady = true;
      console.log('Firebase Analytics initialized successfully');
      
      // Process any pending events
      this.processPendingEvents();
    } catch (err) {
      console.error('Failed to initialize Firebase:', err);
    }
  }, 1000);
}

// Process any events that were attempted before initialization
private processPendingEvents() {
  if (this.pendingEvents.length > 0) {
    console.log(`Processing ${this.pendingEvents.length} pending events`);
    this.pendingEvents.forEach(event => {
      this.logCustomEvent(event.name, event.params);
    });
    this.pendingEvents = [];
  }
}

// Log custom event with safety check
logCustomEvent(eventName: string, params: { [key: string]: any }) {
  // Add operator name to params if available
  if (this.operatorName) {
    params['OPERATOR_NAME'] = this.operatorName;
  }

  if (this.appdata.isIOS) {
    this.firebaseAnalytics.logEvent(eventName, params).then(() => {
      console.log(`Custom event ${eventName} logged on iOS!`);
    }).catch(error => {
      console.error('Error logging custom event on iOS', error);
    });
  } else if (this.appdata.isWEBAPP) {
    // Check if analytics is ready
    if (this.isAnalyticsReady && this.analytics) {
      try {
        logEvent(this.analytics, eventName, params);
        console.log(`Event logged on Web: ${eventName}`, params);
      } catch (error) {
        console.error('Error logging event on Web:', error);
      }
    } else {
      // Queue the event for later when analytics is ready
      console.log(`Queuing event for later: ${eventName}`);
      this.pendingEvents.push({ name: eventName, params });
    }
  }
}
}


