  position: absolute;
  right: 10px;
  padding: 0;
  width: 140px;
  height: 40px;
  top: 10px;
}

.search-txt {
  font-size: 14px;
  font-weight: bold;
  width: 61px;
}

ion-button {
  margin: 0 12px 12px;
  border-radius: 2px;
  --background: var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: "NotoSans";
  text-transform: initial;
}

.note-1 {
  padding: 16px;
}

ion-segment-button {
  text-transform: none !important;
}

.hd-1 {
  font-family: NotoSans;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  font-weight: bold;
  letter-spacing: normal;
  color: #3e3e52;
}

.txt-1 {
  width: 328px;
  font-family: NotoSans;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #3e3e52;
}

.sign-up {
