  font-weight: bolder;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.54;
  letter-spacing: normal;
  text-align: center;
  color: var(--iconsAndButtonsColor);
}

ion-col {
  margin-bottom: 0;
}

