  border-radius: 4px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.12);
  background-color: #ffffff;
  margin: -8px 4px 0;
}

.org-dest {
  height: 20px;
  font-family: NotoSans;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  text-transform: uppercase;
  letter-spacing: normal;
  color: #4a4a4a;
  text-align: left;
  padding: 5px 5px 12px 12px;
}

.date {
  height: 16px;
  font-family: NotoSans;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #747f8d;
  padding: 0 5px 16px 12px;
  text-align: left;
}

ion-content {
  --ion-background-color: var(--viewBgColor);
}

.ios ion-list {
  margin-bottom: 10px;
}

