  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #747f8d;
  margin: -32px 8px 0;
}

.booking-details {
  float: right;
  margin: -25px 16px 0 5px;
}

.ion-toolbar1 {
  background-color: #ffffff !important;
