.theme1 .segment-button-checked {
  --background: #ffffff !important;
  color: var(--iconsAndButtonsColor) !important;
  -webkit-text-fill-color: var(--iconsAndButtonsColor) !important;
}

.ion-color-red {
  --ion-color-base: var(--iconsAndButtonsColor) !important;
}

.themeone-segment-cbus {
  --ion-color-base: #FFBD16 !important;
