ion-card {
  box-shadow: none;
}

.chip-absolute {
  position: absolute;
  top: -18px;
  right: 12px;
  z-index: 4 !important;
}

.date-col {
  border: 1px solid #DFDFDF;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
}
.date-col .date-month {
  background: rgba(95, 95, 95, 0.1);
  font-size: 12px;
  border-radius: 6px;
  padding-left: 8px;
  padding-right: 8px;
}

.trip-data {
  padding: 0px 0px 0px 12px;
}

.devider_set {
  width: 101%;
  position: relative;
  background-color: #fff;
  left: -1.5px;
}

.card-detail-text {
  font-size: 12px;
  color: #191966;
  font-style: normal;
  font-weight: 600;
  margin-top: auto;
  margin-bottom: auto;
}

.travel-day {
  font-size: 18px;
  font-weight: 600;
}

.trip-origin {
  color: #191966;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.themeone-pnrno {
  color: #5F5F5F;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0.14px;
}

.themeone-bookedon {
  color: #5F5F5F;
  font-size: 11px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: 0.11px;
}

ion-header ion-toolbar {
  color: #ffffff;
}

/** New theme-1 design end */
.vertical-align-content {
  display: flex !important;
  align-content: center !important;
  align-items: center !important;
  height: 100%;
}

.ion-text-start {
  display: flex;
  align-items: center;
  padding-left: 0px;
}

.toolbar[primary] .toolbar-title, .toolbar[primary] .bar-button-default {
  color: white;
}

.prevFuture {
  background: #fff;
  border-top: 1px solid #000;
  height: 100%;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  padding: 10px;
  overflow: scroll;
}

.ourbustheme .editProfile {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2D2D2D;
  font-size: 18px;
  font-weight: 700;
}
.ourbustheme .segment-button-checked {
  --indicator-color: transparent !important;
  --indicator-color-checked: transparent !important;
  color: var(--primaryText) !important;
  padding: 0px 8px;
  background: var(--iconsAndButtonsColor) !important;
  margin: 6px;
  border-radius: 8px;
}
.ourbustheme .ourbusRowSet {
  background-color: #EFEFEF;
  border-radius: 8px;
  padding: 8px;
  border: 1px solid var(--iconsAndButtonsColor);
  display: flex;
  align-items: center;
  margin: 16px 0px;
}
.ourbustheme .ourbus_ticket {
  background-color: #DBEFDC;
  padding: 8px;
  border-radius: 4px;
}
.ourbustheme .ourbusIcon {
  width: 24px;
  height: 24px;
}
.ourbustheme .ourbus_plain_text {
  font-size: 14px;
  font-weight: 700;
  color: #2D2D2D;
  text-align: start;
}
.ourbustheme .aroowset {
  display: flex;
  justify-content: end;
  color: var(--iconsAndButtonsColor);
}

.seabirdtheme .seabird_header {
  padding: 10px;
  font-weight: bold;
  font-size: 16px;
}
.seabirdtheme .seabird_header-content {
  display: flex;
  flex-direction: column;
}
.seabirdtheme .seabird_title-container {
  display: flex;
  align-items: center;
  height: 60px;
}
.seabirdtheme .headText {
  margin: 0px;
  font-size: 14px;
  font-weight: 700;
}
.seabirdtheme .upcoming {
  background: #BEF1ED;
  padding: 8px;
  border-radius: 5px;
}
.seabirdtheme .completed {
  background: #D1E6FF;
  padding: 8px;
  border-radius: 5px;
}
.seabirdtheme .cancelled {
  background: #F8D2D6;
  padding: 8px;
  border-radius: 5px;
}
.seabirdtheme .busCircle {
  border-radius: 50%;
  border: 1px solid #000000;
  width: 38px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
}
.seabirdtheme .extraSpace {
  text-align: start;
}
.seabirdtheme .extraSpace p {
  margin: 0px;
  font-size: 12px;
  padding: 2px 0px;
}
.seabirdtheme .upcomingBtn {
  background: #BEF1ED;
  border-radius: 8px;
  --indicator-height: 0px;
  color: var(--textFieldPlaceholderColor);
  margin: 0px 4px 0px 0px;
}
.seabirdtheme .completeBtn {
  background: #D1E6FF;
  border-radius: 8px;
  --indicator-height: 0px;
  color: var(--textFieldPlaceholderColor);
  margin: 0px 4px 0px 0px;
}
.seabirdtheme .cancelBtn {
  background: #F8D2D6;
  border-radius: 8px;
  --indicator-height: 0px;
  color: var(--textFieldPlaceholderColor);
