import { Component, ElementRef, Inject, ViewChild, AfterViewInit, OnInit, Input } from '@angular/core';
import { Platform, NavParams, ModalController } from '@ionic/angular';
import { DOCUMENT } from '@angular/common';

import { Geolocation } from '@ionic-native/geolocation/ngx';
import { darkStyle } from './map-dark-style';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Rx';
import { LoaderService } from '../services';
import { PluginProvider } from '../services/plugin.service';
declare var google;
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
  styleUrls: ['./map.scss']
})
export class MapPage implements OnInit, AfterViewInit {
  @ViewChild('mapCanvas', { static: false }) mapElement: ElementRef;
  mapData = [];
  origin: any;
  // destination: any;
  latitude: any;
  longitude: any;
  title: any = "Map";
  currentLocMarker: any;
  map: any;
  path: any = [];
  poly: any;
  // boardingPoint: any;
  interval;
  @Input() destination: string;
  @Input() boardingPoint: string;
  constructor(
    @Inject(DOCUMENT) private doc: Document,
    public platform: Platform,
    private router: Router,
    private route: ActivatedRoute,
    private geolocation: Geolocation,
    private navParams: NavParams,
    private modalCtrl: ModalController,
    private loader: LoaderService,
    private locationProvider: PluginProvider, ) {
    // this.route.queryParams.subscribe(params => {
    //   if (this.router.getCurrentNavigation().extras.state) {
    //     this.destination = this.router.getCurrentNavigation().extras.state.destination;
    //     this.boardingPoint = this.router.getCurrentNavigation().extras.state.boardingPoint;
    //   }
    //   console.log("destination", this.destination);
    //   this.mapData.push(this.destination)
    // });
    this.destination = navParams.get('destination');
    this.boardingPoint = navParams.get('boardingPoint');
    // console.log("Map destination ::", this.destination, this.boardingPoint)
    this.title = navParams.get('title');

  }

  ngOnInit() {

  }
  ionViewDidLoad() {
    // console.log('did load');
  }

  ionViewWillEnter() {
    // console.log('enter');
    this.loadMap();
  }
  dismiss() {
    // using the injected ModalController this page
    // can "dismiss" itself and optionally pass back data
    this.modalCtrl.dismiss({
      'dismissed': true
    });
  }
  ngAfterViewInit() {

  }

  loadMap() {
    setTimeout(() => {
      var map_options = {
        zoom: 5,
        disableDefaultUI: true,
        center: this.destination,
        mapTypeId: google.maps.MapTypeId.ROADMAP
      };
      this.map = new google.maps.Map(this.mapElement.nativeElement, map_options);
      new google.maps.event.addListenerOnce(this.map, 'idle', () => {
        this.mapElement.nativeElement.classList.add('show-map');
      });
      this.map.setZoom(15);
      this.currentLocMarker = new google.maps.Marker({
        position: this.destination,
        title: this.boardingPoint,
        // label: this.boardingPoint,
        // icon: 'http://maps.google.com/mapfiles/ms/icons/red-dot.png',
        map: this.map
      });
      var iw = new google.maps.InfoWindow({
        content: this.boardingPoint,
        maxWidth: 300,
        infoBoxClearance: new google.maps.Size(1, 1),
        disableAutoPan: false
      });
      google.maps.event.addListener(this.currentLocMarker, "click", function (e) {
        iw.open(this.map, this);
      });
    }, 100);
  }

  getDirection() {
    // console.log('getDirection');
    this.loader.showLoadingDefault();

    this.geolocation.getCurrentPosition({ timeout: 10000, enableHighAccuracy: true, maximumAge: 3600 }).then((resp) => {
      // console.log("currentlocation", resp.coords.latitude, resp.coords.longitude);
      this.currentLocMarker.setMap(null);
      this.loader.hideLoadingDefault();
      this.origin = new google.maps.LatLng(resp.coords.latitude, resp.coords.longitude);
      var pointA = new google.maps.LatLng(resp.coords.latitude, resp.coords.longitude);
      var pointB = this.destination;
      var directionsService = new google.maps.DirectionsService;
      var directionsDisplay = new google.maps.DirectionsRenderer({
        map: this.map
      });
      // console.log(this.origin);
      directionsService.route({
        origin: pointA,
        destination: pointB,
        travelMode: google.maps.TravelMode.DRIVING
      }, function (response, status) {
        if (status == google.maps.DirectionsStatus.OK) {
          directionsDisplay.setDirections(response);
        } else {
          // console.log('Directions request failed due to ' + status);
        }
      });
      this.map.panTo(this.origin);
      this.map.setZoom(12);

    }).catch((error) => {
      this.loader.hideLoadingDefault();
      // console.log('Error getting location', error);
    });
    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
    });
  }

  getCurrentLocation() {
    this.loader.showLoadingDefault();
    this.locationProvider.checkLocationEnabled().then(res => {
      this.loader.hideLoadingDefault();
      this.currentLocMarker.setMap(null);
      // console.log(res);
      this.origin = new google.maps.LatLng(res['latitude'], res['longitude']);
      var pointA = new google.maps.LatLng(res['latitude'], res['longitude']);
      var pointB = this.destination;
      var directionsService = new google.maps.DirectionsService;
      var directionsDisplay = new google.maps.DirectionsRenderer({
        map: this.map
      });
      // console.log(this.origin);
      directionsService.route({
        origin: pointA,
        destination: pointB,
        travelMode: google.maps.TravelMode.DRIVING
      }, function (response, status) {
        if (status == google.maps.DirectionsStatus.OK) {
          directionsDisplay.setDirections(response);
        } else {
          // console.log('Directions request failed due to ' + status);
        }
      });
      this.map.panTo(this.origin);
      this.map.setZoom(12);
      let watch = this.geolocation.watchPosition();
      watch.subscribe((data) => {
      });
    })
  }


  ionViewWillLeave() {
    // console.log("page leave");
    new google.maps.event.addListenerOnce(this.map, 'idle', () => {
      this.mapElement.nativeElement.classList.remove('show-map');
    });
  }




}
