  async gotoMap(type) {
    if (type == 2) {
      this.util.showToast('No location found', 'center');
      return false;
    }
    const modal = await this.modalController.create({
      component: MapPage,
      componentProps: {
        destination: { "lat": parseFloat(this.bookingDetails.latitude), "lng": parseFloat(this.bookingDetails.longitude) },
        boardingPoint: this.bookingDetails.landmark,
        title: type == 1 ? "Boarding point" : "Droping point"
      }
    });
    return await modal.present();
  }

