import { ApiFactory, AppData, CommonService, CommonStorage, ExceptionProvider, LoaderService, UtilProvider } from '../services';
import { Component, OnInit } from '@angular/core';
import { ModalController, NavController } from '@ionic/angular';

import { Location } from "@angular/common";
import { MapSearchPage } from '../map-search/map-search.page';
import { ThemeService } from '../services/theme-service';

// import { CallNumber } from '@ionic-native/call-number/ngx';
@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.page.html',
  styleUrls: ['./contact-us.page.scss'],
})
export class ContactUsPage implements OnInit {

  contacts: any;
  isBitlaPayments: boolean;
  helpLineNumber: string;
  operatorEmail: string;
  searchKey: string = "";
  showSearch = false;
  automaticClose = true;
  shortWithCity:boolean = false;
  shortwithCityList: any = [];
  metaData: any;
  constructor(
    public navCtrl: NavController,
    public apiFactory: ApiFactory,
    public commonStorage: CommonStorage,
    public theme: ThemeService,
    private ex: ExceptionProvider,
    private loader: LoaderService,
    private modalController: ModalController,
    private util: UtilProvider,
    public commonService: CommonService,
    public location:Location,
    public appData: AppData
  ) {
    this.shortWithCity = this.commonStorage.getItem('metaData').sort_with_city;
    this.defaultCall();
    this.isBitlaPayments = this.commonStorage.getItem('metaData').isBitlaPayments;
    this.helpLineNumber = this.commonStorage.getItem('metaData').helpLineNumber;
    this.operatorEmail = this.commonStorage.getItem('metaData').operatorEmail;
    
    
  }
  
  ngOnInit() {
    this.appData.newTheme = this.util.getNewTheme();
    this.metaData = this.commonStorage.getItem('metaData');
  }

  ionViewDidLoad() {
    this.commonService.gTrack('contactUs', 'Contact Us page is displayed')
  }

  defaultCall() {
    this.loader.showLoadingDefault()
    this.apiFactory.getContacts(this.shortWithCity)
      .subscribe(
        contacts => {
          
          this.loader.hideLoadingDefault()
          if(this.shortWithCity){
            // console.log("test1111",contacts)
            this.shortwithCityList = contacts;
            // this.shortwithCityList[0].open = true;
          }else{
            // console.log("test2222",contacts)
            this.contacts = contacts;
          }
          
        },
        err => {
          this.loader.hideLoadingDefault()
          if (!this.util.isConnected()) {
            this.util.showAlert("Error Loading", "Internet is not connected. Please connect internet and try again!");
          } else {
            this.util.showAlert("Error Loading", "Something went wrong, please try after sometime");
          }
          this.ex.call('ContactPage', 'defaultCall() --> this.apiFactory.getContacts()', err, '')
        }
      )
  }

  call(number) {
    // console.log("num", number);
    // this.callNumber.callNumber(number, true)
    //   .then(res => console.log('Launched dialer!', res))
    //   .catch(err => console.log('Error launching dialer', err));

  }

  contactsSplit(contact_details) {
    return contact_details.split(",");
  }


  async gotoMap(data) {
    // if (type == 2) {
    //   this.util.showToast('No latitude langitude found', 'center');
    //   return false;
    // }
    var title = "";

    // if (type == 1) {
    //   title = "Search for closet place to Board "
    // }
    // else {
    //   title = "Search for closet place to Drop "
    // }
    const lng = data.longitude;
    const lat = data.latitude;
    const geocoords = lat + ',' + lng;
    const googleMapsUrl = `https://www.google.com/maps/search/?api=1&query=${lat}+${lng}`; // encode the label!
    window.open(encodeURI(googleMapsUrl), '_system', 'location=no');
    return;
    title = "Location"

    const modal = await this.modalController.create({
      component: MapSearchPage,
      componentProps: {
        destination: {},
        boardingPoint: "",
        title: title,
        lat: data.latitude,
        long: data.longitude
      }
    });
    await modal.present();
  }


  toggleSection(index) {
    // console.log(index);
    this.shortwithCityList[index].open = !this.shortwithCityList[index].open;

    if (this.automaticClose && this.shortwithCityList[index].open) {
      this.shortwithCityList
        .filter((item, itemIndex) => itemIndex != index)
        .map(item => item.open = false);
    }
  }

  // toggleItem(index, childIndex) {
  //   this.shortwithCityList[index].branch_details[childIndex].open = !this.shortwithCityList[index].branch_details[childIndex].open;
  // }

  back(){
    this.location.back();
  }
}