import { Component, OnInit } from '@angular/core';
import { NavController, ModalController } from '@ionic/angular';

import { ApiFactory, AuthenticationService, UtilProvider, CommonStorage, LoaderService, ExceptionProvider, CommonService, AppData } from '../services';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ThemeService } from '../services/theme-service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.page.html',
  styleUrls: ['./reset-password.page.scss'],
})
export class ResetPasswordPage implements OnInit {

  public showPassword: boolean = false;
  public showPassword1: boolean = false; 
  public showPassword2: boolean = false;
  changeFailed: boolean;
  changeForm: any;
  errors: boolean;
  showChangeError: boolean;
  changeErrorMessage: string;
  phoneNumCount: number;
  constructor(
    public navCtrl: NavController,

    private modalCtrl: ModalController,
    private apiFactory: ApiFactory,
    private authenticate: AuthenticationService,
    private util: UtilProvider,
    private formBuilder: UntypedFormBuilder,
    private commonStorage: CommonStorage,
    private loader: LoaderService,
    public theme: ThemeService,
    private ex: ExceptionProvider,
    private commonService: CommonService,
    public appData: AppData) {
    this.changeFailed = false;
    this.errors = false;
    this.showChangeError = false;
    this.changeErrorMessage = null;
    this.validations();
    // console.log(this.phoneNumCount);
    this.appData.newTheme = this.util.getNewTheme();
  }

  ngOnInit() {
  }

  ionViewDidLoad(){
    this.commonService.gTrack('changePassword','“Change Password” option is selected from menu')
    
  }

  dismiss() {
    this.modalCtrl.dismiss();
  }

  changePassword() {
    this.showChangeError = false;
    this.changeErrorMessage = null;
    if (this.isOldPasswordValid()) {
      if (this.isValid()) {
        this.loader.showLoadingDefault();
        this.apiFactory.changePassword(this.changeForm.value)
          .subscribe(result => {
            this.loader.hideLoadingDefault();
            if (result) {
              if (result.success) {
                this.commonService.gTrack('changePassword','password change successful')
    
                this.util.showToast(result.body.message)
                this.authenticate.updateUserAuth(result)
                this.navCtrl.pop();
              } else {
                this.commonService.gTrack('changePassword','password change failed')
                this.showError(result.body.message);
              }
            } else {
              this.commonService.gTrack('changePassword','password change failed')
              this.showError(("Change password failed"));
            }
          },
            err => {
              this.commonService.gTrack('changePassword','password change failed')
              this.loader.hideLoadingDefault()
              if (!this.util.isConnected()) {
                this.util.showAlert("Error Loading", "Internet not connected please connect the internect and try after again");
              } else {
                this.util.showAlert("Error Loading", "Something went wrong please try after some time");
              }
              this.ex.call('ChangePasswordPage', 'changePassword() --> this.apiFactory.changePassword(this.changeForm.value)', err, '')
            })
      }
    }

  }


  /**
 * create validation rules for login details
 */
  validations() {
    this.changeForm = this.formBuilder.group({
      old_password: ['', Validators.compose([Validators.required])],
      password: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
      password_confirmation: ['', Validators.compose([Validators.required])],
    })
  }


  /**
   * if the form is valid as per the validation rules
   */

  isValid(): boolean {
    // console.log('CancellationPage --> cancellationForm --> isValid()', this.changeForm);

    if (this.changeForm.valid) {
      return true
    }
    this.errors = true
    this.isConfirmPasswordValid();
    this.isPasswordValid();
    return false
  }

  /**
   * is confirm password valid
   */
  isOldPasswordValid() {
    if (!this.changeForm.value.old_password) {
      this.showError(('Please enter old password'));
      let elem = <any>document.querySelector('.change-old_password ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return false;
    }
    return true;
  }

  /**
   * is confirm password valid
   */
  isConfirmPasswordValid() {
    if (this.changeForm.value.password != this.changeForm.value.password_confirmation) {
      this.showError(("Password and confirm password are not matching"));
      let elem = <any>document.querySelector('.change-password_confirmation ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return false;
    }
    return true;
  }


  /**
   * method to check is password valid
   */

  isPasswordValid() {
    if (this.changeForm.value.password.length < 8) {
      this.showError(("Password should be of minimum 8 characters"));
      let elem = <any>document.querySelector('.change-password ion-input.ng-invalid input');
      if (elem) {
        elem.focus();
      }
      return false;
    }
    return true;
  }


  /**
   * mehtod to show error message if somethng invalid
   */
  showError(msg) {
    this.errors = true;
    this.showChangeError = true;
    this.changeFailed = false;
    this.changeErrorMessage = msg;

  }

  onInputChange() {
    this.showChangeError = false;
    this.changeErrorMessage = null;
  }

  ionViewDidEnter() {
    //   this.commonService.gTrack("ChangePasswordPage");
  }

  public onPasswordToggle(): void {
    this.showPassword = !this.showPassword;
  }

  public onPasswordToggle1(): void {
    this.showPassword1 = !this.showPassword1;
  }

  public onPasswordToggle2(): void {
    this.showPassword2 = !this.showPassword2;
  }

}
