  font-family: NotoSans;
  font-size: 14px;
  margin: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4a4a4a;
}

.card-1 {
  padding: 16px 12px;
  margin: 0;
  box-shadow: rgba(0, 0, 0, 0) 0 0 0 0px, rgba(0, 0, 0, 0) 0 0 0 0, rgba(0, 0, 0, 0) 0 0 0 0;
  border-radius: 0;
  height: 44px;
}

.h50 {
  height: 50px;
}

.bg-content {
  --background: var(--viewBgColor);
}
