  height: 44px;
  width: 100%;
  z-index: 5;
  --background: white;
  font-family: NotoSans;
}

.frgt-pw {
  font-size: 14px;
  font-weight: normal;
  color: #3e3e52;
}

.click-btn {
  font-size: 13px;
  font-weight: bold;
  line-height: 1.54;
  text-align: center;
  color: #ff002a;
}

.txt-lbl {
  font-family: NotoSans;
  font-size: 14px;
  color: #3e3e52;
}

ion-item {
  --background: #f7f9fb;
}

.center-div {
  margin: 8px;
}

.login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  position: fixed;
  bottom: 0;
  height: 50px;
  border-radius: 4px;
}

