  padding: 18px 16px;
  width: 280px;
  height: 152px;
}

.img-right {
  height: 30px;
  width: 30px;
}

ion-select-option {
  font-family: NotoSans;
}

button {
  font-family: NotoSans;
}

.sc-ion-label-md-h {
  font-family: NotoSans !important;
}

.ok-txt {
  font-family: NotoSans;
  font-size: 14px;
  font-weight: bolder;
