  color: rgba(0, 0, 0, 0.6);
  margin-top: 10px;
  width: 100%;
}

.select-lbl {
  width: 100%;
  padding: 0;
  max-width: none;
}

.themeone-bg-content {
  --background: #fff;
}
.themeone-bg-content .txt-ln2 {
  height: 44px;
  width: 100%;
  z-index: 5;
  --background: white;
  font-family: NotoSans;
}

.themeone-ion-item {
  --background: #fff;
}

.themeone-login-btn {
  position: absolute;
  bottom: 16px;
  left: 16px;
  right: 16px;
