  --ion-background-color: var(--viewBgColor);
}

.FaqBg {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('faqBg.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 250px;
  font-weight: 600;
  padding: 110px 12px 0px;
  color: white;
  backface-visibility: 0.3;
}

.catagoriesSelect {
  height: 50px;
  padding: 10px 16px;
  margin: 10px;
  box-shadow: 1px 2px 10px #efefef;
  width: 95%;
  border-radius: 10px;
}

.header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 2%;
  position: absolute;
  top: 0%;
  left: 0%;
}

.back, .faq, .search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.back {
  margin-left: 2%;
}

.search {
  margin-right: 2%;
}

.faq {
  font-size: 18px;
}

.icon-size {
  font-size: 24px;
}

.accordion-header {
  border-bottom: 1px solid #EFEFEF;
}

.accordion-content {
  padding: 8px 12px;
}

.answer-content {
  font-size: 16px;
}

.txt-lbl {
  white-space: normal;
}

.head {
  font-family: NotoSans;
  font-size: 14px;
  margin: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  -webkit-text-fill-color: black !important;
