import { Component, OnInit } from '@angular/core';
import { ApiFactory, CommonStorage, UtilProvider } from '../services';
import { ModalController, PopoverController } from '@ionic/angular';
import { FaqPopoverPage } from '../faq-popover/faq-popover.page';
import { Router } from '@angular/router';
@Component({
  selector: 'app-faq',
  templateUrl: './faq.page.html',
  styleUrls: ['./faq.page.scss'],
})
export class FaqPage implements OnInit {
  metaData: any
  faqResults: any;
  faq: any;
  allowFaq: any;
  isFaqEmpty: boolean = true;
  selectedOption: any;
  selectedQuesions: any;
  allQuestions: any;
  isCatagory: boolean = true;
  showQuestion: boolean = false;
  categoryQuestions: any;
  fromSearch: boolean = false
  constructor(
    public commonStorage: CommonStorage,
    private popoverCtrl: PopoverController,
    private modalctrl: ModalController,
    private apiFactory: ApiFactory,
    private router: Router,
    private util: UtilProvider
  ) {
    this.metaData = this.commonStorage.getItem("metaData");
    this.allowFaq = this.metaData.isFaq
  }

  ngOnInit() {
    this.apiFactory.getFaq().subscribe(res => {
      if (res.code == 200) {
        if (res.faq_results && res.faq_results.length != 0) {
          this.faqResults = res.faq_results
          this.faqResults.forEach(faq => {
            faq.questions.forEach(question => {
              question.answer = this.cleanHTML(question.answer);
            });
          });
          console.log("this.faqResults",this.faqResults);
          
          this.selectedOption = res.faq_results[0].category
          this.selectedQuesions = res.faq_results.reduce((acc, faq) => acc.concat(faq.questions), []);
          console.log("this.selectedQuesions",this.selectedQuesions);
          
          this.allQuestions = this.selectedQuesions
          this.isFaqEmpty = false;
        }
        if (res.faq && res.faq != '') {
          this.faq = res.faq
          this.isFaqEmpty = false;
        }
      } else {
        this.util.showToast(res.message)
      }
    })

  }
  backHome() {
    this.router.navigate(['home']);
  }
  cleanHTML(htmlString: string): string {
    const parser = new DOMParser();
    const parsedHtml = parser.parseFromString(htmlString, 'text/html');
    return parsedHtml.body.textContent || "";
  }
  GetCatagory(data) {
    if (data == 'none') {
      this.selectedQuesions = this.allQuestions
    } else {
      this.fromSearch = true;
      this.isCatagory = false;
      this.showQuestion = false;
      if (data.length == undefined)
        this.selectedQuesions = [data]
      else {
        this.selectedQuesions = data
      }
    }

  }
  async ClickFocus(event) {
    const popup = await this.modalctrl.create({
      component: FaqPopoverPage,
      componentProps: { 'faqResults': this.faqResults }
    });

    await popup.present();
    const val = await popup.onDidDismiss()
    this.GetCatagory(val.data)
  }
  ClickDismiss(data) {
    this.popoverCtrl.dismiss()
  }
  openQuestion(questions) {
    this.isCatagory = false;
    this.showQuestion = true;
    this.categoryQuestions = questions;
  }
  openCat() {
    this.isCatagory = true;
    this.showQuestion = false;
    this.fromSearch = false
  }
}
