  --ion-background-color:var(--viewBgColor);
}

.login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background:var(--iconsAndButtonsColor);
  color: var(--primaryText);
  font-family: NotoSans;
  font-weight: bolder;
  font-size: 13px;
  position: fixed;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.txt-lbl {
  font-family: NotoSans;
  font-size: 14px;
  font-weight: normal;
  color: #3e3e52;
}

.center-div {
  margin: 15px;
}

.group {
  position: relative;
  margin-bottom: 12px;
}

input {
  font-size: 14px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 100%;
  border: none;
  background-color: #f7f9fb;
  border-bottom: 0.5px solid #757575;
}

input:focus {
  outline: none;
}

/* LABEL ======================================= */
label {
  color: #999;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

/* active state */
input:focus ~ label, input:valid ~ label {
  top: -10px;
  font-size: 14px;
  color: #5264AE;
}

/* BOTTOM BARS ================================= */
.bar {
  position: relative;
  display: block;
}

.bar:before, .bar:after {
  content: "";
  height: 1px;
  width: 0;
  position: absolute;
  background: #5264AE;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 50%;
}

.bar:after {
  right: 50%;
}

/* active state */
input:focus ~ .bar:before, input:focus ~ .bar:after {
  width: 50%;
}

/* HIGHLIGHTER ================================== */
/* ANIMATIONS ================ */
@keyframes inputHighlighter {
  from {
    background: #5264AE;
  }
  to {
    width: 0;
    background: transparent;
  }
}
.center-div {
  margin-top: 15px;
}

ion-content .card-row ion-card {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  width: 100%;
  background-color: #fff !important;
  margin: 0px;
  padding: 16px;
  height: 90vh;
}
ion-content .card-row ion-card ion-item::part(native) {
  background-color: #fff !important;
}
ion-content .card-row ion-card p {
  color: #ADADAD;
  font-size: 12px;
  font-weight: 500;
  margin: 0;
}
ion-content .card-row ion-card ion-item {
  border-radius: 6px;
  border: 1px solid #DFDFDF;
  width: 100%;
  height: 50px;
  --inner-border-width: 0px;
}
ion-content .card-row ion-card ion-item ion-select::part(placeholder) {
  opacity: 1;
}
ion-content .card-row ion-card .box {
  margin-bottom: 16px;
  width: 100%;
  margin-top: 6px;
}
ion-content .card-row ion-card .box h2 {
  color: #191966;
  font-size: 14px;
  font-weight: 500;
  margin: 0;
}
ion-content .card-row ion-card ion-button.ticket {
  width: 100%;
}
ion-content .card-row ion-card ion-button.ticket::part(native) {
  background-color: #F2C21A;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #191966;
  font-weight: 600;
  height: 45px;
}

.themeone-input {
  color: #000;
  height: 50px;
  border-radius: 6px;
  border: 1px solid #DFDFDF;
}

.themeone-login-btn {
  left: 10px;
  right: 10px;
  text-transform: initial;
  --background: #F1C11A;
  color: #191966;
  font-family: NotoSans;
  font-weight: bold;
  font-size: 13px;
  position: fixed;
  bottom: 10px;
  height: 50px;
  border-radius: 4px;
}

.sc-ion-input-ios-h {
  --padding-start: 16px;
}

ion-header ion-toolbar {
  color: #ffffff;
}

.input-spacing {
  margin-bottom: 10px;
  --ion-color-primary: #000 !important;
  --highlight-color-valid: #000 !important;
  --highlight-color-invalid: #000 !important;
  --color-focused:#000 !important;
}

ion-modal#dob {
  --width: 311px !important;
  --height: 543px !important;
  --border-radius: 8px !important;
