.location-line {
  width: 100%;
  height: 1px;
  background-color: #dfdfdf;
  position: relative;
}

.location-line::after {
  content: "";
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(25, 25, 102, 0.2);
  position: absolute;
  left: -8px;
  top: -8px;
}

.location-line::before {
  content: "";
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(242, 194, 26, 0.2);
  position: absolute;
  top: -8px;
  right: -8px;
}

.location-dot {
  width: 7px;
  height: 7px;
  background-color: #191966;
  border-radius: 50%;
  position: absolute;
  left: -4px;
  top: -4px;
}

