import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { ThemeService } from '../services/theme-service';
import { CommonStorage, CommonService, AppData } from '../services';
import { FirebaseAnalyticsService } from '../services/firebase-analytics.service';
@Component({
  selector: 'app-confirm-cancellation',
  templateUrl: './confirm-cancellation.component.html',
  styleUrls: ['./confirm-cancellation.component.scss'],
})
export class ConfirmCancellationComponent implements OnInit {
  refundDetails:any;
  currencySym:string;
  ticketDetails:any = {};
  couponCancel = false;
  metaData: any;
  bookingDetails: any;
  constructor( private route: ActivatedRoute,public navCtrl: NavController,
    private router:Router,
    public theme:ThemeService,
    private commonStorage:CommonStorage,
    private commonService:CommonService,
    public firebaseAnalyticsService : FirebaseAnalyticsService,
    public appData: AppData,
  ) {
    this.route.queryParams.subscribe(params => {
      this.ticketDetails = params["ticketDetails"];
      this.refundDetails = params['refundDetails'];
      this.couponCancel = params['couponCancel'];
      this.currencySym = this.commonStorage.getItem('metaData').currencySym;
  });
  this.metaData=this.commonStorage.getItem("metaData")
  this.bookingDetails = this.commonStorage.getItem('bookingDetails');
   }

  ngOnInit() {
    if(this.appData.isWEBAPP){
      this.firebaseAnalyticsService.logCustomEvent('ticket_details_from_cancel_ticket', { page: 'Confirm Cancellation' });
    }
  }
  home(){
    // this.navCtrl.navigateRoot('tabs/home')
    this.router.navigate(['/tabs/home']);
  }
  navigateToHome() {
    this.router.navigate(['/home']);
  }

  navigateToWallet(){
    this.router.navigate(['/wallet']);
  }

}
