  closeModal() {
    if(this.promotional_url_type == 'video'){
      const videoElement = document.getElementById('floating-video') as HTMLVideoElement;
      videoElement.pause();
      videoElement.muted = true;
    }
    const videoContainer = document.getElementById('floating-container');
    if (videoContainer) {
      videoContainer.style.display = 'none'; // Hide the video container
    }
  }
  
  ngAfterViewInit() {
    const floatingContainer = document.getElementById('floating-container');
    // console.log('Floating Container:', floatingContainer);

    // const floatingContainer = document.getElementById('floating-container');
    const muteButton = document.getElementById('mute-btn');
    const closeButton = document.getElementById('close-btn');

    if (floatingContainer) {
      // Get ion-content dimensions dynamically
      muteButton?.addEventListener('mousedown', (e) => e.stopPropagation());
      muteButton?.addEventListener('touchstart', (e) => e.stopPropagation());
      closeButton?.addEventListener('mousedown', (e) => e.stopPropagation());
      closeButton?.addEventListener('touchstart', (e) => e.stopPropagation());


      const ionContent = document.querySelector('ion-content') as HTMLElement;
      const contentRect = ionContent.getBoundingClientRect(); // Get the bounding rectangle of ion-content
      // Use the content's dimensions for containment
      const draggie = new Draggabilly(floatingContainer, {
        containment: {
          top: 0,
          left: 0,
          bottom: contentRect.height - floatingContainer.offsetHeight, // Contain within the bottom of ion-content
          right: contentRect.width - floatingContainer.offsetWidth, // Contain within the right of ion-content
        },
      });

      // Debug drag move
      draggie.on('dragMove', (event: any, pointer: any, moveVector: any) => {
        // console.log('Dragging...', event, pointer, moveVector); // Logs all drag events
      });

      // Debug when drag ends
      draggie.on('dragEnd', () => {
        // console.log('Drag Ended');
      });
    } else {
      console.log('Floating container not found!');
    }
  }

