        // const pathForBooking = 'res_id=762428 &native_app_type=6&origin=2@184&destination=1@512&boarding_at=13561&drop_off=729&no_of_seats=2&referral_code=&pay_gay_type=53&is_mobile_app=true&is_round_trip=false&device_id='


        this.apiFactory.fareBreakUp(data, pathForBooking)
