                    //console.log('tummm', res, res.fare_break_up_hash);
                    this.fareBreakup_hash = res.fare_break_up_hash;
                    // console.log("this.fareBreakup_hash",this.fareBreakup_hash);
                    
                    //this.loaderService.hideLoadingDefault();
                    // if (res.total_fare) {
