import { Component, OnInit } from '@angular/core';
import { UntypedFormBuilder, Validators } from '@angular/forms';
import { ValidatorService, ApiFactory, UtilProvider, CommonService, AppData } from '../services';
import { AlertController } from '@ionic/angular';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.page.html',
  styleUrls: ['./feedback.page.scss'],
})
export class FeedbackPage implements OnInit {
  feedbackForm: any;
  phoneNumCount: any = 0;
  showModal = false;
  showModalError = false;
  showModalSuccess = false;
  feedbackMsg: any;
  feedback_type: any = [
    {
      "id": 2,
      "name": "Suggestions"
    },
    {
      "id": 4,
      "name": "Complaints"
    },
    {
      "id": 5,
      "name": "Enquiry"
    },
    {
      "id": 6,
      "name": "Compliments"
    },
    {
      "id": 7,
      "name": "Payments/Refunds"
    }
  ];
  constructor(
    private formBuilder: UntypedFormBuilder,
    private api: ApiFactory,
    private util: UtilProvider,
    private alertController: AlertController,
    private commonService: CommonService,
    public appData:AppData

  ) {
    this.appData.newTheme = this.util.getNewTheme();
    this.commonService.gTrack('feedback');
    this.validations();
  }

  ngOnInit() {
    this.appData.newTheme = this.util.getNewTheme();
  }


  validations() {
    // let userid=(Math.random()*1000000).toFixed(0);
    this.feedbackForm = this.formBuilder.group({

      email: ['', Validators.compose([Validators.required, ValidatorService.emailValidator])],
      suggestions: ['2', Validators.compose([Validators.required])],
      feedback: ['', Validators.compose([Validators.required])],
      username: ['', Validators.compose([Validators.required])],
      mobile_number: ['', Validators.compose([Validators.required, Validators.maxLength(this.phoneNumCount), Validators.minLength(this.phoneNumCount)])],

    })
  }


  onInputChange(ev) {
    // this.showSignupError = false;
    // this.signupErrorMessage = null;
    // let mob: string = this.signupForm.value.mobile_number.toString();
    // if (mob.length > this.phoneNumCount) {
    //   this.signupForm.controls['mobile_number'].setValue(mob.substr(0, this.phoneNumCount));
    // }
  }


  submitFeedback() {
    this.commonService.gTrack('feedback','“Submit” button is clicked in Feedback page')
    let data = {
      "mobile_number": this.feedbackForm.value.mobile_number,
      "name": this.feedbackForm.value.username,
      "email": this.feedbackForm.value.email,
      "dept_id": this.feedbackForm.value.suggestions,
      "message": this.feedbackForm.value.feedback
    }
    this.api.feedback_message_from_app(data).subscribe(async res => {
      console.log("feedback response", res);
      this.feedbackMsg = res.message;
      this.showModalSuccess = true;
      // const alert = await this.alertController.create({
      //   header: 'Alert',

      //   message: res.message,
      //   buttons: ['OKAY']
      // });

      // await alert.present();


    }, err => {
      this.showModalError = true;
      // this.util.showToast("Error", "Something went wrong with the app , please try again later!");
    });
  }

  feedbackOkay() {
    this.showModalSuccess = !this.showModalSuccess;
    this.feedbackForm.reset();
  }

}
