button.item-block.item.item-md {
  border-bottom: 1px solid #dedede;
}

.fixed-top {
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 55px;
}
.fixed-top + * {
  margin-top: 75px;
}

.cities-list {
  display: block;
  padding: 0;
  list-style-type: none;
  overflow-y: scroll;
  height: 100vh;
  padding-bottom: 110px;
  background-color: #fff;
}
.cities-list ion-list {
  margin-bottom: 0;
}
.cities-list ion-list .item-ios {
  padding-left: 0;
}
.cities-list ion-list .item-ios .input-wrapper {
  padding-left: 10px;
}

.content-ios .fixed-top {
  top: 64px;
}

.ct-spinner {
  position: absolute;
  top: 50vh;
  left: 50vw;
  margin-top: -20px;
  margin-left: -14px;
}

ion-spinner * {
  width: 28px;
  height: 28px;
  stroke: #444;
  fill: #222;
  z-index: -1;
}

.fnt-14 {
  font-size: 14px !important;
}

.content {
  --background: #191966;
}

.searchbar-input-container.sc-ion-searchbar-ios {
  background-color: #f5f5f5 !important;
  border-radius: 10px !important;
  padding: 10px !important;
  border: 1px solid #D9D9D9 !important;
}

.fnt-14-ourbus {
  font-size: 14px !important;
  font-family: "Test Söhne" !important;
  --background:#FCFCFC;
}

.sc-ion-searchbar-ios-h {
  --background:transparent;
  padding: 0px;
  margin: 10px 7px 7px 14px;
  width: 93%;
}

.bddpText {
  font-size: x-small;
  color: #adb5bd;
}

.cityText {
  float: right;
  color: #adb5bd;
}

.bpDpPoint {
  font-size: 14px;
  font-weight: 400;
  color: #4a4a4a;
}

.sourcelist {
  margin-top: 15px;
  margin-right: 20px;
  margin-bottom: 19px;
  margin-left: 10px;
}

.mainCityName {
  font-weight: 600;
  font-size: 16px;
  color: #4a4a4a;
